import React from "react";
import { colors } from "../../../constants/colors";
import { withStore } from "../../../utils/store.util";
import {
  FormGroup,
  FormControlLabel,
  Switch,
  makeStyles,
} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  link: {
    color: colors.orange,
    cursor: "pointer",
    textDecoration: "underline",
  },
  p: {
    color: "grey",
    fontWeight: "bold",
    fontSize: "1rem",
  },
}));

const DeliveryDetailsComponent = (props) => {
  const token = props.store.getFromStore("token");
  const classes = useStyles();
  const {
    DeliveryInformation,
    login,
    notification,
    handleChange,
    onInputChange,
    handleEditOrder,
  } = props;

  return (
    <div>
      <h1>Your delivery informations</h1>
      {token ? (
        <p
          onClick={
            token
              ? () =>
                  handleChange({
                    dialogData: {
                      open: true,
                      comp: "delivery",
                    },
                  })
              : () => handleEditOrder()
          }
          className={classes.link}
        >
          Change
        </p>
      ) : null}
      <div className="p-t-30">
        {DeliveryInformation.map((item, index) => (
          <div className="m-b-20">
            <div style={{ display: "flex" }}>
              <p className={classes.p}>{item.icon} </p>
              <p className={classes.p}> {item.label} </p>
            </div>
            <p>{item.subTitle}</p>
          </div>
        ))}
      </div>
      {token ? (
        <div className="subheading">
          <p className={classes.p}>Just to make your delivery easier</p>
          <FormGroup>
            <FormControlLabel
              control={
                <Switch
                  checked={notification}
                  onChange={onInputChange}
                  name="notification"
                  style={{ color: "rgb(250, 120, 22)" }}
                />
              }
              label="Send me text notifications"
            />
          </FormGroup>
        </div>
      ) : null}
    </div>
  );
};

export default withStore(DeliveryDetailsComponent);
