import React from 'react';
import styled from 'styled-components';
import {
  Heading,
  HeadingLink,
  LinkList,
  DropdownSection,
  Icon,
} from './Components';
import routes from '../../../constants/route-constants';

const CourierDropdownEl = styled.div`
  width: 18.5rem;
`;

const CourierDropdown = () => {
  return (
    <CourierDropdownEl>
      <DropdownSection data-first-dropdown-section>
        <ul>
          <HeadingLink>
            <a href={`#/${routes.Apply}`}>Apply Now</a>
          </HeadingLink>
          <HeadingLink>
            <a href={`#/${routes.Faqs}`}>Need help for carrier</a>
          </HeadingLink>
        </ul>
      </DropdownSection>
    </CourierDropdownEl>
  );
};

export default CourierDropdown;
