import React from 'react';
import PropTypes from 'prop-types';

import { Typography, Grid } from '@material-ui/core';

import { getPrimaryColors } from '../utils/functions';
import ContactDetails from './ContactDetails';
import { colors } from '../constants/colors';
import ContactFormforCarrier from './ContactFormforCarrier';
import ContactFormforCustomer from './ContactFormforCustomer';
import ContactFormforEmerchant from './ContactFormforEmerchant';

const ContactUs = (props) => {
  const { heading, description, formType } = props;
  return (
    <div
      className="home-contact-us mobile-padding"
      style={{ backgroundColor: colors.blue }}
    >
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
        }}
      >
        <div   style={{marginTop:'50px'}}>
          <h1
            className="main-title text-center m-b-40"
            style={{ color: colors.white }}
          >
            {heading}
          </h1>
          <div className="m-v-50">
            <p className="text-center" style={{ color: colors.white }}>
              {description}
            </p>
          </div>
        </div>
      </div>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          paddingBottom: '3rem',
        }}
      >
        <Grid
          container
          className="contact-details"
          style={{ justifyContent: 'center' }}
        >
          <Grid
            item
            xs={12}
            sm={12}
            md={7}
            style={{ justifyContent: 'center' }}
          >
            {formType === 'Carrier' ? (
              <ContactFormforCarrier />
            ) : formType === 'Customer' ? (
              <ContactFormforCustomer />
            ) : formType === 'Merchant' ? (
              <ContactFormforEmerchant />
            ) : null}
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

ContactUs.propTypes = {
  bgImagePath: PropTypes.string.isRequired,
};

export default ContactUs;
