import { Grid } from '@material-ui/core';
import React from 'react';
import Slide from 'react-reveal/Slide';
import ButtonComponent from '../ButtonComponent';
import { Link } from 'react-router-dom';

const HeadingAndText = (props) => {
  const {
    headingcolor,
    heading,
    text,
    textColor,
    headingfontsize,
    textfontsize,
    lineheight,
  } = props;
  return (
    <Grid xs={12} md={6} container>
      <Slide bottom cascade>
        <div className={'align--center'}>
          <Grid item lg={12}>
            <h1
              style={{ fontSize: headingfontsize, color: headingcolor }}
              className="centerResponsive"
            >
              {heading}
            </h1>
            <p
              style={{
                marginTop: '25px',
                color: textColor,
                lineHeight: lineheight,
                fontSize: textfontsize,
              }}
              className="centerResponsive"
            >
              {text}
            </p>
          </Grid>
        </div>
      </Slide>
      {props.applyNow ? (
        <Grid container spacing={2} className="demo-button">
          <Grid item xs={12} sm={4}>
            <Link to="/fleetmanager">
              <ButtonComponent className="mobile-spcae" orange="btn-peach">
                Register here!
              </ButtonComponent>
            </Link>
          </Grid>
          <Grid item xs={12} sm={4}>
            <ButtonComponent orange="btn-orange">Need help</ButtonComponent>
          </Grid>{' '}
        </Grid>
      ) : null}
    </Grid>
  );
};

export default HeadingAndText;
