import { Button, Checkbox, Dialog, DialogActions, DialogContent, DialogTitle, Grid, InputAdornment, TextField } from '@material-ui/core';
import Autocomplete, { createFilterOptions } from '@material-ui/lab/Autocomplete';
import React, { Component } from 'react';
import icon from './icon-down.png'
import { customFlagList } from "./FlagList";
// import "./InputFlag.css";
import SearchIcon from '@material-ui/icons/Search';
export class FlagInput extends Component {
    constructor (props) {
        super(props)

        this.state = {
            openList: false,
            countryList: customFlagList,
            search: "",

            countryIcon: require("../../upload/country-flags/France.png"),
            countryCode: "+33"
        }
    }
    openList = () => {
        try {
            const { showList } = this.props;
            this.setState({ openList: showList })
        } catch (error) {
            console.log(error);
        }
    }
    handleClose = () => {
        try {
            const { showList } = this.props;
            this.setState({ openList: showList, countryList: customFlagList, })
        } catch (error) {
            console.log(error);
        }
    }

    filterCountries = (e) => {
        const filter = customFlagList.filter(phone => {
            return (
                phone.name.toLowerCase().indexOf(e.target.value.toLowerCase()) !== -1 ||
                phone.dial_code.indexOf(e.target.value) !== -1 ||
                phone.code.toLowerCase().indexOf(e.target.value.toLowerCase()) !== -1
            );
        });
        this.setState({
            countryList: filter,
            search: e.target.value
        })
    }
    onClickItem = (country, event) => {
        try {
            this.props.handleOpenList(this.state.countryCode);
            this.setState({
                countryIcon: country.flag,
                countryCode: country.dial_code,
                countryList: customFlagList,
            })
            this.props.handleChangeMobileCode(country.dial_code, event)
        } catch (error) {
            console.log(error);
        }
    }
  
    render() {
        return (
            <div className={ `mobile-container mobile-input-background-en`}>

                <div className={ "cursor-pointer" } onClick={ () => this.props.handleOpenList() }>
                    <img src={ this.state.countryIcon } className="mobile-container-flag" />
                </div>

                <div className={ "cursor-pointer-caret" } onClick={ () => this.props.handleOpenList() }>
                    <img src={icon} className="mobile-container-caret" />
                </div>

                <div>
                    <span className="mobile-container-countrycode">
                        { this.state.countryCode }
                    </span>
                </div>
                <div className="mobile-container-input-container">

                    <input
                        maxLength={ 15 }
                        type={ "number" }
                        className="mobile-container-input"
                        value={ this.props.value }
                        name={ "mobile" }
                        onChange={this.props.onInputChange}
                    />
                </div>

                <Dialog
                    open={ this.props.open }
                    maxWidth={ "sm" }
                    onClose={ () => this.props.handleOpenList() }
                    aria-labelledby="responsive-dialog-title"
                >
                    <DialogTitle style={ {
                        paddingTop: "12px",
                        paddingLeft: "0px",
                        paddingRight: "0px",
                        paddingBottom: "0px",

                    } }>
                        <span style={ { fontSize: "14px", float: "left", padding: "10px" } }>
                            Search
                        </span>
                        <span onClick={ () => this.props.handleOpenList() }
                            style={ {
                                fontSize: "14px",
                                float: "right",
                                padding: "10px",
                                cursor: "pointer",
                                color: "rgb(70 70 70)",
                                fontWeight: "600",
                                textTransform: "capitalize"
                            } } >
                           Cancel
                        </span>

                        <div style={ {
                            paddingTop: "10px",
                            paddingLeft: "10px",
                            paddingRight: "10px",
                        } }>

                            <TextField
                                fullWidth
                                autoFocus
                                InputLabelProps={ { shrink: true } }
                                variant={ "standard" }
                                style={ {
                                    fontSize: "12px",
                                    // borderTop: "1px solid rgb(0 0 0 / 33%)"
                                } }

                                placeholder={ "Country Code" }
                                onChange={ (e) => this.filterCountries(e) }
                                InputProps={ {
                                    startAdornment: (
                                        <InputAdornment position="start">
                                            <SearchIcon />
                                        </InputAdornment>
                                    ),
                                } }
                                inputProps={ { maxLength: 15 } }
                            />
                        </div>
                    </DialogTitle>
                    <DialogContent className={ "mobile-list-width" } >
                        <div className={ "mobile-list" }>
                            {
                                this.state.search === ""
                                    ?
                                    <div className={ "flag-item" }
                                        onClick={ () => this.onClickItem(this.state.countryList[15]) }
                                    >
                                        <Grid container
                                            style={ {
                                                display: "flex",
                                                alignItems: "center",
                                            } }
                                        >
                                            <Grid xs={ 1 } item>
                                                <img className={ "mobile-flag-icon" } src={ (this.state.countryList[15] || []).flag }
                                                />
                                            </Grid>
                                            <Grid xs={ 8 } item style={ { fontWeight: "bold", paddingLeft: "10px" } }>
                                                { (this.state.countryList[15] || []).name }
                                            </Grid>
                                            <Grid xs={ 3 } item style={ {
                                                display: "flex",
                                                justifyContent: "flex-end"
                                            } }>
                                                { (this.state.countryList[15] || []).dial_code }
                                            </Grid>
                                        </Grid>

                                        {/* { index === 1 ? <hr style={ { marginBottom: "0px", marginTop: "5px", border: "1.4px solid rgb(0 0 0 / 10%)" } } /> : null } */ }
                                    </div>
                                    : null
                            }
                            {
                                (this.state.countryList || []).map((_country, index) => {
                                    return (
                                        <>
                                            {
                                                _country.title ?
                                                    <div style={ {
                                                        background: "grey",
                                                        color: "#3c3c3c",
                                                        padding: "10px",
                                                        fontWeight: "bolder",
                                                        textTransform: "uppercase"
                                                    } }>
                                                        { _country.name }
                                                    </div>
                                                    :
                                                    <div className={ "flag-item" }
                                                        onClick={ () => this.onClickItem(_country) }
                                                    >
                                                        <Grid container
                                                            style={ {
                                                                display: "flex",
                                                                alignItems: "center",
                                                            } }
                                                        >
                                                            <Grid xs={ 1 } item>
                                                                <img className={ "mobile-flag-icon" } src={ _country.flag }
                                                                />
                                                            </Grid>
                                                            <Grid xs={ 8 } item style={ { fontWeight: "bold", paddingLeft: "10px" } }>
                                                                { _country.name }
                                                            </Grid>
                                                            <Grid xs={ 3 } item style={ {
                                                                display: "flex",
                                                                justifyContent: "flex-end"
                                                            } }>
                                                                { _country.dial_code }
                                                            </Grid>
                                                        </Grid>

                                                        {/* { index === 1 ? <hr style={ { marginBottom: "0px", marginTop: "5px", border: "1.4px solid rgb(0 0 0 / 10%)" } } /> : null } */ }
                                                    </div>

                                            }

                                        </>
                                    )
                                })
                            }
                        </div>
                    </DialogContent>
                </Dialog>

            </div >


        )
    }
}

export default FlagInput;
