import React, { Component } from 'react';
import routes from '../constants/route-constants';
import HeadingWithText from './HeadingWithText';
import { Grid, makeStyles } from '@material-ui/core';
import { colors } from '../constants/colors';
import FormInputS from './form/FormInputS';
import { Link } from 'react-router-dom';
// import PropTypes from 'prop-types';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: colors.grey,
    display: 'flex',
    flexWrap: 'wrap',
    // '& > *': {
    //   marginLeft: theme.spacing(25),
    //   marginRight: theme.spacing(25),
    //   marginTop: theme.spacing(3),
    //   marginBottom: theme.spacing(2),
    //   padding: theme.spacing(3),
    //   width: '100%',
    //   //   height: theme.spacing(16),
    // },
  },
  box: {
    backgroundColor: colors.white,
    borderRadius: 10,
    padding: 50,
  },
  input: {
    display: 'flex',
    alignItems: 'center',
  },
}));

const TrackParcelSearchBar = (props) => {
  const classes = useStyles();
  const {
    heading,
    description,
    handleRouteTo,
    handleChange,
    orderNumber,
    error,
  } = props;
  return (
    <div className={`${classes.root} p-v-100 p-h-250`}>
      <Grid
        container
        alignItems="center"
        justify="center"
        className={classes.box}
      >
        <HeadingWithText heading={heading} text={description} />
        <div className="track-parcel-search-bar">
          <div>
            <input
              style={{
                borderBottom: error ? '1px solid red' : 'unset',
              }}
              maxLength={16}
              id="trackParcel"
              autoComplete="off"
              placeholder="Enter your ID to have access to your delivery"
              placeholderColor={colors.white}
              onChange={handleChange}
              value={orderNumber}
            />
            <button style={{ cursor: 'pointer' }} onClick={handleRouteTo}>
              Track
            </button>
          </div>
        </div>
        <p className="m-t-20">
          Have a question?{' '}
          <Link className="link" to="/help-center-for-customers">
            Contact us.
          </Link>
        </p>
      </Grid>
    </div>
  );
};

