import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { colors } from '../../constants/colors';
import ButtonComponent from '../ButtonComponent';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

export default function CallToActionComponents(props) {
  const classes = useStyles();
  const {
    heading,
    description,
    buttons,
    buttonCount,
    buttonText,
    image,
  } = props;

  return (
    <div className={classes.root}>
      <Grid
        container
        alignItems="center"
        className="fifth-section p-v-100 p-h-120"
        spacing={5}
        style={{ backgroundColor: colors.blue }}
      >
        {image ? (
          <Grid item xs={12} sm={6}>
            <img
              alt=""
              style={{ width: '100%' }}
              src={require('../../static/images/request-demo.png')}
            />
          </Grid>
        ) : null}

        <Grid item xs={12} sm={image ? '6' : '12'}>
          <div>
            <h1
              className="main-title text-center m-b-40"
              style={{ color: colors.white }}
            >
              {heading}
            </h1>
            <div className="m-b-100">
              <p className="text-center" style={{ color: colors.white }}>
                {description}
              </p>
            </div>
            {buttons ? (
              buttonCount > 1 ? (
                <Grid container className="demo-button" spacing={3}>
                  <Grid item xs={12} sm={6} className="text-center">
                    <ButtonComponent
                      bgColor={colors.darkBlue}
                      borderRadius="50px"
                      padding="15px"
                      width="200px"
                      color={colors.white}
                    >
                      Contact Sales
                    </ButtonComponent>
                  </Grid>
                  <Grid item xs={12} sm={6} className="text-center">
                    <ButtonComponent
                      bgColor={colors.white}
                      borderRadius="50px"
                      padding="15px"
                      width="200px"
                      color={colors.blue}
                    >
                      Request a demo
                    </ButtonComponent>
                  </Grid>
                </Grid>
              ) : (
                <Grid container className="demo-button" spacing={3}>
                  <Grid
                    item
                    xs={12}
                    sm={image ? '6' : '12'}
                    className="text-center"
                  >
                    <ButtonComponent
                      bgColor={colors.white}
                      borderRadius="50px"
                      padding="15px"
                      width="200px"
                      color={colors.blue}
                    >
                      {buttonText}
                    </ButtonComponent>
                  </Grid>
                </Grid>
              )
            ) : null}
          </div>
        </Grid>
      </Grid>
    </div>
  );
}
