import React, { Component } from 'react';
import { Grid } from '@material-ui/core';
import { colors } from '../constants/colors';

import PropTypes from 'prop-types';

class HeadingWithText extends Component {
  render() {
    return (
      <Grid item xs={12}>
        <div>
          <h1
            className="main-title text-center"
            style={{
              textAlign: 'center',
            }}
          >
            {this.props.heading}
          </h1>
          <p className="text-center">{this.props.text}</p>
        </div>
      </Grid>
    );
  }
}

HeadingWithText.propTypes = {
  heading: PropTypes.string,
};

export default HeadingWithText;
