import React from 'react';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import FormSubmitBtn from '../../../components/form/FormSubmitBtn';
import EmptyPlaceholder from '../../../components/ui/EmptyPlaceholder';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
    width: '75vw',
  }
  
});

export default function WarehouseListComponent(props) {
  const { warehouses, handleDeleteWarehouse, onWarehouseEdit } = props;
  const classes = useStyles();
  console.log('This is warehouse array', warehouses);
  return (
    <React.Fragment>
      <div className="p-col-12 p-md-6 p-lg-12">
        <p className="heading">Available Warehouses</p>
      </div>

      <TableContainer className="myScrollbarY" component={Paper}  style={{ overflow: 'auto', height: '500px'}}>
          <Table className="list-layout-table" aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell align="center">Name</TableCell>
                <TableCell>Address</TableCell>
                <TableCell align="center">Edit</TableCell>
                <TableCell align="center">Delete</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>

              {warehouses.length ? (warehouses.map((item, index) => (
                <>
                <TableRow className="onHoverTableRow" key={index} style={{
                  backgroundColor: index % 2 !== 0 ? '#e1e1e1' : 'none',
                  cursor: 'pointer',
                }}>
                  <TableCell align="center" scope="row">
                    {item.name}
                  </TableCell>
                  <TableCell>{item.address}</TableCell>
                  <TableCell align="center">
                    <EditIcon
                      style={{ cursor: 'pointer' }}
                      onClick={() => {
                        onWarehouseEdit(item);
                      }}
                    />
                  </TableCell>
                  <TableCell align="center">
                    <DeleteIcon
                      style={{ cursor: 'pointer', color: 'red' }}
                      onClick={() => {
                        handleDeleteWarehouse(item._id);
                      }}
                    />
                  </TableCell>
                </TableRow>
                </>
              ))
              ) : (
                <div className="p-col-12">
                  <EmptyPlaceholder message="We could not find any warehouses." />
                </div>
              )}
            
            </TableBody>
          </Table>
        </TableContainer>
    </React.Fragment>
  );
}
