import React, { useState } from 'react';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';
import { Link } from 'react-router-dom';
import {
  createMuiTheme,
  ListItemAvatar,
  makeStyles,
  MuiThemeProvider,
} from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
    
  },
 
  button: {
    '&:hover': {
      backgroundColor: 'transparent',
    },
  },
}));

export const theme = createMuiTheme({
  props: {
    // Name of the component
    MuiButtonBase: {
      // The properties to apply
      disableRipple: true, // No more ripple, on the whole application!
    },
  },
});

const CardWithImage = (props) => {
  const classes = useStyles();

  return (
    <React.Fragment>
      <MuiThemeProvider theme={ theme }>
        <Card className={ classes.root } style={ { margin: '10px 0' } }>
          <CardContent >
            <CardMedia
              style={ { objectFit: 'contain', marginTop: '10px' } }
              component="img"
              alt="profile"
              height="100"
              image={ props.image }
            />
            <CardContent>
              <Tooltip title={ props.title } placement="top-end">
                <h2
                  className={ 'my-news-title-hide' }
                  style={ { marginBottom: '2px', marginTop: '10px' } }
                >
                  { props.title }
                </h2>
              </Tooltip>

              { props.subTitle ? (
                <Tooltip title={ props.subTitle } placement="top-end">
                  <p
                    className={ 'my-news-content-hide' }
                    style={ { fontWeight: '700', minHeight: '40px' } }
                  >
                    { props.subTitle }
                  </p>
                </Tooltip>
              ) : null }

              <p
                className={ 'my-card-content-hide' }
                // variant="body2"
                // color="textSecondary"
                // component="p"
              >
                { props.description }
              </p>
              <div style={ { marginTop: '5px' } }>
                <p className={ 'see-more' }>
                  <Link to={ props.link ? props.link : '' }>
                    <button
                      style={ {
                        border: 'none',
                        background: 'transparent',
                      } }
                      className={ 'see-more' }
                    >
                      Read article { ' > ' }
                    </button>
                  </Link>
                </p>
              </div>
            </CardContent>
          </CardContent>
        </Card>
      </MuiThemeProvider>
    </React.Fragment>
  );
};

export default CardWithImage;
