/**
 * Validator for My Warehouses
 */

export const validateAdd = (data) => {
  const errors = {};
  let isValid = true;

  if (!data.name) {
    errors.name = true;
    isValid = false;
  }

  if (!data.dropoff.address) {
    errors.dropoff = true;
    isValid = false;
  }

  return { isValid, errors };
};
