import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { withStore } from '../../../utils/store.util';
import { colors } from '../../../constants/colors';

const NavbarItemTitle = styled.button`
  background: transparent;
  border: 0;
  // font-weight: bold;
  // font-family: inherit;
  font-family: 'Raleway-Bold';
  font-size: 16px;
  padding: 1rem 1.5rem 1.2rem 1rem;
  color: ${(props) => props.color};
  display: inline-block;
  margin-right: 20px;
  justify-content: center;
  transition: opacity 250ms;
  cursor: pointer;
  transition: all 0.5s ease-out;
    transition-property: all;
    transition-duration: 0.5s;
    transition-timing-function: ease-out;
    transition-delay: 0s;
  /* position above the dropdown, otherwise the dropdown will cover up the bottom sliver of the buttons */
  position: relative;
  z-index: 2;
  &:hover,
  &:focus {
    opacity: 0.7;
    outline: none;
    transition: all 0.5s ease-out;
    transition-property: all;
    transition-duration: 0.5s;
    transition-timing-function: ease-out;
    transition-delay: 0s;
  },
  &:before{
    content: "";
  }
`;

const NavbarItemEl = styled.li`
  position: relative;
`;

const DropdownSlot = styled.div`
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  perspective: 1500px;
`;

class NavbarItem extends Component {
  static propTypes = {
    onMouseEnter: PropTypes.func.isRequired,
    title: PropTypes.string.isRequired,
    index: PropTypes.number.isRequired,
    children: PropTypes.node,
  };
  onMouseEnter = () => {
    this.props.onMouseEnter(this.props.index);
  };

  render() {
    const { title, children } = this.props;
    const headershow = this.props.store.getFromStore('headerShow');
    return (
      <NavbarItemEl
        onMouseEnter={this.onMouseEnter}
        onFocus={this.onMouseEnter}
      >
        <NavbarItemTitle color={headershow ? colors.orange : colors.white}>
          {title}
        </NavbarItemTitle>
        <DropdownSlot>{children}</DropdownSlot>
      </NavbarItemEl>
    );
  }
}

export default withStore(NavbarItem);
