import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import i18n from 'i18next';
import PropTypes from 'prop-types';
import { colors } from '../constants/colors';
import Grid from '@material-ui/core/Grid';
import { Typography } from '@material-ui/core';
import { Dialog } from '@material-ui/core';
import _ from 'lodash';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import FormNativeSelectInput from '../components/form/FormNativeSelectInput';
import DialogTitle from '@material-ui/core/DialogTitle';
import { timeFormat } from '../constants/project-constants';
import { validateCallbackInformation } from '../components/callback-validator';
import ApiCalls from '../service/RequestHandler';
import FormTextArea from '../components/form/FormTextArea';
import FormInput from '../components/form/FormInputS';
import FormSubmitBtn from '../components/form/FormSubmitBtn';
import * as authUtil from '../utils/auth.util';
import { newGrowl } from '../components/ui/GrowlComponent';
import Divider from '@material-ui/core/Divider';
import EmptyPlaceholder from '../components/ui/EmptyPlaceholder';
import Container from '@material-ui/core/Container';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import routes from '../constants/route-constants';
import ButtonComponent from '../components/ButtonComponent';
import AppFooterComponent from '../components/app/AppFooterComponent';

const INITIAL_STATE = {
  errors: {},
  activity: false,
  name: null,
  number: null,
  language: '',
};

class Footer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      errors: {},
      activity: false,
      name: null,
      number: null,
      isDialogVisible: false,
      language: 'English',
      // language: i18n.language,
    };
  }

  handleChange = (event) => {
    let newlang = event.target.value;
    this.setState((prevState) => ({ language: newlang }));
    i18n.changeLanguage(newlang);
  };

  handleDialogClose = () => {
    this.setState({
      isDialogVisible: false,
    });
  };

  handleDialogVisibility = () => {
    this.setState({
      isDialogVisible: true,
    });
  };

  handleCallBack = async () => {
    if (this.isValid()) {
      const { name, number } = this.state;

      const data = {
        name: name,
        number: number,
      };
      this.setState({ activity: true });
      try {
        const response = await ApiCalls.requestCallBack(data);
        this.setState({
          ...INITIAL_STATE,
        });
        this.handleDialogClose();
        newGrowl.showGrowl(
          'success',
          'Success',
          'Your callback request has been submitted successfully.'
        );
      } catch (error) {
        this.setState({ activity: false });
      }
    }
  };

  isValid() {
    const { errors, isValid } = validateCallbackInformation(this.state);

    if (!isValid) {
      this.setState({ errors });
    }

    return isValid;
  }

  selectChange = (e) => {
    this.setState({
      language: e.target.value,
    });
  };

  onInputChange = (event) => {
    const { name, value, checked, type } = event.target;
    if (this.state.errors[name]) {
      this.setState((prevState) => {
        return {
          [name]: type === 'checkbox' ? checked : value,
          errors: { ...prevState.errors, [name]: false },
          getRate: false,
          // deliveryType: this.changeDeliveryType({
          //   pickupDate: prevState.pickupDate,
          //   deliveryDate: prevState.deliveryDate,
          //   [name]: value
          // })
        };
      });
    } else {
      this.setState({
        [name]: type === 'checkbox' ? checked : value,
        getRate: false,
        // deliveryType: this.changeDeliveryType({
        //   pickupDate: this.state.pickupDate,
        //   deliveryDate: this.state.deliveryDate,
        //   [name]: value
        // })
      });
    }
  };
  render() {
    console.log('Hello language', i18n.language);
    const { isDialogVisible, name, number, errors, activity } = this.state;
    const socialSites = [
      { label: 'facebook', link: 'https://www.facebook.com/' },
      { label: 'twitter', link: 'https://twitter.com/' },
      { label: 'linkedin', link: 'https://www.linkedin.com/' },
      { label: 'instagram', link: 'https://www.instagram.com/' },
      { label: 'youtube', link: 'https://www.youtube.com/' },
    ];
    return (
      <div>
        <div className="footer-container">
          <Container>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6} md={6} lg={3}>
                <Link to="/" style={{ outline: 'none' }}>
                  <img
                    className={'footer-logo'}
                    alt="Link to PPost Home"
                    src={`${process.env.PUBLIC_URL}/assets/images/footer-logo.png`}
                    // style={{ maxWidth: '8rem' }}
                  />
                </Link>
              </Grid>

              <Grid item xs={12} sm={6} md={6} lg={3}>
                <Grid container spacing={2}>
                  <Grid item xs={12} sm={12} style={{ paddingTop: '30px' }}>
                    <button className={'footer-buttons'}>Get in touch</button>
                  </Grid>
                  <Grid item xs={12} sm={12}>
                    <button className={'footer-buttons'}>
                      Become a driver
                    </button>
                  </Grid>
                </Grid>
              </Grid>

              <Grid item xs={12} sm={6} md={4} lg={2}>
                <h3 className={'footer-content-heading'}>ECOMMERCE SERVICES</h3>
                {[
                  {
                    label: 'Ship form store',
                    route: routes.ShipFromStore,
                  },
                  {
                    label: 'Client returns',
                    route: routes.ClientReturns,
                  },
                  // {
                  //   label: 'Frank for developers',
                  //   route: routes.FrankForDevelopers,
                  // },
                  {
                    label: 'Help center',
                    route: routes.HelpCenter,
                  },
                ].map((item, key) => {
                  return (
                    <Link className={'link'} key={key} to={item.route}>
                      <p className={'footer-content-links'}>{item.label}</p>
                    </Link>
                  );
                })}
              </Grid>

              <Grid item xs={12} sm={6} md={4} lg={2}>
                <h3 className={'footer-content-heading'}>HELP CENTER</h3>
                {[
                  {
                    label: 'Sign in',
                    route: '#',
                  },
                  {
                    label: `I'm a professional`,
                    route: '#',
                  },
                  {
                    label: `I'm a consumer`,
                    route: '#',
                  },
                  {
                    label: `I'm a courier`,
                    route: 'faqs',
                  },
                  {
                    label: 'Terms and conditions',
                    route: 'terms-and-conditions',
                  },
                ].map((item, key) => {
                  return (
                    <Link key={key} className={'link'} to={item.route}>
                      <p className={'footer-content-links'}>{item.label}</p>
                    </Link>
                  );
                })}
              </Grid>

              <Grid item xs={12} sm={6} md={4} lg={2}>
                <h3 className={'footer-content-heading'}>OUR COMPANY</h3>
                {[
                  {
                    key: 1,
                    label: 'About Frank',
                    route: routes.aboutUs,
                  },
                  {
                    label: `Careers`,
                    route: routes.Career,
                  },
                  {
                    label: `Newsroom`,
                    route: routes.Newsroom,
                  },
                  {
                    label: `Legal information`,
                    route: '',
                  },
                  {
                    label: 'Privacy Policy',
                    route: routes.privacyAndPolicy,
                  },
                ].map((item, key) => {
                  return (
                    <Link className={'link'} key={key} to={item.route}>
                      <p className={'footer-content-links'}>{item.label}</p>
                    </Link>
                  );
                })}
              </Grid>
            </Grid>
          </Container>
        </div>

        <Grid container style={{ background: colors.peach, width: '100%' }}>
          <Grid xs={12} md={12} lg={12} item>
            <hr className={'orange-divider'} />
          </Grid>
        </Grid>

        <div className="footer">
          <Container>
            <Grid container>
              <Grid xs={12} md={12} lg={4} item>
                <div style={{ marginTop: '10px', marginLeft: '5px' }}>
                  <FormControl>
                    <Select
                      value={this.state.language}
                      onChange={this.selectChange}
                      className="border-remove"
                    >
                      <MenuItem value="English">English</MenuItem>
                      <MenuItem value="French">French</MenuItem>
                    </Select>
                  </FormControl>
                </div>
                {/* <FormNativeSelectInput
                  addStyle={{
                    backgroundColor: 'transparent',
                    color: colors.orange,
                  }}
                  addSelectStyle={{
                    color: colors.orange,
                    backgroundPositionX: '20%',
                  }}
                  name="status"
                  value={this.state.language}
                  onChange={this.handleChange}
                  options={[
                    { value: 'en-US', label: 'English' },
                    { value: 'fre', label: 'French' },
                  ]}
                /> */}
              </Grid>
              <Grid xs={12} md={12} lg={5} item className={'social-margin-top'}>
                <div className={'mobile-application-container'}>
                  <p className={'download-label'}>Download our app &emsp;</p>
                  <a
                    href={'https://play.google.com/store/apps'}
                    target={'blank'}
                  >
                    <img
                      alt="google-play"
                      className={'google-play'}
                      src={require('../static/images/google-play.png')}
                    />
                  </a>
                  &nbsp; &nbsp; &nbsp;
                  <a href={'https://www.apple.com/app-store/'} target={'blank'}>
                    <img
                      alt="app-store"
                      className={'app-store'}
                      src={require('../static/images/app-store.png')}
                    />
                  </a>
                </div>
              </Grid>

              <Grid
                xs={12}
                md={12}
                lg={3}
                item
                className={'social-margin-top'}
                style={{ fontSize: '12px' }}
              >
                <div className={'mobile-application-container'}>
                  <p className={'download-label'}>Follow us:</p>
                  {socialSites.map((item, key) => {
                    return (
                      <a href={item.link} target={'blank'}>
                        <i
                          key={key}
                          className={`fab fa-2x fa-${item.label} icon-style`}
                        />
                      </a>
                    );
                  })}
                </div>
              </Grid>
            </Grid>
          </Container>
        </div>
        <Dialog
          open={isDialogVisible}
          onClose={this.handleDialogClose}
          aria-labelledby="actions-dialog"
          className="u-r-d-actions-dialog"
        >
          <div className="cancel-request-dialog">
            <DialogTitle>Request Call Back</DialogTitle>
            <DialogContent>
              <div>
                <div className="p-col-12 p-md-12 p-lg-12">
                  <p className="heading">Name</p>
                  <FormInput
                    placeholder="Name"
                    name="name"
                    value={name}
                    onChange={(e) =>
                      this.onInputChange({
                        target: {
                          name: 'name',
                          value: e.target.value,
                        },
                      })
                    }
                    error={errors.name}
                    left={
                      <img
                        style={{ width: '1rem' }}
                        alt=""
                        src={require('../static/icons/date-icon.png')}
                      />
                    }
                  />
                </div>
                <div className="p-col-12 p-md-12 p-lg-12">
                  <p className="heading">Phone Number</p>
                  <FormInput
                    placeholder="Phone Number"
                    name="number"
                    type="tel"
                    value={number}
                    onChange={(e) =>
                      this.onInputChange({
                        target: {
                          name: 'number',
                          value: e.target.value,
                        },
                      })
                    }
                    error={errors.number}
                    left={
                      <img
                        style={{ width: '1rem' }}
                        alt=""
                        src={require('../static/icons/date-icon.png')}
                      />
                    }
                  />
                </div>
              </div>
            </DialogContent>
            <DialogActions>
              <div>
                <FormSubmitBtn
                  label="Cancel"
                  disabled={activity}
                  onSubmit={this.handleDialogClose}
                  style={{ width: 'unset', borderRadius: 4 }}
                />
                <FormSubmitBtn
                  className="confirm-btn"
                  label="Submit"
                  disabled={activity}
                  onSubmit={this.handleCallBack}
                  style={{
                    width: 'unset',
                    borderRadius: 4,
                  }}
                />
              </div>
            </DialogActions>
          </div>
        </Dialog>
        {/* <div className="footer-foot">
          <AppFooterComponent />
        </div> */}
      </div>
    );
  }
}

Footer.propTypes = {
  bgImagePath: PropTypes.string,
};

export default Footer;
