import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Grid } from '@material-ui/core';
import { Typography } from '@material-ui/core';
import { NonFuncFeatures, NonFuncFeaturesCard } from '../NonFuncFeatures';
import Header from '../Home/Header/Header';
import Footer from '../Footer';
import ContactUs from '../ContactUs';
import Parcel from '../../components/Parcel';
import Benefits from '../../components/Benefits';
import Service from '../../components/Service';
import Feature from '../../components/Feature';
import { colors } from '../../constants/colors';
import ServicesComponent from '../ServicesComponent';
import SliderComponent from '../../components/SliderComponent';
import UseCasesCards from '../../components/ReusableComponents/HelpCenterComponents/UseCasesCards';
import ImageWithText from '../../components/ReusableComponents/ImageWithText';
import { useTranslation } from 'react-i18next';
import SignUpForm from '../../components/ReusableComponents/SignUpForm';
import ApiCalls from '../../service/RequestHandler';
import { validateLogin } from '../../features/login/validations/login-validator';
import { authClass } from '../../utils/auth.util';
import { toast } from 'react-toastify';
import Container from '@material-ui/core/Container';
import TextField from '@material-ui/core/TextField';
import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import './Login.css';

const apiCallType = {
  user: {
    login: ApiCalls.verifyLogin,
    createAccount: ApiCalls.verifyUOtp,
    resendCode: ApiCalls.resendCodeUser,
  },
  fleetmanager: {
    login: ApiCalls.twoFactorLoginTransporter,
    createAccount: ApiCalls.verifyTOtp,
    resendCode: ApiCalls.resendCodeTransporter,
  },
  merchant: {
    login: ApiCalls.loginMerchant,
    // createAccount: ApiCalls.verifyTOtp,
    // resendCode: ApiCalls.resendCodeTransporter,
  },
};

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
      errors: {},
      userType: props.match.params.userType,
    };
  }

  onSubmit = async () => {
    if (this.isValid()) {
      this.setState({ activity: true });
      const userType = `${this.props.location.pathname.substring(1)}`;

      console.log('userType', userType);
      const params = {
        email: this.state.email,
        password: this.state.password,
        platform: 'web',
      };
      try {
        const response = await apiCallType[userType].login(params);
        console.log('This is response of login', response.data);
        if (
          (response.data.transporterType === 'fleetOwner' ||
            response.data.platform === 'Prestashop') &&
          userType === 'merchant'
            ? response.data.active === true
            : 1
        ) {
          await authClass.setUser({
            ...response.data,
            userType,
            token: response.data.token,
          });
          toast.success('Login successfully');
          await this.props.history.push(`/${authClass.getUser.userType}/home`);
          this.props.handleLogin(true);
        } else {
          toast.error('Your store is not verified!');
        }
      } catch (error) {
        if (error.response) {
          console.log(
            'LOCATION',
            this.props.history.location.pathname.substring(1)
          );
          toast.error(
            userType === 'merchant'
              ? `${error.response.data.message} or your store is not verified!`
              : error.response.data.message
          );
        }
        console.log(error);
      }
    } else {
      toast.error('All fields are required');
    }
  };

  onInputChange = (e) => {
    e.persist();
    const key = e.target.name;
    if (this.state.errors[key]) {
      this.setState((prevState) => ({
        [key]: e.target.value,
        errors: { ...prevState.errors, [key]: false },
      }));
    } else {
      this.setState({ [key]: e.target.value });
    }
  };

  isValid() {
    const { errors, isValid } = validateLogin(this.state);

    if (!isValid) {
      this.setState({ errors });
    }
    return isValid;
  }

  renderSliderComponent = () => {
    const userType = this.props.history.location.pathname;
    if (userType === '/merchant') {
      return (
        <SliderComponent
          color={colors.white}
          heading="E-merchant BO access"
          description="Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book."
          image={require('../../static/images/Im-a-professional.png')}
        />
      );
    } else if (userType === '/fleetmanager') {
      return (
        <SliderComponent
          color={colors.white}
          heading="Carrier BO Access Or Fleet Manager"
          description="Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book."
          image={require('../../static/images/Im-a-courier.png')}
        />
      );
    }
  };

  render() {
    console.log('PARAMS', this.props.history.location.pathname);

    const userType = this.props.history.location.pathname;
    console.log('This is all usertype', userType);
    return (
      <React.Fragment>
        <Header
          showHeader={true}
          handleLoginModalOpen={this.props.handleLoginModalOpen}
        />
        <div>
          <div style={{ marginTop: '100px' }}>
            <Container>
              <Grid container>
                <Grid
                  item
                  xs={12}
                  sm={12}
                  md={6}
                  lg={6}
                  style={{ paddingRight: '20px' }}
                >
                  <Container>
                    <Grid container spacing={2}>
                      <Grid
                        item
                        xs={12}
                        sm={12}
                        md={12}
                        lg={12}
                        style={{ marginBottom: '30px', marginTop: '100px' }}
                      >
                        {userType === '/merchant' ? (
                          <h1 className="heading-size">Login to E-merchant</h1>
                        ) : (
                          <h1 className="heading-size">
                            Login to Fleet Manager
                          </h1>
                        )}
                      </Grid>
                      <Grid item xs={12} sm={12} md={12} lg={12}>
                        <div>
                          <TextField
                            required
                            id="email"
                            label="Email"
                            placeholder="Enter your email address"
                            fullWidth
                            margin="normal"
                            name="email"
                            value={this.state.email}
                            onChange={this.onInputChange}
                            error={this.state.errors.email}
                          />
                        </div>
                      </Grid>
                      <Grid
                        item
                        xs={12}
                        sm={12}
                        md={12}
                        lg={12}
                        style={{ marginBottom: '25px' }}
                      >
                        <div>
                          <TextField
                            required
                            id="password"
                            label="Password"
                            type="password"
                            placeholder="Enter your password"
                            fullWidth
                            margin="normal"
                            name="password"
                            value={this.state.password}
                            onChange={this.onInputChange}
                            error={this.state.errors.password}
                          />
                        </div>
                      </Grid>
                      <Grid item xs={6} sm={6} md={6} lg={6}>
                        <Link to={`/${userType}`}>
                          <p style={{ color: colors.orange }}>
                            Forget Password?
                          </p>
                        </Link>
                        <p
                          style={{
                            display: 'inline-block',
                            paddingRight: '20px',
                          }}
                        >
                          Don't have an account?{' '}
                          <Link to={`/create-account${userType}`}>
                            <span
                              style={{
                                color: colors.orange,
                                fontWeight: '700',
                              }}
                            >
                              Signup
                            </span>
                          </Link>
                        </p>
                      </Grid>
                      <Grid
                        item
                        xs={6}
                        sm={6}
                        md={6}
                        lg={6}
                        style={{ marginBottom: '70px' }}
                      >
                        <div style={{ textAlign: 'right' }}>
                          <Button
                            variant="contained"
                            style={
                              this.state.email === '' ||
                              this.state.password === ''
                                ? { minWidth: '100px' }
                                : {
                                    background: colors.orange,
                                    color: '#fff',
                                    minWidth: '100px',
                                  }
                            }
                            onClick={() => this.onSubmit()}
                            disabled={
                              this.state.email === '' ||
                              this.state.password === ''
                                ? true
                                : false
                            }
                          >
                            Login
                          </Button>
                        </div>
                      </Grid>
                      {/* <Grid item xs={12} sm={12} md={12} lg={12}>
                        <div style={{ textAlign: 'left' }}>
                          <div>
                            <p
                              style={{
                                display: 'inline-block',
                                paddingRight: '20px',
                              }}
                            >
                              Don't have an account?{' '}
                              <span
                                style={{
                                  color: colors.orange,
                                  fontWeight: '700',
                                }}
                              >
                                Sign Up
                              </span>
                            </p>
                            <Button
                              variant="contained"
                              style={{
                                background: colors.orange,
                                color: '#fff',
                                minWidth: '100px',
                              }}
                            >
                              Sign Up
                            </Button>
                          </div>
                        </div>
                      </Grid> */}
                    </Grid>
                  </Container>
                </Grid>

                <Grid
                  item
                  xs={12}
                  sm={12}
                  md={6}
                  lg={6}
                  className="content-hide-at-959px"
                >
                  <div>
                    <img
                      alt="image"
                      src={
                        userType === '/merchant'
                          ? require('../../static/images/Im-a-professional-signup.png')
                          : require('../../static/images/Im-a-courier-signup.png')
                      }
                    />
                  </div>
                </Grid>
              </Grid>
            </Container>
          </div>
        </div>

        {/* <div style={{ backgroundColor: colors.grey }}>
          {this.renderSliderComponent(userType)}

          <SignUpForm
            {...this.state}
            onSubmit={this.onSubmit}
            userType={userType}
            handleLoginModalOpen={this.props.handleLoginModalOpen}
            onInputChange={this.onInputChange}
            image={
              userType === 'merchant'
                ? require('../../static/images/Im-a-professional-signup.png')
                : require('../../static/images/Im-a-courier-signup.png')
            }
          />
          <Footer bgImagePath="/assets/images/footer-bg.png" />
        </div> */}
      </React.Fragment>
    );
  }
}

Login.propTypes = {
  handleLoginModalOpen: PropTypes.func,
};

export default Login;
