import React, { Component } from 'react';
import routes from '../../constants/route-constants';
import FormSubmitBtn from '../../components/form/FormSubmitBtn';
import { Link } from 'react-router-dom';
import MerchantHomeComponent from './MerchantHomeComponent';
import ApiCalls from '../../service/RequestHandler';
import * as authUtil from '../../utils/auth.util';
import 'primeicons/primeicons.css';
import 'primereact/resources/primereact.css';
import 'primeflex/primeflex.css';

class MerchantHomeContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activity: false,
      userType: '',
      store: '',
      lastWeek: 0,
      lastTwoWeeks: 0,
      lastMonth: 0,
      newShippingOrders: [],
      inProgressShippingOrders: [],
      delieveredShippingOrders: [],
      cancelledShippingOrders: [],
      newReturnOrders: [],
      inProgressReturnOrders: [],
      delieveredReturnOrders: [],
      cancelledReturnOrders: [],
    };
  }

  async componentDidMount() {
    try {
      const user = await authUtil.getUser();
      this.setState({ userType: user.userType, store: user._id });
      this.getGraphData();
      const responseShipping = await ApiCalls.getStoreStatsByBooleanId(0);
      const responseReturn = await ApiCalls.getStoreStatsByBooleanId(1);
      this.setState(
        {
          newShippingOrders: responseShipping.orders.filter(
            (order) => order._id.status === 'pending'
          ),
          inProgressShippingOrders: responseShipping.orders.filter(
            (order) => order._id.status === 'inprogress'
          ),
          delieveredShippingOrders: responseShipping.orders.filter(
            (order) => order._id.status === 'delivered'
          ),
          cancelledShippingOrders: responseShipping.orders.filter(
            (order) => order._id.status === 'cancelled'
          ),
          newReturnOrders: responseReturn.orders.filter(
            (order) => order._id.status === 'pending'
          ),
          inProgressReturnOrders: responseReturn.orders.filter(
            (order) => order._id.status === 'inprogress'
          ),
          delieveredReturnOrders: responseReturn.orders.filter(
            (order) => order._id.status === 'delivered'
          ),
          cancelledReturnOrders: responseReturn.orders.filter(
            (order) => order._id.status === 'cancelled'
          ),
        },
        () => {
          console.log('newShippingOrders', this.state.newShippingOrders);
        }
      );
    } catch (error) {
      console.log(error);
    }
  }

  changeBtnActive(val) {
    if (val === 'shippings-daily') {
      this.setState({
        shippingsChartBtns: {
          daily: true,
          monthly: false,
          yearly: false,
        },
      });
    } else if (val === 'shippings-monthly') {
      this.setState({
        shippingsChartBtns: {
          daily: false,
          monthly: true,
          yearly: false,
        },
      });
    } else if (val === 'shippings-yearly') {
      this.setState({
        shippingsChartBtns: {
          daily: false,
          monthly: false,
          yearly: true,
        },
      });
    } else if (val === 'returns-daily') {
      this.setState({
        returnsChartBtns: {
          daily: true,
          monthly: false,
          yearly: false,
        },
      });
    } else if (val === 'returns-monthly') {
      this.setState({
        returnsChartBtns: {
          daily: false,
          monthly: true,
          yearly: false,
        },
      });
    } else if (val === 'returns-yearly') {
      this.setState({
        returnsChartBtns: {
          daily: false,
          monthly: false,
          yearly: true,
        },
      });
    }
  }

  getGraphData = async () => {
    const { store } = this.state;
    try {
      const response = await ApiCalls.getStoreStatsById(store);
      this.state.lastWeek = response.data.shippedOrders.shippedThisWeek;
      this.state.lastTwoWeeks = response.data.shippedOrders.shippedTwoWeeks;
      this.state.lastMonth = response.data.shippedOrders.shippedLastMonth;
      this.setState({
        activity: true,
      });
    } catch (error) {
      console.log(error);
    }
  };
  render() {
    const menuItem = [
      {
        id: '1',
        label: 'Last week',
        data: this.state.lastWeek,
      },
      {
        id: '2',
        label: 'Last 2 weeks',
        data: this.state.lastTwoWeeks,
      },
      {
        id: '3',
        label: 'Last month',
        data: this.state.lastMonth,
      },
    ];

    return (
      <div style={{ padding: '10px' }}>
        <MerchantHomeComponent menu={menuItem} {...this.state} />
      </div>
    );
  }
}

export default MerchantHomeContainer;
