import React, { useState } from 'react';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';
import {
  createMuiTheme,
  makeStyles,
  MuiThemeProvider,
} from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
  },
  button: {
    '&:hover': {
      backgroundColor: 'transparent',
    },
  },
  Card: {
    '&:hover': {
      backgroundColor: 'blue',
    },
  },
}));

export const theme = createMuiTheme({
  props: {
    // Name of the component
    MuiButtonBase: {
      // The properties to apply
      disableRipple: true, // No more ripple, on the whole application!
    },
  },
});

const NewsCard = (props) => {
  const [id, setId] = useState([]);

  const loadMore = (e) => {
    const value = e.target.getAttribute('id');
    id.some((btnId) => btnId == value)
      ? setId(id.filter((el) => el != value))
      : setId(id.concat(value));
  };

  const classes = useStyles();

  return (
    <React.Fragment>
      <MuiThemeProvider theme={theme}>
        <Card className={classes.root}>
          <CardActionArea>
            <CardMedia
              style={{ objectFit: 'fill' }}
              component="img"
              alt="profile"
              height="230"
              image={props.image}
              className="item-figure"
            />
            <CardContent>
              <Tooltip title={props.title} placement="top-end">
                <h2
                  className={
                    id.some((btnId) => btnId == props.id)
                      ? null
                      : 'my-news-title-hide'
                  }
                  style={{ marginBottom: '2px' }}
                >
                  {props.title}
                </h2>
              </Tooltip>

              {props.subTitle ? (
                <Tooltip title={props.title} placement="top-end">
                  <p
                    className={
                      id.some((btnId) => btnId == props.id)
                        ? null
                        : 'my-news-content-hide'
                    }
                    style={{ fontWeight: '700', minHeight: '40px' }}
                  >
                    {props.subTitle}
                  </p>
                </Tooltip>
              ) : null}

              <Typography
                className={
                  id.some((btnId) => btnId == props.id)
                    ? null
                    : 'my-news-content-hide'
                }
                variant="body2"
                color="textSecondary"
                component="p"
              >
                {props.description}
              </Typography>
              <div style={{ marginTop: '20px' }}>
                <p className={'see-more'}>
                  <button
                    id={props.id}
                    onClick={(e) => loadMore(e)}
                    style={{
                      border: 'none',
                      background: 'transparent',
                    }}
                    className={'see-more'}
                  >
                    {id.some((btnId) => btnId == props.id)
                      ? 'Load less'
                      : 'Load More'}
                  </button>
                </p>
              </div>
            </CardContent>
          </CardActionArea>
        </Card>
      </MuiThemeProvider>
    </React.Fragment>
  );
};

export default NewsCard;
