import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import HeadingWithText from '../HeadingWithText';
import Link from '@material-ui/core/Link';
import { colors } from '../../constants/colors';

const useStyles = makeStyles((theme) => ({
  cards: {
    flexGrow: 1,
    boxShadow: '0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23)',
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,
  },
}));

export default function IconImageWithContent(props) {
  const {
    content,
    heading,
    description,
    columnUnit,
    link,
    linkText,
    backgroundColor,
  } = props;
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid
        container
        className={
          backgroundColor
            ? 'fourth-section padding-70 grey-color'
            : 'fourth-section padding-70'
        }
        spacing={5}
      >
        <HeadingWithText heading={heading} text={description} />
        {content.map((item, index) => (
          <Grid item xs={12} sm={columnUnit}>
            <Card
              className={classes.cards}
              style={{
                borderRadius: 20,
                marginBottom: 20,
                padding: 30,
                minHeight: 400,
              }}
            >
              <CardActionArea>
                <img
                  className={classes.media}
                  src={item.image}
                  style={{ width: 'auto' }}
                  title="Contemplative Reptile"
                />
                <CardContent style={{ padding: 0 }}>
                  <p
                    style={{
                      fontSize: 15,
                      fontWeight: 'bold',
                      textAlign: 'left',
                    }}
                  >
                    {item.label}
                  </p>
                  <p style={{ fontSize: 15, textAlign: 'left' }}>
                    {item.description}
                  </p>
                  {link ? (
                    <Link
                      style={{ color: colors.blue }}
                      variant="body2"
                      onClick={() => {
                        console.info("I'm a button.");
                      }}
                    >
                      {linkText}
                    </Link>
                  ) : null}
                </CardContent>
              </CardActionArea>
            </Card>
          </Grid>
        ))}
      </Grid>
    </div>
  );
}
