import React from 'react';
import FormSubmitBtn from '../../../components/form/FormSubmitBtn';
import Emails from './Emails';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
    width: '75vw',
  }
  
});

const emailHeader = [
  {
    label: 'Email',
    col: 4,
  },

  {
    label: 'Role',
    col: 1,
  },

  {
    label: 'Company',
    col: 2,
  },
  {
    label: 'Status',
    col: 2,
  },
];

export default function EmailListComponent(props) {
  const { emailAddresses } = props;
  const classes = useStyles();
  return (
    <div className="orders-add-container myScrollbarX"  style={{ overflowX: 'auto', width: '100%'}}>
      <div className="p-grid">
        <div className="p-col-12 p-md-6 p-lg-12">
          <p className="heading">Email Address</p>
        </div>
        
        <TableContainer className="myScrollbarY" component={Paper}  style={{ overflow: 'auto', height: '500px'}}>
          <Table className="list-layout-table" aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell align="center">Email</TableCell>
                <TableCell align="center">Role</TableCell>
                <TableCell align="center">Company</TableCell>
                <TableCell align="center">Status</TableCell>
                <TableCell align="center">Delete</TableCell>
                <TableCell align="center">Action</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {emailAddresses.map((item, index) => (
                <Emails data={item} index={index} {...props} />
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        

        {/* {emailHeader.map((item, index) => (
          <div
            className={`p-col-${item.col}`}
            style={{ fontWeight: 'bold', color: '#2c2d5b' }}
          >
            {item.label}
          </div>
        ))}

        {emailAddresses.map((item, index) => (
          <Emails data={item} {...props} />
        ))} */}
      </div>
    </div>
  );
}
