import React from 'react';
import PropTypes from 'prop-types';

import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import ListLayoutComponent from './ListLayoutComponent';
import Paginate from './Paginate';
import ContainerLayout from './layout/ContainerLayout';
import routes from '../constants/route-constants';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

const TabComponent = (props) => {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChangeTab = (event, newValue) => {
    setValue(newValue);
    handleChange(event, newValue);
  };

  const {
    activity,
    status,
    mode,
    requests,
    onInputChange,
    fetched,
    handlePageClick,
    handleChange,
    pageCount,
    tabItem,
    getRequests,
    categories,
  } = props;

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Tabs
          value={value}
          onChange={handleChangeTab}
          aria-label="simple tabs example"
        >
          {tabItem.map((item, index) => (
            <Tab label={item.label} {...a11yProps(item.id)} />
          ))}
        </Tabs>
      </AppBar>
      {tabItem.map((item, index) => (
        <TabPanel value={value} index={index}>
          {console.log('rendered : ', value)}
          <ContainerLayout>
            <ListLayoutComponent
              searchValue={props.searchValue}
              user={props.user}
              sortBy={props.sortBy}
              options={props.options}
              headings={props.headings}
              handleSort={props.handleSort}
              values={props.sortedRequests}
              shouldListLayoutComponentUpdate={
                props.shouldListLayoutComponentUpdate
              }
              goTo={`${routes.shipping}/${routes.shippingDetails}`}
            />
          </ContainerLayout>
          {fetched && requests[status].length && pageCount > 1 ? (
            <Paginate pageCount={pageCount} handlePageClick={handlePageClick} />
          ) : null}
        </TabPanel>
      ))}
    </div>
  );
};

export default TabComponent;
