import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Box from '@material-ui/core/Box';
import {Link} from 'react-router-dom';

function NotFound(props) {
    return (
        <div>
            <CssBaseline />
            <Container fixed>
                <Box className="margin-top-50" mx="auto" height='70vh' bgcolor="background.paper" p={1} py={5}>
                    <Typography gutterBottom color="error" align="center" variant="h1" component="h2">
                        404
                    </Typography>
                    <Typography gutterBottom align="center" variant="h6" component="h6">
                        OPPS! PAGE NOT FOUND
                    </Typography>
                    <Typography gutterBottom align="center" variant="p" component="p">
                        Sorry , the page you're looking for doesn't exist.
                    </Typography>
                    <Typography align="center" variant="div" component="div">
                        <Link to='/'>
                            <button className={'return-to-home-btn margin-top-50'}>
                                RETURN HOME
                            </button>
                        </Link>
                    </Typography>
                    
                </Box>
            </Container>
        </div>
    );
}

export default NotFound;