import React, { Component } from "react";
import PropTypes from "prop-types";

import moment from "moment";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import { colors } from "../../../constants/colors";
import ApiCalls from "../../../service/RequestHandler";
import { getPrimaryColors } from "../../../utils/functions";
import FullCalendarDemo from "./FullCalender";
import FormInputS from "../../../components/form/FormInputS";
import FormSubmitBtn from "../../../components/form/FormSubmitBtn";
import FormTelInput from "../../../components/form/FormTelInput";
import FormTextArea from "../../../components/form/FormTextArea";

export default class DeliveryInformationComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lastName: "",
      firstName: "",
      number: "",
      countryCode: "",
      deliveryDate: "",
      deliveryTime: "",
      showSlots: false,
    };
  }

  componentDidMount() {
    const { contact, deliveryDate, deliveryTime } = this.props.details;

    this.setState({
      lastName: contact.lastName,
      firstName: contact.firstName,
      number: contact.number,
      countryCode: contact.countryCode,
      deliveryDate: deliveryDate,
      deliveryTime: deliveryTime,
      showSlots: false,
    });
  }

  handleChange = (e) => {
    const { name, value } = e.target;
    this.setState({
      [name]: value,
      error: false,
      ...(name === "reason" ? { cancellationReason: value.value } : {}),
    });
  };

  onSlotSelect = (data) => {
    const { date, timeSlot } = data;
    this.setState({
      deliveryDate: date,
      deliveryTime: timeSlot,
    });
  };

  handleTimeSlot = () => {
    this.setState({ showSlots: true });
  };

  handleEditOrder = async () => {
    const { details } = this.props;
    const data = {
      token: details.editToken,
      _id: details._id,
      contact: {
        firstName: this.state.firstName,
        lastName: this.state.lastName,
        name: `${this.state.firstName} ${this.state.lastName}`,
        number: this.state.number,
        countryCode: "92",
      },
      deliveryDate: this.state.deliveryDate,
      deliveryTime: this.state.deliveryTime,
    };

    try {
      const response = await ApiCalls.editOrderPwa(data);
      this.props.handleDialogClose();
    } catch (error) {
      console.log(error);
    }
  };

  handleReasonLabelRef = (ref) => (this.reasonLabelRef = ref);
  handleSelectChange = (e) => {
    const { name, value } = e.target;
    if (value.name === "Other") {
      this.setState({
        dialogChangeUnits: {
          open: true,
          comp: "otherCommodity",
        },
      });
    }
  };

  onInputChange = (event) => {
    const { name, value, checked, type } = event.target;
    // if (this.state.errors[name]) {
    this.setState((prevState) => {
      return {
        [name]: type === "checkbox" ? checked : value,
        errors: { ...prevState.errors, [name]: false },
        getRate: false,
        // deliveryType: this.changeDeliveryType({
        //   pickupDate: prevState.pickupDate,
        //   deliveryDate: prevState.deliveryDate,
        //   [name]: value
        // })
      };
    });
    // } else {
    //   this.setState({
    //     [name]: type === 'checkbox' ? checked : value,
    //     getRate: false,
    //     // deliveryType: this.changeDeliveryType({
    //     //   pickupDate: this.state.pickupDate,
    //     //   deliveryDate: this.state.deliveryDate,
    //     //   [name]: value
    //     // })
    //   });
    // }
  };

  render() {
    const {
      activity,
      contactNumber,
      notes,
      firstName,
      lastName,
      number,
      deliveryTime,
      deliverTo,
      emergencyNumber,
      showSlots,
    } = this.state;
    const { details, user, dialogData } = this.props;

    const dropoff = details.dropoff ? details.dropoff.address : {};

    return (
      <div className="cancel-request-dialog">
        <DialogTitle style={{ textAlign: "center" }}>
          Delivery information
        </DialogTitle>
        <DialogContent>
          <div className="p-col-12 p-md-12 p-lg-12">
            <p className="heading">Address</p>
            <p style={{ color: "grey", fontSize: 16 }}>{dropoff}</p>
          </div>
          <div style={{ display: "flex" }}>
            <div style={{ flex: 1 }}>
              <p className="heading">Timeslot</p>
              <p style={{ color: "grey", fontSize: 16 }}>
                {deliveryTime ? deliveryTime.from : "-"} -{" "}
                {deliveryTime ? deliveryTime.to : "-"}
              </p>
            </div>
            <div>
              <p
                onClick={() => this.handleTimeSlot()}
                style={{ color: colors.orange, cursor: "pointer" }}
              >
                Change
              </p>
            </div>
          </div>
          {showSlots ? (
            <div className="p-col-12 p-md-12 p-lg-12">
              <FullCalendarDemo onSlotSelect={this.onSlotSelect} />
            </div>
          ) : null}
          <div className="p-col-12 p-md-12 p-lg-12">
            <p className="heading">Contact Information</p>
            <div style={{ display: "flex" }}>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  name="lastName"
                  value={lastName}
                  onChange={this.onInputChange}
                  placeholder="Last Name"
                  // error={errors.contactName}
                  left={
                    <img
                      style={{ width: "1rem" }}
                      alt=""
                      src={require("../../../static/icons/fullname-icon.png")}
                    />
                  }
                />
              </div>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  name="firstName"
                  value={firstName}
                  onChange={this.onInputChange}
                  placeholder="First Name"
                  // error={errors.contactName}
                  left={
                    <img
                      style={{ width: "1rem" }}
                      alt=""
                      src={require("../../../static/icons/fullname-icon.png")}
                    />
                  }
                />
              </div>
            </div>
            <div style={{ display: "flex" }}>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  inputType="number"
                  // error={errors.contactNumber}
                  input={
                    <FormTelInput
                      name="number"
                      value={number}
                      onChange={this.onInputChange}
                      placeholder="Phone Number"
                      ref={this.props.tellInputRef}
                      className="add-order-phone-input"
                      styles={{
                        background: "none",
                        padding: "5px 5px 5px 83px",
                        margin: "5px",
                        border: "none",
                        width: "100%",
                        fontSize: "1.2em",
                        outline: "none",
                        fontFamily: "Exo2-Medium",
                        color: getPrimaryColors("primary"),
                      }}
                    />
                  }
                />
              </div>
            </div>
          </div>
          <div className="p-col-12 p-md-12 p-lg-12">
            <p className="heading">About address</p>
            <div style={{ display: "flex" }}>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  name="contactName"
                  // value={contactName}
                  onChange={this.onInputChange}
                  placeholder="Building"
                  // error={errors.contactName}
                />
              </div>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  name="contactName"
                  // value={contactName}
                  onChange={this.onInputChange}
                  placeholder="Door"
                  // error={errors.contactName}
                />
              </div>
            </div>
            <div style={{ display: "flex" }}>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  name="contactName"
                  // value={contactName}
                  onChange={this.onInputChange}
                  placeholder="Digital code"
                  // error={errors.contactName}
                />
              </div>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  name="contactName"
                  // value={contactName}
                  onChange={this.onInputChange}
                  placeholder="Floor"
                  // error={errors.contactName}
                />
              </div>
            </div>
            <div className="p-col-12 p-md-12 p-lg-12">
              <FormTextArea
                rows={1}
                name="notes"
                value={notes}
                onChange={this.onInputChange}
                // error={errors.notes}
                placeholder="Additional information"
              />
            </div>
          </div>
        </DialogContent>
        <DialogActions>
          <FormSubmitBtn
            label="Back"
            // disabled={activity}
            onSubmit={this.props.handleDialogClose}
            style={{ width: "unset", borderRadius: 4 }}
          />
          <FormSubmitBtn
            label="Validate"
            // disabled={activity}
            onSubmit={() => {
              this.handleEditOrder();
            }}
            style={{
              width: "unset",
              borderRadius: 4,
            }}
          />
        </DialogActions>
      </div>
    );
  }
}
DeliveryInformationComponent.propTypes = {
  cancelType: PropTypes.string,
  handleSetDetails: PropTypes.func,
  handleDialogClose: PropTypes.func,
  details: PropTypes.shape({
    _id: PropTypes.string,
    pickupTime: PropTypes.shape(),
    pickupDate: PropTypes.string,
  }),
};
