import React, { Component } from 'react';
import FormInputS from '../../../components/form/FormInputS';
import FormSubmitBtn from '../../../components/form/FormSubmitBtn';
import { authClass } from '../../../utils/auth.util';
import { getPrimaryColors } from '../../../utils/functions';
import { classNames, getDiscountedPrice } from '../../../utils/functions';
import SelectLocationReusableComponent from '../../../components/map/SelectLocationReusableComponent';
import { Dialog } from '@material-ui/core';
import ApiCalls from '../../../service/RequestHandler';
import { newGrowl } from '../../../components/ui/GrowlComponent';
import ButtonComponent from '../../../components/ButtonComponent';
import { colors } from '../../../constants/colors';

const WarehousesAddComponent = (props) => {
  const {
    name,
    errors,
    dropoff,
    activity,
    dialogData,
    isDialogOpen,
    onInputChange,
    handleAddWarehouse,
    handleDialogVisibility,
    handleMapComponentRef,
    handleDialogCallback,
    handleEditWarehouse,
    isUpdate,
  } = props;

  return (
    <React.Fragment>
      <div className="p-col-12 p-md-6 p-lg-3">
        <p>Warehouse name</p>

        <FormInputS
          name="name"
          value={name}
          onChange={onInputChange}
          placeholder="Warehouse name"
          error={errors.name}
        />
      </div>
      <div className="p-col-12 p-md-12 p-lg-6">
        <p>Warehouse address</p>
        <div
          className={classNames([
            'pickup-dropoff-input-div',
            ['form-error', errors.dropoff],
          ])}
          onClick={() =>
            handleDialogVisibility(true, {
              dropoff: dropoff,
              heading: 'Select Location',
              name: 'dropoff',
            })
          }
        >
          <img
            style={{ width: '1rem' }}
            alt=""
            src={require('../../../static/icons/location-icon.png')}
          />
          <span
            style={{
              color: dropoff.address
                ? getPrimaryColors('primary')
                : getPrimaryColors('font-color'),
            }}
          >
            {dropoff.address || 'Select Warehouse Location'}
          </span>
        </div>
        {/* {errors.dropoff ? (
          <small>{this.props.pickupDropOffMessage}</small>
        ) : null} */}
      </div>
      <div className="p-col-12 p-md-6 p-lg-3">
        <ButtonComponent
          bgColor={colors.orange}
          borderRadius="50px"
          padding="10px 5px"
          width="200px"
          marginRight="20px"
          marginTop="35px"
          color={colors.white}
          onClick={isUpdate ? handleEditWarehouse : handleAddWarehouse}
        >
          Save
        </ButtonComponent>
      </div>
      <Dialog
        fullScreen={true}
        className="full-width-dialog full-height-dialog"
        style={{ margin: 50 }}
        open={isDialogOpen}
        onClose={handleDialogVisibility}
      >
        <SelectLocationReusableComponent
          editable={true}
          addressKey="address"
          ref={handleMapComponentRef}
          heading={isDialogOpen ? dialogData.heading: ''}
          markerCoords={dialogData}
          location={isDialogOpen ? dialogData.dropoff.location : []}
          handleMapClick={handleDialogCallback}
          handleSelectPlace={handleDialogCallback}
          actions={
            <div>
              <FormSubmitBtn
                label="Done"
                disabled={activity}
                style={{ borderRadius: 4, width: 'unset' }}
                onSubmit={handleDialogVisibility}
              />
            </div>
          }
        />
      </Dialog>
    </React.Fragment>
  );
};

export default WarehousesAddComponent;
