import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { validateAdd } from '../my-drivers-validator';
import ApiCalls from '../../../service/RequestHandler';
import MyDriversAddComponent from './MyDriversAddComponent';
import { toast } from 'react-toastify';
import { newGrowl } from '../../../components/ui/GrowlComponent';
import { isEmail } from '../../../utils/helpers';
import { readImageAsBase64Promise } from '../../../utils/functions';
const INITIAL_STATE = {
  files: [],
  documents: [],
  errors: {},
  activity: false,
  email: '',
  countryCode: '+33',
  mobile: '',
  lastName: '',
  firstName: '',
  open: false,
  licensePicture: [],
};

class MyDriversAddContainer extends Component {
  constructor(props) {
    super(props);
    this.state = INITIAL_STATE;
  }

  handleSubmit = async () => {
    if (this.isValid()) {
      this.setState({ activity: true });
      const data = { 
        email : this.state.email,
        firstName : this.state.firstName,
        lastName : this.state.lastName,
        licensePicture : this.state.licensePicture,
        mobile : this.state.countryCode + this.state.mobile
       };
      delete data.activity;
      delete data.errors;

      console.log('THIS IS ADD DRIVER', this.state);
      // if(isEmail(data.email)){
      //   try {
      //     const response = await ApiCalls.addDriver(data);
      //     toast.success('You have created a driver.');
      //     const myDrivers = [...this.props.myDrivers];
      //     myDrivers.push(response.data);
      //     this.props.handleSetMyDrivers(myDrivers);
      //   } catch (error) {
      //     toast.error(error.response.data.message);
      //     this.setState({ activity: false });
      //   }
      // }
      // else{
      //   toast.error('Please Enter a valid email');
      //   this.setState({ activity: false });
      // }
    }
  };
  handleChange = async (e) => {
    const { name, key, value, index } = e.target;
console.log(name,"Name");

console.log(key,"key");
console.log(value,"value");
console.log(index,"index");

switch (name) {
      case 'files': {
        const files = [...this.state.files];

        Promise.all(
          value.map((item) => {
            item.stateKey = key;
            files.push(item);
            return readImageAsBase64Promise(item);
          })
        ).then((res) => {
          this.setState({
            files,
            [key]: [...this.state[key], ...res],
          });
        });

        break;
      }
    }
  }
  removeDocument = (index, key) => {
    try {
      this.state[key].splice(index, 1);
      let currentKey = '';
      let currentKeyIndex = -1;
      const files = this.state.files.filter((item) => {
        if (currentKey !== item.stateKey) {
          currentKeyIndex = 0;
          currentKey = item.stateKey;
        } else {
          currentKeyIndex++;
        }
        if (currentKey === key && currentKeyIndex === index) {
          return false;
        }
        return true;
      });

      this.setState({ files });
    } catch (error) {
      //
    }
  };
  handleOpenList = (value) => {
    console.log(value);
    this.setState({ open: !this.state.open });
  };
  handleInputChange = (e) => {
    const { name, value } = e.target;

    if (this.state.errors[name]) {
      this.setState((prevState) => ({
        [name]: value,
        errors: { ...prevState.errors, [name]: false },
      }));
    } else {
      // if( name === "mobile" && this.state.mobile.length  <= 11){

      //  this.setState({ mobile: value.replace(/[^\dA-Z]/g, '').trim(), });
      // }
      // else if(name !== "mobile"){
      // alert("ye to else h ;(")
      this.setState({
        [name]: value,
      });
      // }
    }
  };

  isValid() {
    const { isValid, errors } = validateAdd(this.state);

    if (!isValid) {
      this.setState({ errors });
    }

    return isValid;
  }

  handleChangeMobileCode = (value) => {
    if (value !== undefined) {
      this.setState({
        countryCode: value,
      });
    }
  };

  render() {
    return (
      <MyDriversAddComponent
        {...this.state}
        onSubmit={this.handleSubmit}
        handleOpenList={this.handleOpenList}
        onInputChange={this.handleInputChange}
        handleChangeMobileCode={this.handleChangeMobileCode}
        handleChange={this.handleChange}
        removeDocument={this.removeDocument}
      />
    );
  }
}

MyDriversAddContainer.propTypes = {
  handleSetMyDrivers: PropTypes.func,
  myDrivers: PropTypes.arrayOf(PropTypes.object),
};

export default MyDriversAddContainer;
