import React,{useState,useEffect }from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import { getPrimaryColors } from '../../utils/functions';
import { IconButton, InputAdornment} from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import isEmpty,{isMatch , isEmail} from '../../utils/helpers';
import { toast } from 'react-toastify';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
export default function DialogModal(props) {
    const [password,setPassword] = useState('')
    const [newEmail,setNewEmail] = useState('')
    const [password2,setPassword2] = useState('')
    const [oldpassword,setOldpassword] = useState('')
    const [showPass, setShowPass] = useState(false)
  // const [open, setOpen] = React.useState(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

  // const handleClickOpen = () => {
  //   setOpen(true);
  // };

  // useEffect(() => {
  //   if(props.openModal){
  //     setOpen(true)
  //   }
  // else{
  //   console.log("propssss" , props);
  // }
  //   // return () => {
  //   //   cleanup
  //   // }
  // }, [props.openModal])
  
  const changeOldPassword = (e) => {
    setOldpassword(e.target.value)
}
  const changePassword = (e) => {
      setPassword(e.target.value)
  }
  const changePassword2 = (e) => {
    setPassword2(e.target.value)
}
const changeEmail = (e) => {
  setNewEmail(e.target.value)
}

  const UpdateEmailHandler = () => {

      if(isEmail(newEmail)){
       
        props.handleUpdateProfile(newEmail);
        props.getProfile(true);
        handleClose();
      }
      else if(isEmpty(newEmail)){
        toast.error('Email Field is required..')
      }
      else{ 
        toast.error('Please Enter a Valid Email.')
      }
  }



  const UpdatePasswordHandler = () => {
  if(!isEmpty(oldpassword) && isMatch(password,password2)){
        props.handleChangePassword(oldpassword,password)
        handleClose();
    }
    else if(isEmpty(password2) || isEmpty(password) || isEmpty(oldpassword)){
      toast.error('Please fill all the fields..')
     }
  
    else if (!isMatch(password,password2)){
      toast.error('Password Does Not Match.')
    }

}


  const handleClose = () => {
    console.log(props);
    props.ModelCloseHandler(false)
  };

  const handleClickShowPassword = () => {
   setShowPass(!showPass)
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };



  return (
      <div>
            {/* <button
              style={{
                width: 'unset',
                background: 'none',
                padding: 0,
                border: 'none',
                color: getPrimaryColors('primary'),
                filter: 'none',
                fontSize: '0.9em',
              }}
              onClick={handleClickOpen}
            >
              <i className="fas fa-pencil-alt" />
            </button> */}
      <Dialog
        fullScreen={fullScreen}
        open={props.openModal}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title"><span  style={{color: getPrimaryColors('primary')}} >{props.heading}</span></DialogTitle>
        <DialogContent>
          <DialogContentText>
              {props.password ? <>  
              <TextField 
              onChange={changeOldPassword} 
               label='Old Password'
                style={{width : '500px'}}
                type={showPass ? 'text' : 'password'}
                InputProps={{
                  endAdornment: <InputAdornment position="end">
                 <IconButton
                  aria-label="toggle password visibility"
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}
                  edge="end"
                >
                  {showPass ? <Visibility /> : <VisibilityOff />}
                </IconButton></InputAdornment>,
                }}  
                 /> 
               <TextField 
                onChange={changePassword} 
                label='New Password'
                style={{width : '500px' , marginTop:'5px'}}
                type={showPass ? 'text' : 'password'}
                InputProps={{
                  endAdornment: <InputAdornment position="end">
                 <IconButton
                  aria-label="toggle password visibility"
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}
                  edge="end"
                >
                  {showPass ? <Visibility /> : <VisibilityOff />}
                </IconButton></InputAdornment>,
                }}
                />
               <TextField
                onChange={changePassword2} 
                 label='Confirm Password' 
                 type={showPass ? 'text' : 'password'}
                 style={{width : '500px' , marginTop:'5px'}}
                 InputProps={{
                  endAdornment: <InputAdornment position="end">
                 <IconButton
                  aria-label="toggle password visibility"
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}
                  edge="end"
                >
                  {showPass ? <Visibility /> : <VisibilityOff />}
                </IconButton></InputAdornment>,
                }}
                 />
                 </>
              : <TextField   
              label={props.placeholder} 
              style={{width : '500px'}}    
               onChange={changeEmail} 
               />
              }
          </DialogContentText>
        </DialogContent>
        <DialogActions style={{marginTop:'10px'}}>
          <Button onClick={() => handleClose()}  style={{color: getPrimaryColors('primary')}}>
            Cancel
          </Button>
          <Button autoFocus  onClick={props.password ? UpdatePasswordHandler : UpdateEmailHandler}  style={{color: getPrimaryColors('primary')}} >
            Update
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
