import React from 'react';
import styled from 'styled-components';
import {
  Heading,
  HeadingLink,
  LinkList,
  DropdownSection,
  Icon,
} from './Components';
import routes from '../../../constants/route-constants';

const TrackingDropdownEl = styled.div`
  width: 18.5rem;
`;

const TrackingDropdown = () => {
  return (
    <TrackingDropdownEl>
      <DropdownSection data-first-dropdown-section>
        <ul>
          <HeadingLink>
            <a href={`#/${routes.FollowMyOrder}`}>Follow my order</a>
          </HeadingLink>
          <HeadingLink noMarginBottom>
            <a href={`#/${routes.HelpCenterForCustomers}`}>
              Help center for customers
            </a>
          </HeadingLink>
        </ul>
      </DropdownSection>
    </TrackingDropdownEl>
  );
};

export default TrackingDropdown;
