import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import ButtonComponent from '../ButtonComponent';
import { colors } from '../../constants/colors';
import ReactPlayer from 'react-player';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },

  reverse: {
    display: 'flex',
    flexDirection: 'row-reverse',
  },
}));

export default function ImageWithText(props) {
  const {
    heading,
    description,
    buttons,
    imageRight,
    image,
    backgroundColor,
    buttonCount,
    buttonText,
    mainColor,
    imagePadding,
    video,
  } = props;

  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid
        container
        alignItems="center"
        className={
          imageRight
            ? `${classes.reverse} p-h-70 p-v-100`
            : backgroundColor
            ? 'p-v-50 p-h-70 first-section grey-color'
            : imagePadding
            ? `${classes.reverse} p-l-120 grey-color`
            : `p-v-50 p-h-70 first-section `
        }
      >
        <Grid
          item
          xs={12}
          sm={6}
          className={imagePadding ? null : 'padding-50'}
        >
          {video ? (
            <ReactPlayer
              url="https://vimeo.com/251356858"
              width="100%"
              height="500px"
              playing
              light={require('../../static/images/video-placeholder-1.png')}
            />
          ) : (
            <img alt="intro-image" style={{ width: '100%' }} src={image} />
          )}
        </Grid>
        <Grid item xs={12} sm={6} style={{ paddingRight: 70 }}>
          <h1 className="main-title" style={{ color: mainColor }}>
            {heading}
          </h1>
          <div className="m-b-40">
            <p>{description}</p>
          </div>
          {buttons ? (
            buttonCount > 1 ? (
              <Grid container className="demo-button" spacing={3}>
                <Grid item xs={12} sm={6}>
                  <ButtonComponent
                    bgColor={colors.orange}
                    borderRadius="50px"
                    padding="15px"
                    width="200px"
                    color={colors.white}
                  >
                    Register here!
                  </ButtonComponent>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <ButtonComponent
                    bgColor={colors.lightOrange}
                    borderRadius="50px"
                    padding="15px"
                    width="200px"
                    color={colors.orange}
                  >
                    Need help
                  </ButtonComponent>
                </Grid>
              </Grid>
            ) : (
              <Grid container className="demo-button" spacing={3}>
                <Grid item xs={12} sm={image ? '6' : '12'}>
                  <ButtonComponent
                    bgColor={mainColor ? mainColor : colors.white}
                    borderRadius="50px"
                    padding="15px"
                    width="200px"
                    color={mainColor ? colors.white : colors.blue}
                  >
                    {buttonText}
                  </ButtonComponent>
                </Grid>
              </Grid>
            )
          ) : null}
        </Grid>
      </Grid>
    </div>
  );
}
