import React from 'react';
import TextField from '@material-ui/core/TextField';

const FormTextField = ({
  name,
  placeholder,
  value,
  label,
  error,
  type,
  onChange,
  required,
  select,
}) => {
  return (
    <TextField
      select={select}
      type={type}
      required={required}
      label={label}
      placeholder={placeholder}
      fullWidth
      margin="normal"
      name={name}
      value={value}
      onChange={onChange}
      error={error}
    />
  );
};

export default FormTextField;
