export const getMenu = (user) => {
  switch (user.userType) {
    case userTypes.USER: {
      return typeUserMenu;
    }
    case userTypes.MERCHANT: {
      return typeMerchantMenu;
    }
    case userTypes.FLEETMANAGER: {
      if (user.isSubDriver) {
        return typeDriverMenu;
      }
      return typeTransporterMenu;
    }
    default:
      return [];
  }
};

