const TempComponent = () => {
  return null;
};

export const getRoutes = (user) => {
  switch (user.userType) {
    case userTypes.USER: {
      return typeUserRoutes;
    }
    case userTypes.MERCHANT: {
      return typeMerchantRoutes;
    }
    case userTypes.FLEETMANAGER: {
      if (user.isSubDriver) {
        return typeDriverRoutes;
      }
      return typeTransporterRoutes;
    }
    default:
      return [{ path: '', component: TempComponent }];
  }
};
