import React from 'react';
import Page from '../../components/layout/Page';
import WarehousesAddComponent from './components/WarehousesAddComponent';
import ContainerLayout from '../../components/layout/ContainerLayout';
import WarehouseListComponent from './components/WarehouseListComponent';
import routes from '../../constants/route-constants';
import ButtonComponent from '../../components/ButtonComponent';
import Grid from '@material-ui/core/Grid';
import { colors } from '../../constants/colors';

const WarehousesComponent = (props) => {
  const { activity } = props;
  return (
    <Page activity={activity} className="orders-add-container" noActivity>
      <div className="page-title">
        <Grid container>
          <Grid item xs={12} sm={12} md={10} lg={10}>
            <div
              className="page-title"
              style={{ padding: '0px', marginBottom: '15px' }}
            >
              <span>Warehouse Settings</span>
            </div>
          </Grid>
          <Grid
            item
            xs={12}
            sm={12}
            md={2}
            lg={2}
          >
            <a href={'#merchant/store-settings'}>
              <ButtonComponent
                bgColor={colors.orange}
                borderRadius="50px"
                padding="15px"
                width="200px"
                color={colors.white}
                // onClick={() =>
                //   props.history.push(
                //     `/${routes.typeMerchant}/${routes.storeSettings}/${routes.storeSettings}`
                //   )
                // }
              >
                Account Settings
              </ButtonComponent>
            </a>
          </Grid>
        </Grid>
      </div>
      <ContainerLayout>
        <WarehousesAddComponent {...props} />
        <WarehouseListComponent {...props} />
      </ContainerLayout>
    </Page>
  );
};

export default WarehousesComponent;
