import React, { Component } from 'react';
import Container from '@material-ui/core/Container';
import { Grid } from '@material-ui/core';
import Carousel from 'react-multi-carousel';
import ApiCalls from '../../service/RequestHandler';
import NewsCard from './NewsCard';

class News extends Component {
  constructor(props) {
    super(props);
    this.state = {
      newsData: [],
    };
  }

  async componentDidMount() {
    const response = await ApiCalls.getNews();
    this.setState({
      newsData: response.data.news
        .filter((item, index) => item.active === true)
        .filter((item2, index2) => index2 < 6),
    });
  }

  render() {
    const responsive = {
      superLargeDesktop: {
        // the naming can be any, depends on you.
        breakpoint: { max: 4000, min: 3000 },
        items: 5,
      },
      desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 3,
      },
      tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 2,
      },
      mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 1,
      },
    };

    return (
      <div>
        <div className="my-bg-1">
          <Container>
            <Grid container spacing={2}>
              <Grid item md={12}>
                <h1 style={{ textAlign: 'center', marginBottom: '50px' }}>
                  Frank’s News
                </h1>
              </Grid>
            </Grid>
            {this.state.newsData.length > 0 ? (
              <div style={{ cursor: 'all-scroll' }}>
                <Carousel responsive={responsive}>
                  {this.state.newsData.map((item) => (
                    <Grid
                      item
                      xs={12}
                      sm={12}
                      md={12}
                      lg={10}
                      xlg={3}
                      style={{ paddingBottom: '10%' }}
                    >
                      <NewsCard
                        id={item._id}
                        image={item.image}
                        title={item.title}
                        description={item.description}
                      />
                    </Grid>
                  ))}
                </Carousel>
                <Grid container spacing={2}>
                  <Grid item md={12}>
                    <div style={{ paddingLeft: '42%', marginTop: '30px' }}>
                      <p className={'meet-team'}>
                        <a style={{ color: '#fff' }} href={'#/news'}>
                          See more news {'>'}
                        </a>
                      </p>
                    </div>
                  </Grid>
                </Grid>
              </div>
            ) : (
              <h3
                style={{
                  textAlign: 'center',
                  color: '#a9b1bbb0',
                  fontSize: '30px',
                }}
              >
                {' '}
                No news found{' '}
              </h3>
            )}
          </Container>
        </div>
      </div>
    );
  }
}

export default News;
