import React from 'react';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import BarChartIcon from '@material-ui/icons/BarChart';
import SettingsIcon from '@material-ui/icons/Settings';
import routes from '../constants/route-constants';
import { colors } from '../constants/colors';
import FormSearchInput from './form/FormSearchInput';

const SubHeader = (props) => {
  const { pageTitle, onClickSetting, onClickChart } = props;
  const ipadWidth = useMediaQuery('(max-width:768px)');
  const mobileWidth = useMediaQuery('(max-width:420px)');
  const veryLargeWidth = useMediaQuery('(min-width:1600px)');
  const [searchValue, setSearchValue] = React.useState('');

  const onChangeSearch = (value) => {
    setSearchValue(value);
    props.onChangeSearch(value);
  };
  return (
    <div className="page-title multiple-items">
      <span style={mobileWidth ? { width: '100%', paddingBottom: 10 } : {}}>
        {pageTitle}
      </span>
      <div style={{ display: 'flex' }}>
        {/* <BarChartIcon className="icon" onClick={onClickChart} /> */}
        <FormSearchInput
          style={{ marginRight: '1rem', width: '100%' }}
          name="search"
          value={searchValue}
          placeholder="Search Your Requests"
          right={
            <img alt="" src={require('../static/icons/icon-search.png')} />
          }
          onChange={onChangeSearch}
        />

        {/* <SettingsIcon onClick={onClickSetting} className="icon" /> */}
      </div>
    </div>
  );
};

export default SubHeader;
