import React from 'react';
import { Grid, makeStyles } from '@material-ui/core';
import ButtonComponent from '../ButtonComponent';
import { colors } from '../../constants/colors';
import Card from '@material-ui/core/Card';
import FormInputS from '../form/FormInputS';
import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  cards: {
    marginTop: -50,
    flexGrow: 1,
    marginLeft: 30,
    marginRight: 30,
    boxShadow: '0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23)',
  },
  p: {
    color: colors.orange,
    marginBottom: 5,
  },
  root: {
    overflow: 'visible',
    backgroundColor: 'transparent',
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },

  reverse: {
    display: 'flex',
    flexDirection: 'row-reverse',
    paddingLeft: 120,
    paddingRight: 120,
    paddingTop: 100,
    paddingBottom: 100,
  },
}));

const SignUpForm = (props) => {
  const {
    image,
    email,
    password,
    errors,
    onInputChange,
    onSubmit,
    userType,
    handleLoginModalOpen,
  } = props;

  const classes = useStyles();

  return (
    <div className={classes.root}>
      {/* <div style={{position: "absolute",top:300, left:0, height: "50%", width: "100%", backgroundColor: colors.grey}}></div> */}
      <Card
        className={classes.cards}
        style={{ borderRadius: 20, marginBottom: 20 }}
      >
        <Grid container alignItems="center">
          <Grid item xs={12} sm={6}>
            <img alt="intr0-imageu" style={{ width: '100%' }} src={image} />
          </Grid>
          <Grid item xs={12} sm={6} className="p-h-130">
            <h1 style={{ color: colors.darkBlue, marginBottom: '10px' }}>Sign In</h1>
            <Grid container>
              <Grid className="p-t-20" item xs={12} sm={12}>
                <p className={classes.p}>Email Address*</p>
                <FormInputS
                  style={{
                    border: `1px solid ${colors.orange} `,
                    marginBottom: '10px'
                  }}
                  name="email"
                  value={email}
                  onChange={onInputChange}
                  placeholder="Enter your email address"
                  error={errors.email}
                />
              </Grid>
              <Grid className="p-t-20" item xs={12} sm={12}>
                <p className={classes.p}>Password*</p>
                <FormInputS
                  style={{
                    border: `1px solid ${colors.orange} `,
                  }}
                  name="password"
                  value={password}
                  type="password"
                  onChange={onInputChange}
                  placeholder="Enter your Password"
                  error={errors.password}
                />
              </Grid>
              {/* <p className="p-t-10">Forgot/Reset Password</p> */}
              <Grid item className="p-t-40" xs={12} sm={12}>
                <ButtonComponent
                  style={{ marginBottom: '10px' }}
                  bgColor={colors.orange}
                  borderRadius="50px"
                  padding="15px"
                  width="200px"
                  color={colors.white}
                  onClick={onSubmit}
                >
                  Sign in
                </ButtonComponent>
                <br />
                <Link to={`/create-account/${userType}`}>
                  <ButtonComponent
                    bgColor={colors.orange}
                    borderRadius="50px"
                    padding="15px"
                    width="200px"
                    color={colors.white}
                  >
                    Register Now!
                  </ButtonComponent>
                </Link>

                {/* <Grid item className="p-t-20" xs={12} sm={12}>
                  <Link to={`/create-account/${userType}`}>Register Now!</Link>
                </Grid> */}
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Card>
    </div>
  );
};

export default SignUpForm;
