import React, { Component } from "react";
import PropTypes from "prop-types";

import moment from "moment";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";

import ApiCalls from "../../../service/RequestHandler";
import { getPrimaryColors } from "../../../utils/functions";
import { newGrowl } from "../../../components/ui/GrowlComponent";
import FormNativeSelectInputField from "../../../components/form/FormNativeSelectInputField";
import FormSubmitBtn from "../../../components/form/FormSubmitBtn";
import FormInputS from "../../../components/form/FormInputS";
import FormTelInput from "../../../components/form/FormTelInput";

export default class AbsentCaseComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lastName: "",
      firstName: "",
      number: "",
      deliverTo: "",
      emergencyNumber: "",
    };
  }

  componentDidMount() {
    const { contact, deliverTo, emergencyNumber } = this.props.details;
    this.setState({
      lastName: contact.lastName,
      firstName: contact.firstName,
      number: contact.number,
      deliverTo: { value: deliverTo, label: deliverTo },
      emergencyNumber: emergencyNumber,
    });
  }

  handleEditOrder = async () => {
    const { details } = this.props;
    const data = {
      token: details.editToken,
      _id: details._id,
      contact: {
        lastName: this.state.lastName,
        firstName: this.state.firstName,
        name: `${this.state.firstName} ${this.state.lastName}`,
        number: this.state.number,
        countryCode: "92",
      },
      deliverTo: this.state.deliverTo.value,
      emergencyNumber: this.state.emergencyNumber,
    };
    try {
      const response = await ApiCalls.editOrderPwa(data);
      // newGrowl.showGrowl(
      //   "success",
      //   "Success",
      //   "Your confirmation code is successfully submitted."
      // );
      this.props.handleDialogClose();
    } catch (error) {
      console.log(error);
    }
  };

  handleReasonLabelRef = (ref) => (this.reasonLabelRef = ref);

  handleSelectChange = (e) => {
    const { name, value } = e.target;

    this.setState((prevState) => {
      return {
        [name]: value,
      };
    });
  };

  onInputChange = (event) => {
    const { name, value, checked, type } = event.target;
    this.setState((prevState) => {
      return {
        [name]: type === "checkbox" ? checked : value,
      };
    });
  };

  render() {
    const {
      firstName,
      lastName,
      number,
      deliverTo,
      emergencyNumber,
    } = this.state;
    console.log("THis is all props i receive here in absent", this.props);
    return (
      <div className="cancel-request-dialog">
        <DialogTitle style={{ textAlign: "center" }}>
          In case you're absent
        </DialogTitle>
        <DialogContent>
          <div className="p-col-12 p-md-12 p-lg-12">
            <p className="heading">Contact Information</p>
            <div style={{ display: "flex" }}>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  name="lastName"
                  value={lastName}
                  onChange={this.onInputChange}
                  placeholder="Last Name"
                  // error={errors.contactName}
                  left={
                    <img
                      style={{ width: "1rem" }}
                      alt=""
                      src={require("../../../static/icons/fullname-icon.png")}
                    />
                  }
                />
              </div>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  name="firstName"
                  value={firstName}
                  onChange={this.onInputChange}
                  placeholder="First Name"
                  // error={errors.contactName}
                  left={
                    <img
                      style={{ width: "1rem" }}
                      alt=""
                      src={require("../../../static/icons/fullname-icon.png")}
                    />
                  }
                />
              </div>
            </div>
            <div style={{ display: "flex" }}>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  inputType="number"
                  // error={errors.contactNumber}
                  input={
                    <FormTelInput
                      name="number"
                      value={number}
                      onChange={this.onInputChange}
                      placeholder="Phone"
                      ref={this.props.tellInputRef}
                      className="add-order-phone-input"
                      styles={{
                        background: "none",
                        padding: "5px 5px 5px 83px",
                        margin: "5px",
                        border: "none",
                        width: "100%",
                        fontSize: "1.2em",
                        outline: "none",
                        fontFamily: "Exo2-Medium",
                        color: getPrimaryColors("primary"),
                      }}
                    />
                  }
                />
              </div>
            </div>
          </div>
          <div className="p-col-12 p-md-12 p-lg-12">
            <p className="heading">Specify delivery method</p>
            <div style={{ display: "inline-block" }}>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormNativeSelectInputField
                  type="react-select"
                  name="deliverTo"
                  options={[
                    { value: "At my doorstep", label: "At my doorstep" },
                    { value: "At pickup point", label: "At pickup point" },
                  ]}
                  value={deliverTo}
                  onChange={this.handleSelectChange}
                  // error={errors.type}
                  left={
                    <img
                      style={{ width: "1rem" }}
                      alt=""
                      src={require("../../../static/icons/request-type-icon.png")}
                    />
                  }
                />
              </div>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  inputType="number"
                  // error={errors.contactNumber}
                  input={
                    <FormTelInput
                      name="emergencyNumber"
                      value={emergencyNumber}
                      onChange={this.onInputChange}
                      placeholder="Enter emergency phone number"
                      ref={this.props.tellInputRef}
                      className="add-order-phone-input"
                      styles={{
                        background: "none",
                        padding: "5px 5px 5px 83px",
                        margin: "5px",
                        border: "none",
                        width: "100%",
                        fontSize: "1.2em",
                        outline: "none",
                        fontFamily: "Exo2-Medium",
                        color: getPrimaryColors("primary"),
                      }}
                    />
                  }
                />
              </div>
            </div>
          </div>
        </DialogContent>
        <DialogActions>
          <FormSubmitBtn
            label="Back"
            // disabled={activity}
            onSubmit={this.props.handleDialogClose}
            style={{ width: "unset", borderRadius: 4 }}
          />
          <FormSubmitBtn
            label="Validate"
            // disabled={activity}
            onSubmit={() => {
              this.handleEditOrder();
            }}
            style={{
              width: "unset",
              borderRadius: 4,
            }}
          />
        </DialogActions>
      </div>
    );
  }
}
AbsentCaseComponent.propTypes = {
  cancelType: PropTypes.string,
  handleSetDetails: PropTypes.func,
  handleDialogClose: PropTypes.func,
  details: PropTypes.shape({
    _id: PropTypes.string,
    pickupTime: PropTypes.shape(),
    pickupDate: PropTypes.string,
  }),
};
