import PropTypes from 'prop-types';
import React from 'react';

import useMediaQuery from '@material-ui/core/useMediaQuery';

import { Dialog, DialogTitle, DialogActions, Button } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import { colors } from '../../constants/colors';

import {
  currencyUnits,
  weightUnits,
  measurementUnits,
  languages,
} from '../../utils/unit.util';
import Page from '../../components/layout/Page';
import FormInputS from '../../components/form/FormInputS';
import { getPrimaryColors } from '../../utils/functions';
import FormSubmitBtn from '../../components/form/FormSubmitBtn';
import FormNativeSelectInputField from '../../components/form/FormNativeSelectInputField';
import ContainerLayout from '../../components/layout/ContainerLayout';
import EmailListComponent from './components/EmailListComponent';
import AddEmailComponent from './components/AddEmailComponent';
import ContactDetailsComponent from './components/ContactDetailsComponent';
import ChangePasswordComponent from './components/ChangePasswordComponent';
import routes from '../../constants/route-constants';
import DeleteAccountComponent from './components/DeleteAccountComponent';
import ButtonComponent from '../../components/ButtonComponent';

const StoreSettingsComponent = (props) => {
  const {
    isUpdate,
    errors,
    activity,
    dialogData,
    handleChange,
    handleDialogVisibility,
    handleChangePassword,
    emailAddresses,
    handleResendVerification,
    getProfile,
  } = props;

  const mobileWidth = useMediaQuery('(max-width:425px)');

  return (
    <Page activity={activity} className="orders-add-container" noActivity>

      <div className="page-title">
        <Grid container>
          <Grid item xs={12} sm={12} md={10} lg={10}>
            <div className="page-title" style={{padding: '0px', marginBottom: '15px'}}>
              <span>Account Settings</span>
            </div>
            
          </Grid>
          <Grid item xs={12} sm={12} md={2} lg={2}>
            <ButtonComponent
                bgColor={colors.orange}
                borderRadius="50px"
                padding="15px"
                width="200px"
                color={colors.white}
                onClick={() =>
                  props.history.push(
                    `/${routes.typeMerchant}/${routes.storeSettings}/${routes.warehouses}`
                  )
                }
            >
                Warehouse Settings
            </ButtonComponent>
          </Grid>
        </Grid>
        
      </div>
      
      <ContainerLayout>
        {/* <div style={{ textAlign: 'right', margin: '1rem 2rem' }}>
          <div>
            <ButtonComponent
              bgColor={colors.orange}
              borderRadius="50px"
              padding="15px"
              width="200px"
              marginRight="20px"
              color={colors.white}
              onClick={() =>
                props.history.push(
                  `/${routes.typeMerchant}/${routes.storeSettings}`
                )
              }
            >
              Account
            </ButtonComponent> */}
            {/* <ButtonComponent
              bgColor={colors.orange}
              borderRadius="50px"
              padding="15px"
              width="200px"
              marginRight="20px"
              color={colors.white}
              // onClick={() =>
              //   props.history.push(
              //     `/${routes.typeMerchant}/${routes.storeSettings}`
              //   )
              // }
            >
              Financial
            </ButtonComponent> */}

            {/* <ButtonComponent
              bgColor={colors.orange}
              borderRadius="50px"
              padding="15px"
              width="200px"
              marginRight="20px"
              color={colors.white}
              onClick={() =>
                props.history.push(
                  `/${routes.typeMerchant}/${routes.storeSettings}/${routes.warehouses}`
                )
              }
            >
              Warehouse
            </ButtonComponent>
          </div>
        </div> */}

        <ContactDetailsComponent {...props} />
        <EmailListComponent {...props} />
        <AddEmailComponent {...props} />
        <ChangePasswordComponent {...props} />
        <DeleteAccountComponent {...props} />
      </ContainerLayout>
      <div
        className="text-right"
        style={{
          padding: '2em',
          flexGrow: 1,
          display: 'flex',
          justifyContent: 'flex-end',
          alignItems: 'flex-end',
        }}
      >
        {isUpdate ? (
          <FormSubmitBtn
            label="Update"
            disabled={activity}
            onSubmit={props.handleUpdateUserProfile}
            style={{ width: 'unset', borderRadius: 4 }}
          />
        ) : null}
      </div>
      <Dialog
        className="full-width-dialog"
        maxWidth="sm"
        open={props.isDialogVisible}
        onClose={handleDialogVisibility}
      >
        <div className="user-settings-dialog">
          <DialogTitle>{dialogData.heading || ''}</DialogTitle>
          <div className="content">
            {dialogData.values.map((item, i) => (
              <div
                key={`update-item-${i + 1}`}
                style={{ paddingBottom: '1em' }}
              >
                <FormInputS
                  name={item.name}
                  label={item.label}
                  type={item.type}
                  value={props[item.name]}
                  placeholder={item.placeholder}
                  onChange={handleChange}
                  error={errors[item.name]}
                />
              </div>
            ))}
          </div>
          <DialogActions style={{ paddingTop: 0 }}>
            <FormSubmitBtn
              label="Submit"
              disabled={activity}
              onSubmit={
                isUpdate ? handleDialogVisibility : handleChangePassword
              }
              style={{
                width: 'unset',
                borderRadius: 4,
              }}
            />
          </DialogActions>
        </div>
      </Dialog>
    </Page>
  );
};

StoreSettingsComponent.defaultProps = {
  dialogData: { values: [] },
  profile: {
    picture: '',
    firstName: '',
    lastName: '',
  },
};

StoreSettingsComponent.propTypes = {
  isUpdate: PropTypes.bool,
  email: PropTypes.string,
  activity: PropTypes.bool,
  mobile: PropTypes.string,
  errors: PropTypes.shape(),
  lastName: PropTypes.string,
  firstName: PropTypes.string,
  handleChange: PropTypes.func,
  dialogData: PropTypes.shape(),
  isDialogVisible: PropTypes.bool,
  handleDialogVisibility: PropTypes.func,
  handleUpdateUserProfile: PropTypes.func,
  currency: PropTypes.shape({ value: PropTypes.string }),
  language: PropTypes.shape({ value: PropTypes.string }),
  weightUnit: PropTypes.shape({ value: PropTypes.string }),
  measurementUnit: PropTypes.shape({ value: PropTypes.string }),
};

export default StoreSettingsComponent;
