import React, { Component } from 'react';
import PropTypes from 'prop-types';

import ApiCalls from '../../service/RequestHandler';
import { authClass } from '../../utils/auth.util';
import CreateAccountComponent from './CreateAccountComponent';
import { newGrowl } from '../../components/ui/GrowlComponent';
import { validateCreateAccount } from './validators/create-account-validator';
import isEmpty, { isEmail, minLength, isNumber } from '../../utils/helpers';
import { toast } from 'react-toastify';

const apiCallType = {
  user: ApiCalls.signup,
  fleetmanager: ApiCalls.signupTransporter,
};

export default class CreateAccountContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activity: false,
      userType: props.match.params.type,
      verify: false,
      errors: {},
      countryCode: '',

      firstName: '',
      lastName: '',
      email: '',
      mobile: '',
      password: '',
      numberOfVehicles: '',
      numberOfDrivers: '',
      agreeToTerms: false,
      preference: false,
    };

    this.handleTelInputRef = (ref) => (this.telInput = ref);
  }

  handleSubmit = async (e) => {
    e.preventDefault();

    if (this.isValid()) {
      const countryCode = this.state.countryCode;
      // const countryCode = this.telInput.iti.getSelectedCountryData().dialCode;
      this.setState({ activity: true });
      try {
        if (isEmpty(this.state.firstName)) {
          newGrowl.showGrowl('error', 'Error', 'First name is required!');
        } else if (isEmpty(this.state.lastName)) {
          newGrowl.showGrowl('error', 'Error', 'Last name is required!');
        } else if (isEmpty(this.state.email)) {
          newGrowl.showGrowl('error', 'Error', 'Email address is required!');
        } else if (!isEmail(this.state.email)) {
          newGrowl.showGrowl(
            'error',
            'Error',
            'Email address field contains only email addresses!'
          );
        } else if (isEmpty(this.state.password)) {
          newGrowl.showGrowl('error', 'Error', 'Password is required!');
        } else if (!minLength(this.state.password)) {
          newGrowl.showGrowl(
            'error',
            'Error',
            'Password length required at least 4 characters!'
          );
        } else if (isEmpty(this.state.countryCode)) {
          newGrowl.showGrowl('error', 'Error', 'Country code is required!');
        } else if (!isNumber(this.state.countryCode)) {
          newGrowl.showGrowl(
            'error',
            'Error',
            'Country code field contains only numbers!'
          );
        } else if (this.state.countryCode.length > 4) {
          newGrowl.showGrowl(
            'error',
            'Error',
            'Country code field contains maximum 4 numbers!'
          );
        } else if (isEmpty(this.state.mobile)) {
          newGrowl.showGrowl('error', 'Error', 'Mobile number is required!');
        } else if (!isNumber(this.state.mobile)) {
          newGrowl.showGrowl(
            'error',
            'Error',
            'Mobile number field contains only numbers!'
          );
        } else if (
          this.state.mobile.length < 8 ||
          this.state.mobile.length > 12
        ) {
          newGrowl.showGrowl(
            'error',
            'Error',
            'Mobile number field contains numbers between 8 to 12!'
          );
        } else if (isEmpty(this.state.numberOfVehicles)) {
          newGrowl.showGrowl('error', 'Error', 'No of vehicles is required!');
        } else if (!isNumber(this.state.numberOfVehicles)) {
          newGrowl.showGrowl(
            'error',
            'Error',
            'No of vehicles field contains only numbers!'
          );
        } else if (isEmpty(this.state.numberOfDrivers)) {
          newGrowl.showGrowl('error', 'Error', 'No of Drivers is required!');
        } else if (!isNumber(this.state.numberOfDrivers)) {
          newGrowl.showGrowl(
            'error',
            'Error',
            'No of Drivers field contains only numbers!'
          );
        }
        const response = await apiCallType[this.state.userType]({
          firstName: this.state.firstName,
          lastName: this.state.lastName,
          email: this.state.email,
          password: this.state.password,
          numberOfVehicles: this.state.numberOfVehicles,
          numberOfDrivers: this.state.numberOfDrivers,
          mobile: countryCode + this.state.mobile,
          transporterType: 'fleetOwner',
        });
        const data = response.data;
        if (data.token) {
          const userDataToSave = {
            token: data.token,
          };
          // await authClass.setUser(userDataToSave);
          // authClass.setUser = userDataToSave;
          this.setState({
            activity: false,
            verify: true,
            countryCode,
          });
          toast.success('You are registered successfully');
          this.props.history.push(`/${this.state.userType}`);
        }
      } catch (error) {
        newGrowl.showGrowl('error', 'Error', error.response.data.message);
        this.setState({ activity: false });
      }
    }
  };

  handleChange = (key, value) => {
    if (this.state.errors[key]) {
      this.setState((prevState) => ({
        [key]: value,
        errors: { ...prevState.errors, [key]: false },
      }));
    } else {
      if (key === 'countryCode') {
        if ((isNumber(value) && value.length < 5) || value === '') {
          this.setState({ [key]: value });
        }
      } else if (key === 'mobile') {
        if ((isNumber(value) && value.length < 13) || value === '') {
          this.setState({ [key]: value });
        }
      } else if (key === 'numberOfVehicles') {
        if (
          (isNumber(value) && value.length < 3 && value != 0) ||
          value === ''
        ) {
          this.setState({ [key]: value });
        }
      } else if (key === 'numberOfDrivers') {
        if (
          (isNumber(value) && value.length < 3 && value != 0) ||
          value === ''
        ) {
          this.setState({ [key]: value });
        }
      } else {
        this.setState({ [key]: value });
      }
    }
  };

  showPreference = () => {
    this.setState({
      preference: true,
    });
  };

  isValid() {
    const { errors, isValid } = validateCreateAccount(this.state);

    if (!isValid) {
      this.setState({ errors });
    }
    return isValid;
  }

  handleNavigateTo = (route) => {
    const { _id, firstName, lastName } = authClass.getUser;
    const data = {
      _id,
      firstName,
      lastName,
    };

    if (this.state.userType === 'transporter') {
      ApiCalls.createIdenfyToken(data);
    }
    this.props.history.push(route);
  };

  render() {
    return (
      <CreateAccountComponent
        {...this.state}
        handleChange={this.handleChange}
        handleSubmit={this.handleSubmit}
        handleTelInputRef={this.handleTelInputRef}
        handleNavigateTo={this.handleNavigateTo}
        showPreference={this.showPreference}
        handleLoginModalOpen={this.props.handleLoginModalOpen}
        handleLoginModalClose={this.props.handleLoginModalClose}
      />
    );
  }
}

CreateAccountContainer.propTypes = {
  history: PropTypes.shape({ push: PropTypes.func }),
  match: PropTypes.shape({
    params: PropTypes.shape({ type: PropTypes.string }),
  }),
  handleLoginModalOpen: PropTypes.func,
  handleLoginModalClose: PropTypes.func,
};
