import React from 'react';
import FormSubmitBtn from '../../../components/form/FormSubmitBtn';
import ApiCalls from '../../../service/RequestHandler';
import { newGrowl } from '../../../components/ui/GrowlComponent';
import ButtonComponent from '../../../components/ButtonComponent';
import { colors } from '../../../constants/colors';
import DeleteIcon from '@material-ui/icons/Delete';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';

export default function Emails(props) {
  const { data, handleResendVerification, index } = props;
  return (
    
    <React.Fragment>
      <TableRow className="onHoverTableRow" key={index} style={{
                  backgroundColor: index % 2 !== 0 ? '#e1e1e1' : 'none',
                  cursor: 'pointer',
                }}>
        <TableCell align="center" scope="row">
          {data.email}
        </TableCell>
        <TableCell align="center">{data.role ? data.role : 'Admin'}</TableCell>
        <TableCell align="center">{data.company ? data.company : 'Le choclate'}</TableCell>
        <TableCell align="center">{data.verified ? 'verified' : 'unverified'}</TableCell>
        <TableCell align="center">
          <DeleteIcon
            style={{ cursor: 'pointer', color: 'red' }}
            onClick={() => {alert('TODO: Delete Email')}}
          />
        </TableCell>
        <TableCell align="center">
          <ButtonComponent
            bgColor="transparent"
            borderRadius="50px"
            border={`1px solid ${colors.orange}`}
            padding="5px 1em"
            width="200px"
            marginRight="20px"
            color={colors.orange}
            onClick={() => {
              handleResendVerification(data);
            }}
          >
            Resend Verification
          </ButtonComponent>
        </TableCell>
      </TableRow>
      {/* <div className="p-col-4">{data.email}</div>
      <div className="p-col-1">{data.role ? data.role : 'Admin'}</div>
      <div className="p-col-2">
        {data.company ? data.company : 'Le choclate'}
      </div>
      
      <div className="p-col-2">{data.verified ? 'verified' : 'unverified'}</div>
      <div className="p-col-1">
        <DeleteIcon
          style={{ cursor: 'pointer', color: 'red' }}
          onClick={() => {alert('TODO: Delete Email')}}
        />
      </div>
      <div className="p-col-2">
        <ButtonComponent
          bgColor="transparent"
          borderRadius="50px"
          border={`1px solid ${colors.orange}`}
          padding="5px 1em"
          width="200px"
          marginRight="20px"
          color={colors.orange}
          onClick={() => {
            handleResendVerification(data);
          }}
        >
          Resend Verification
        </ButtonComponent>
      </div> */}
      
    </React.Fragment>
  );
}
