import React, { Component } from 'react';
import { getPrimaryColors } from '../../../utils/functions';
import FormInputS from '../../../components/form/FormInputS';
import FormSubmitBtn from '../../../components/form/FormSubmitBtn';
import ContainerLayout from '../../../components/layout/ContainerLayout';
import ApiCalls from '../../../service/RequestHandler';
import { newGrowl } from '../../../components/ui/GrowlComponent';
import ButtonComponent from '../../../components/ButtonComponent';
import { colors } from '../../../constants/colors';
import { roles } from '../../../utils/unit.util';
import FormNativeSelectInputField from '../../../components/form/FormNativeSelectInputField';

const AddEmailComponent = (props) => {
  const {
    email,
    role,
    onInputChange,
    handleAddEmail,
    handleSelectChange,
  } = props;
  return (
    <div className="orders-add-container">
      <div className="p-grid">
        <div className="p-col-12 p-md-6 p-lg-5">
          <p>Add a new email address</p>
          <FormInputS
            type="email"
            name="email"
            value={email}
            onChange={onInputChange}
            placeholder="email"
            // error={errors.contactName}
          />
        </div>
        <div className="p-col-12 p-md-6 p-lg-5">
          <p> Role</p>
          <FormNativeSelectInputField
            name="role"
            type="react-select"
            style={{ padding: 0 }}
            // error={this.state.error}
            value={
              roles.find((options) => options.value === role.value) || {
                value: '',
                label: 'Role',
              }
            }
            onChange={handleSelectChange}
            options={roles}
            getOptionValue={(item) => item._id}
            // formatOptionLabel={(item) => getVehicleName(item)}
          />
        </div>
        {/* <div className="p-col-12 p-md-6 p-lg-5">
          <p>Role</p>
          <FormInputS
            name="role"
            value={role}
            onChange={onInputChange}
            placeholder="role"
            // error={errors.contactName}
          />
        </div> */}
        <div className="p-col-12 p-md-6 p-lg-2" style={{ padding: '1em' }}>
          <ButtonComponent
            bgColor={colors.orange}
            borderRadius="50px"
            padding="10px 5px"
            width="200px"
            marginRight="20px"
            marginTop="27px"
            color="#fff"
            onClick={handleAddEmail}
          >
            Add
          </ButtonComponent>
        </div>
      </div>
    </div>
  );
};

export default AddEmailComponent;
