import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { importFirebase } from '../../../utils/functions';
import Grid from '@material-ui/core/Grid';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import { authClass } from '../../../utils/auth.util';
import LocationOffOutlinedIcon from '@material-ui/icons/LocationOffOutlined';
import { getActions } from '../../../utils/request-details.util';
import Avatar from '@material-ui/core/Avatar';
import { colors } from '../../../constants/colors';
import './styles.css';
import DateRangeIcon from '@material-ui/icons/DateRange';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import PersonIcon from '@material-ui/icons/Person';
import LocationOffIcon from '@material-ui/icons/LocationOff';
import PhoneOutlinedIcon from '@material-ui/icons/PhoneOutlined';

import { Dialog } from '@material-ui/core';
import moment from 'moment';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import PersonOutlineOutlinedIcon from '@material-ui/icons/PersonOutlineOutlined';
import StarRatingComponent from 'react-star-rating-component';
import CircularProgress from '@material-ui/core/CircularProgress';
import Divider from '@material-ui/core/Divider';

import Carousel from 'react-material-ui-carousel';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import {
  getVehicleData,
  getContactDetails,
  getPickupDropoffDetails,
  getPrice,
  requestInActive,
} from '../../../utils/request-details.util';
import {
  dateFormat,
  trackDateTimeFormat,
  dateTimeFormat,
} from '../../../constants/project-constants';
import { withStore } from '../../../utils/store.util';
import ApiCalls from '../../../service/RequestHandler';
import { getPrimaryColors } from '../../../utils/functions';
import { convertWeight, convertMeasurement } from '../../../utils/unit.util';
import RoomOutlinedIcon from '@material-ui/icons/RoomOutlined';
import MicRecorder from 'mic-recorder-to-mp3';
import ChatComponent from '../../../components/chat/ChatComponent';
import GoogleMapsComponent from '../../../components/map/GoogleMapsComponent';
import { newGrowl } from '../../../components/ui/GrowlComponent';
import AbsentCaseComponent from '../components/AbsentCaseComponent';
import DeliveredDetails from '../components/DeliveredDetails';
import DeliveryInformationsComponent from '../components/DeliveryInformationsComponent';
import ProductCarousel from '../components/ProductCarousel';
import DeliveryDriver from '../components/deliveryDriver';
import NotHomeComponent from '../components/NotHomeComponent';
import DeliveryDetailsComponent from '../components/DeliveryDetailsComponent';
import GoogleMapsTrackerComponent from '../../../components/map/GoogleMapsTrackerComponent';
import { cancelledStatus, trackingStatus } from '../../../utils/Helper';

const Mp3Recorder = new MicRecorder({ bitRate: 128 });

const normalizeData = (type, state, contactNumber) => {
  let data = {};
  if (type === 'submit') {
    data = {
      token: state.token,
      _id: '5d4d69b869d50d001742750f',
      contact: {
        name: 'Test User',
        number: '123456789',
        countryCode: '92',
      },
      deliveryDate: state.deliveryDate,
      deliveryTime: state.deliveryTime,
      deliverTo: state.deliverTo,
      emergencyNumber: state.emergencyNumber,
    };

    return data;
  }
};

class ShippingListDetailsContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: {},
      activity: true,
      notification: true,
      details: {},
      showChat: false,
      messages: [],
      dialogData: { open: false, comp: '' },
      login: false,
      orders: [],
      rating: 0,
      comment: '',
      transporterDetails: {},
    };
    this.orderNumber = props.match.params.id;
    this.token = props.match.params.token;
  }

  componentDidMount() {
    this.getRequestDetails();
  }

  async getRequestDetails() {
    try {
      const response = await ApiCalls.trackOrder({ _id: this.orderNumber });

      console.log('shipping response', response);

      this.setState(
        {
          activity: false,
          ...(response.data ? { details: response.data } : {}),
        },
        () => {
          console.log(
            'THis is all order detail through email',
            this.state.details
          );
        }
      );
      this.handleFirebase();
    } catch (error) {
      this.setState({ activity: false });
    }
  }

  handleEditOrder = async () => {
    const data = normalizeData('submit', this.state);
    console.log('This is all data i received in handle submit order', data);
    try {
      const response = await ApiCalls.editOrderPwa(data);
      console.log('This is the response of handle return', response);
      this.setState((prevState) => {
        return {
          status: 3,
        };
      });
    } catch (error) {
      console.log(error);
    }
  };

  handlePicturesClick = (images) => {
    this.props.store.setMultiWithRender({
      lightBoxIsOpen: true,
      lightBoxImages: images.map((item) => ({ src: item })),
    });
  };

  handleChange = (event) => {
    this.setState({ [event.target.name]: event.target.checked });
  };

  handleToggleChat = (value) => {
    this.setState({
      showChat: typeof value === 'boolean' ? value : false,
    });
  };

  async handleFirebase() {
    const { database } = await importFirebase();
    const { details } = this.state;
    console.log('This is all details insta', details);
    this.messageRef = database().ref('messages').child(this.state.details._id);

    this.messageRef.on('value', (message) => {
      if (message.val()) {
        this.setState({
          messages: Object.values(message.val()),
        });
      }
    });
  }

  handleSendMessage = (message) => {
    if (message) {
      const newItem = {
        userName: 'Anonymous User',
        message: message,
        sender: 'anonymous',
        date: new Date().getTime(),
        status: 'pending',
        type: 'text',
      };
      this.messageRef.push(newItem);
    }
  };

  handleSendLocationMessage = (message) => {
    if (message) {
      const newItem = {
        userName: 'Anonymous User',
        message: message,
        sender: 'anonymous',
        date: new Date().getTime(),
        status: 'pending',
        type: 'location',
      };
      this.messageRef.push(newItem);
    }
  };

  handleSendAudioMessage = (audio) => {
    if (audio) {
      const newItem = {
        userName: 'Anonymous User',
        message: 'audio',
        sender: 'anonymous',
        date: new Date().getTime(),
        status: 'pending',
        type: 'audio',
        media: audio,
      };
      this.messageRef.push(newItem);
    }
  };

  // onShareCurrentLocation = async () => {
  //   const location = await authUtil.getCurrentLocation();
  //   const ShareLocationUrl = `https://www.google.com/maps/place/${location[1]},${location[0]}`;
  //   this.handleSendLocationMessage(ShareLocationUrl);
  // };

  onStartVoiceRecord = () => {
    if (this.state.isBlocked) {
    } else {
      Mp3Recorder.start()
        .then(() => {
          this.setState({ isRecording: true });
        })
        .catch((e) => console.error(e));
    }
  };

  onStopVoiceRecord = () => {
    Mp3Recorder.stop()
      .getMp3()
      .then(async ([buffer, blob]) => {
        const audioFile = this.createFile(blob);
        const audioUrl = await this.uploadFile(audioFile);
        try {
          const response = await ApiCalls.trackOrder({ _id: this.orderNumber });
          this.setState({
            activity: false,
            ...(response.data ? { details: response.data } : {}),
          });
          this.handleFirebase();
        } catch (error) {
          this.setState({ activity: false });
        }
        this.handleSendAudioMessage(audioUrl.path);
        this.setState({ audioUrl: audioUrl.path, isRecording: false });
      })
      .catch((e) => console.log(e));
  };

  onRemoveVoiceRecord = () => {
    Mp3Recorder.stop()
      .getMp3()
      .then(async ([buffer, blob]) => {
        this.setState({ isRecording: false });
      })
      .catch((e) => console.log(e));
  };

  handleReturnOrder = async () => {
    const { details } = this.state;
    const id = details._id;

    try {
      const response = await ApiCalls.returnLinkForOrder({
        _id: id,
      });
      console.log('This is all reponse of return order', response.data);
      newGrowl.showGrowl(
        'success',
        'Success',
        'Kindly check your email for returning order link'
      );
    } catch (error) {
      this.setState({ activity: false });
    }
  };

  handleEditOrder = async () => {
    const { details } = this.state;
    const id = details._id;

    try {
      const response = await ApiCalls.editLinkForOrder({
        _id: id,
      });
      newGrowl.showGrowl(
        'success',
        'Success',
        'Kindly check your email for edit order link'
      );
    } catch (error) {
      this.setState({ activity: false });
    }
  };

  handleDialogClose = () => {
    this.setState({ dialogData: { open: false, comp: '' } });
  };

  handleSetDetails = (details) => {
    this.setState({
      dialogData: { open: false, comp: '' },
      details,
    });
  };

  handleGetDialogComponent = (type) => {
    switch (type) {
      case 'absent':
        return AbsentCaseComponent;
      case 'delivery':
        return DeliveryInformationsComponent;
      default:
        return () => null;
    }
  };

  handleChange = (state) => {
    this.setState(state);
  };

  getMapCoordinates = () => {
    const { details } = this.state;
    const status = details.status || '';
    if (status === 'pending' || status === 'accepted' || status === 'onmyway') {
      return [details.pickup.location];
    } else if (status === 'picked') {
      return [details.transporter.location, details.dropoff.location];
    }
  };

  handleTransporterChat = () => {
    alert("TODO: FIREBASE CONNECTION");
  }

  render() {
    const token = this.props.match.params.token;
    const { details, user, dialogData, login, transporterDetails } = this.state;
    const rating = details.rating || '';
    const status = details.status || '';
    const dropoff = details.dropoff ? details.dropoff.address : {};
    const pickup = details.pickup ? details.pickup.address : {};
    const pickupTime = details.pickupTime || '';
    const transporter = details.transporter || '';
    const deliveryTime = details.deliveryTime || '';
    const contactDetails = details.contact ? details.contact.number : '';
    const commodities = details.commodities || [];
    const { rates } = details;
    const actions = getActions(details, user, details.status);
    const customerRating = details.customerRating;
    // const pickupDropoffDetails = getPickupDropoffDetails(details);
    const timeLogs = details.timeLogs ? Object.keys(details.timeLogs) : [];
    // console.log('all details', transporter);
    const { open: isOpen, comp } = dialogData;
    const Comp = this.handleGetDialogComponent(comp);
    const imageSrc =
      transporter.picture || require('../../../static/images/image-user.png');

    const menuItem = [
      {
        id: '1',
        label: 'Order placed',
        subTitle: details.pickupDate
          ? moment(details.pickupDate).format('L')
          : '-',
        iconWhite: require('../../../static/images/calender-white.png'),
        iconBlack: require('../../../static/images/calender-black.png'),
        status: ['pending'],
        customStyle: {
          borderRadius: '15px 0 0 15px',
          alignItems: 'center',
        },
        active: false,
      },
      {
        id: '2',
        label: 'Send with',
        subTitle: details.deliveryType,
        status: ['accepted', 'onmyway'],
        iconWhite: require('../../../static/images/frank-white.png'),
        iconBlack: require('../../../static/images/frank-black.png'),
        active: false,
      },
      {
        id: '3',
        label: 'Delivery in progress',
        subTitle: details.deliveryDate
          ? moment(details.deliveryDate).format('L')
          : 'Flexible Date',
        iconWhite: require('../../../static/images/calender-white.png'),
        iconBlack: require('../../../static/images/calender-black.png'),
        status: ['picked'],
        active: true,
      },
      {
        id: '4',
        label: 'Delivered',
        subTitle: dropoff,
        status: ['delivered'],
        iconWhite: require('../../../static/images/location-white.png'),
        iconBlack: require('../../../static/images/location-black.png'),
        customStyle: {
          borderRadius: '0px 15px 15px 0',
          alignItems: 'center',
        },
        active: false,
      },
    ];

    const DeliveryInformation = [
      {
        id: '1',
        icon: <CalendarTodayIcon className="track-icon" />,
        label: 'Timeslot',
        subTitle: (
          <div>
            <p style={{ fontSize: 20, fontWeight: 'bold' }}>
              {details.deliveryDate
                ? moment(details.deliveryDate).format(trackDateTimeFormat)
                : 'Flexible Date'}
            </p>
            <p>
              {deliveryTime
                ? `${deliveryTime.from} - ${deliveryTime.to}`
                : 'Flexible Time'}
            </p>
          </div>
        ),
      },
      {
        id: '2',
        icon: <RoomOutlinedIcon className="track-icon" />,
        label: 'Address',
        subTitle: dropoff,
      },
      {
        id: '3',
        icon: <PersonOutlineOutlinedIcon className="track-icon" />,
        label: 'Contact',
        subTitle: `${contactDetails}`,
      },
    ];

    const NotHomeInformation = [
      {
        id: '1',
        icon: <PersonOutlineOutlinedIcon className="track-icon" />,
        label: 'Contact',
        subTitle: `${contactDetails}`,
      },
      {
        id: '2',
        icon: <LocationOffOutlinedIcon className="track-icon" />,
        label: 'Delivery instruction if absent',
        subTitle: 'Please leave the package in front of the door',
      },
    ];

    return (
      <div>
        {/* <Header showHeader={false} /> */}
        <div style={{ display: 'flex', justifyContent: 'center' }}>
          {this.state.activity ? (
            <div style={{ paddingTop: 150 }}>
              <CircularProgress />
            </div>
          ) : details._id ? (
            <React.Fragment>
              <div
                className="u-r-details-container padding-70"
                style={{
                  width: '100%',
                  backgroundColor: '#fff',
                }}
              >
                <div className={'status-container'}>
                  {cancelledStatus[status || ''] ? (
                    <Grid
                      item
                      xs={12}
                      md={12}
                      style={{ background: colors.orange }}
                      className={'content-center'}
                    >
                      <div className={'status-name'}>
                        <h3>Order Cancelled</h3>
                      </div>
                    </Grid>
                  ) : (
                    <Grid container>
                      {menuItem.map((item, index) => (
                        <Grid item xs={12} sm={6} md={3} spacing={3}>
                          <div
                            className={
                              item.status.indexOf(status) > -1
                                ? 'track-menu-active'
                                : 'track-menu-disable'
                            }
                            style={
                              item.status.indexOf(status) > -1
                                ? {}
                                : item.customStyle
                            }
                          >
                            <Grid container>
                              <Grid
                                xs={4}
                                md={4}
                                item
                                className={'content-right'}
                                style={{ paddingRight: '15px' }}
                              >
                                <h1
                                  style={{
                                    fontSize: 40,
                                    fontFamily: 'Raleway-Regular',
                                  }}
                                >
                                  {item.id}
                                </h1>
                              </Grid>
                              <Grid
                                xs={8}
                                md={8}
                                item
                                // className={'content-center'}
                                style={{ flexDirection: 'column' }}
                              >
                                <h3>{item.label}</h3>
                                <div style={{ display: 'flex' }}>
                                  <img
                                    src={
                                      item.status.indexOf(status) > -1
                                        ? item.iconWhite
                                        : item.iconBlack
                                    }
                                    style={{
                                      paddingRight: 5,
                                      width: 16,
                                      height: 16,
                                    }}
                                    alt="icon"
                                  />
                                  <div className="text">
                                    <p
                                      style={{
                                        color:
                                          item.status.indexOf(status) > -1
                                            ? '#fff'
                                            : '',
                                      }}
                                    >
                                      {item.subTitle.substring(0, 20)}
                                    </p>
                                  </div>
                                </div>
                              </Grid>
                            </Grid>
                            {/* <div className='p-r-20'></div> */}
                          </div>
                        </Grid>
                      ))}
                    </Grid>
                  )}
                </div>

                <Grid className="track-root" container spacing={2}>
                  <Grid item xs={12} sm={4} md={3}>
                    {commodities.map((item) => (
                      <Card
                        className="card"
                        style={{
                          borderRadius: 30,
                          WebkitBoxShadow:
                            '0px 0px 7px 0px rgba(184,184,184,1)',
                          MozBoxShadow: '0px 0px 7px 0px rgba(184,184,184,1)',
                          boxShadow: '0px 0px 7px 0px rgba(184,184,184,1)',
                        }}
                      >
                        <CardActionArea>
                          <img
                            src={item.images[0]}
                            alt="productImage"
                            style={{ width: '100%', height: 250 }}
                          />
                          <CardContent>
                            <Grid container>
                              <Grid item xs={12} md={11}>
                                <h3>{item.name}</h3>
                              </Grid>
                              <Grid item xs={12} md={1}>
                                <h3>{rates?.total}€</h3>
                              </Grid>
                              <p>Quantity : {item.quantity}</p>
                              <Divider className="divider" />
                              {/* <p>Ref: 123456789 </p> */}
                              <p>Order number: {details.storeOrderID} </p>
                              <div className="trackingbox">
                                <div style={{ paddingRight: 20 }}>
                                  <img
                                    src={require('../../../static/images/trackingnumber.png')}
                                    alt="image"
                                  />
                                </div>

                                <div>
                                  <p
                                    style={{
                                      margin: 0,
                                      color: 'rgb(250, 120, 22)',
                                      fontWeight: 'bold',
                                    }}
                                  >
                                    Tracking number
                                  </p>
                                  <p
                                    style={{
                                      margin: 0,
                                      color: 'rgb(250, 120, 22)',
                                    }}
                                  >
                                    {details.orderNumber}
                                  </p>
                                </div>
                              </div>
                            </Grid>
                          </CardContent>
                        </CardActionArea>
                      </Card>
                    ))}
                    <div style={{ marginTop: 20 }}>
                      <img
                        src={require('../../../static/images/frank-logo.png')}
                        alt="logo"
                      />
                    </div>
                  </Grid>
                  <Grid item xs={12} sm={8} md={9}>
                    {status === 'delivered' ? (
                      <Grid container spacing={4}>
                        <Grid item xs={8}>
                          <div>
                            <h4
                              style={{ marginTop: '0px' }}
                              className="main-title"
                            >
                              Delivery confirmed
                            </h4>
                            <Card className="card">
                              <CardContent>
                                <Grid container>
                                  <Grid item xs={5}>
                                    <h5>Delivery informations</h5>
                                    <Grid container>
                                      <Grid
                                        xs={1}
                                        md={1}
                                        item
                                        className={'information-border'}
                                      ></Grid>

                                      <Grid
                                        xs={2}
                                        md={2}
                                        item
                                        className="p-r-50"
                                      >
                                        <Avatar
                                          style={{
                                            width: 32,
                                            height: 32,
                                            marginBottom: 85,
                                          }}
                                          alt="image"
                                          src={imageSrc}
                                        />
                                        <Avatar
                                          style={{
                                            width: 32,
                                            height: 32,
                                          }}
                                          alt="image"
                                          src={require('../../../static/images/check-icon.png')}
                                        />
                                      </Grid>
                                      <Grid xs={8} md={8} item>
                                        <div className="m-b-60">
                                          <p className="class-p">
                                            Shipping address
                                          </p>
                                          <p>
                                            {pickup.substring(0, 35) + '...'}
                                          </p>
                                        </div>
                                        <div className="m-b-60">
                                          <p className="class-p">
                                            Delivery address
                                          </p>
                                          <p>
                                            {dropoff.substring(0, 35) + '...'}
                                          </p>
                                        </div>
                                        <div>
                                          <p className="class-p">Dropoff</p>
                                          <p style={{ marginTop: 0 }}>
                                            {deliveryTime
                                              ? deliveryTime.to
                                              : 'Flexible Time'}
                                          </p>
                                        </div>
                                      </Grid>
                                    </Grid>
                                  </Grid>
                                  <Grid item xs={7}>
                                    <h5>
                                      {`  Message from ${transporter?.firstName} ${transporter?.lastName}`}
                                    </h5>
                                    <p className="m-b-20">
                                      I've left the package at doorstep
                                    </p>
                                    {console.log('image', details)}
                                    <img
                                      alt="image"
                                      style={{
                                        maxHeight: '300px',
                                        objectFit: 'cover',
                                      }}
                                      src={
                                        (details || {}).deliveredImages[0] ||
                                        require('../../../static/images/doorstep.png')
                                      }
                                    />
                                  </Grid>
                                </Grid>
                              </CardContent>
                            </Card>
                          </div>
                        </Grid>

                        <Grid item xs={4}>
                          {/* <h4 className="main-title">Congrats!</h4> */}
                          <Card
                            className="card"
                            style={{ borderRadius: 30, textAlign: 'center' }}
                          >
                            <CardContent>
                              <Avatar
                                style={{
                                  width: 100,
                                  height: 100,
                                  marginLeft: 'auto',
                                  marginRight: 'auto',
                                  marginBottom: 10,
                                }}
                                alt="image"
                                src={imageSrc}
                              />
                              {token ? (
                                customerRating ? (
                                  <>
                                    <StarRatingComponent
                                      name="rating"
                                      value={customerRating.rating}
                                      starColor={getPrimaryColors('secondary')}
                                      emptyStarColor="#888"
                                      editing={false}
                                    />
                                    <h4>{customerRating.comment}</h4>
                                  </>
                                ) : (
                                  <></>
                                )
                              ) : (
                                <>
                                  <p>
                                    Delivered by <br />
                                    {` ${transporter.firstName} ${transporter.lastName}`}
                                  </p>
                                </>
                              )}

                              <Divider className="divider" />
                              {/* <p
                                  style={{
                                    color: "grey",
                                    fontSize: 14,
                                    fontWeight: "bold",
                                  }}
                                >
                                  you can also add tips
                                </p>
                               
                                  <Button
                                    href={`tel:+${transporter.mobile}`}
                                    variant="contained"
                                    style={{
                                      marginTop: 5,
                                      background: "rgb(250, 120, 22)",
                                      color: colors.white,
                                      borderRadius: 10,
                                      textTransform: "inherit",
                                      fontSize: 15,
                                    }}
                                  >
                                    {`Add a tip for ${transporter.firstName}`}
                                  </Button>
                                  <div
                                    style={{
                                      marginTop: 10,
                                      marginBottom: 10,
                                    }}
                                  ></div>
                                </div> */}
                            </CardContent>
                            {/* <CardActions>
                        <Button size="small">Learn More</Button>
                      </CardActions> */}
                          </Card>
                        </Grid>
                      </Grid>
                    ) : (
                      <Grid container className="track-details" spacing={4}>
                        <Grid item xs={12} style={{ marginBottom: 20 }}>
                          <div className="location-details">
                            {details._id ? (
                              requestInActive.some(
                                (item) => item.indexOf(details.status) !== -1
                              ) ? (
                                details.staticMap ? (
                                  <img
                                    src={details.staticMap}
                                    style={{ width: '100%', height: '100%' }}
                                  />
                                ) : null
                              ) : (
                                <GoogleMapsTrackerComponent
                                  {...this.state}
                                  showMarkers={true}
                                  status={status}
                                  getDirections={this.getDirections}
                                  mapType="static"
                                  transporterLocation={
                                    transporter.location
                                      ? transporter.location
                                      : []
                                  }
                                  mapElement={
                                    <div style={{ height: '100%' }} />
                                  }
                                  containerElement={
                                    <div className={'map-height'} />
                                  }
                                  coordinates={this.getMapCoordinates()}
                                />
                              )
                            ) : null}
                          </div>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4}>
                          <div>
                            <h1>
                              Delivery
                              <br />
                              Informations
                            </h1>
                            {
                              // (details || {}).status !== "cancelledbytransporter" ?
                              token ? <p>Change</p> : null
                              // :
                              // "cancelled"
                            }
                            <div className="p-t-30">
                              {/* { DeliveryInformation.map((item, index) => (
          <div className="m-b-20">
            <div style={ { display: "flex" } }>
              <p className={ classes.p }>{ item.icon } </p>
              <p className={ classes.p }> { item.label } </p>
            </div>
            <p>{ item.subTitle }</p>
          </div>
        )) } */}
                            </div>
                            <div style={{ marginBottom: '30px' }}>
                              <h3 className={'sub-heading'}>
                                {' '}
                                <DateRangeIcon fontSize={'12px'} /> &nbsp;
                                Timelot
                              </h3>
                              {/* <p className={ "bold-paragraps" }>Friday 17 May 2020 </p> */}
                              {/* <span className={ "regular-paragraphs" }>06:00 pm - 12:00 am</span> */}
                              <p className={'bold-paragraps'}>
                                No Delivery Date Available{' '}
                              </p>
                              <span className={'regular-paragraphs'}>
                                No Delivery Time Available
                              </span>
                            </div>

                            <div style={{ marginBottom: '30px' }}>
                              <h3 className={'sub-heading'}>
                                {' '}
                                <LocationOnIcon fontSize={'12px'} /> &nbsp;
                                Address
                              </h3>
                              {/* <p className={ "bold-paragraps" }>Friday 17 May 2020 </p> */}
                              {/* <span className={ "regular-paragraphs" }>06:00 pm - 12:00 am</span> */}
                              <p className={'bold-paragraps'}>
                                {' '}
                                {((details || {}).dropoff || {}).shortAddress}
                              </p>
                              <span className={'regular-paragraphs'}>
                                {((details || {}).dropoff || {}).address}
                                <br />
                                {((details || {}).dropoff || {}).city},
                                {((details || {}).dropoff || {}).country}
                              </span>
                            </div>

                            <div>
                              <h3 className={'sub-heading'}>
                                {' '}
                                <PersonIcon fontSize={'12px'} /> &nbsp; Contact
                              </h3>
                              <span className={'regular-paragraphs'}>
                                + {((details || {}).contact || {}).countryCode}{' '}
                                {((details || {}).user || {}).mobile}
                              </span>
                              {token ? (
                                <div style={{ marginTop: '15px' }}>
                                  <p className="class-p">
                                    Just to make your delivery easier
                                  </p>
                                </div>
                              ) : null}
                            </div>
                          </div>
                        </Grid>

                        <Grid item xs={12} sm={6} md={4}>
                          <div>
                            <h1>
                              Won't be <br />
                              Home
                            </h1>
                            {token ? <p>Change</p> : null}
                            <div className="p-t-30">
                              {/* { NotHomeInformation.map((item, index) => (
          <div className="m-b-20">
            <div style={ { display: "flex" } }>
              <p className={ classes.p }>{ item.icon } </p>
              <p className={ classes.p }> { item.label } </p>
            </div>
            <p>{ item.subTitle }</p>
          </div>
        )) } */}
                            </div>
                            <div style={{ marginBottom: '30px' }}>
                              <h3 className={'sub-heading'}>
                                {' '}
                                <PersonIcon fontSize={'12px'} /> &nbsp; Contact
                              </h3>
                              <p className={'bold-paragraps capitalize'}>
                                {((details || {}).contact || {}).name}{' '}
                              </p>
                              <span className={'regular-paragraphs'}>
                                {((details || {}).contact || {}).number === ''
                                  ? ((details || {}).user || {}).mobile
                                  : `+${
                                      ((details || {}).contact || {})
                                        .countryCode
                                    } ${
                                      ((details || {}).contact || {}).number
                                    }`}
                              </span>
                            </div>
                            {(details || {}).deliverTo ? (
                              <div style={{ marginBottom: '30px' }}>
                                <h3 className={'sub-heading'}>
                                  {' '}
                                  <LocationOffIcon fontSize={'12px'} /> &nbsp;
                                  Delivery Instructions if Absent
                                </h3>
                                {/* <p className={ "bold-paragraps capitalize" }>{ ((details || {}).contact || {}).name } </p> */}
                                <span className={'regular-paragraphs'}>
                                  {(
                                    (details || {}).deliverTo || ''
                                  ).toLowerCase() === 'at pickup point'
                                    ? 'Please leave the package at the pickup point'
                                    : (
                                        (details || {}).deliverTo || ''
                                      ).toLowerCase() === 'at my doorstep'
                                    ? 'Please leave the package in front of the door'
                                    : ''}
                                </span>
                              </div>
                            ) : null}
                          </div>
                        </Grid>
                        {!cancelledStatus[status || ''] &&
                        status !== 'pending' ? (
                          <Grid item xs={12} sm={6} md={4}>
                            <div>
                              <h1
                                style={{ fontWeight: 'bold', marginBottom: 30 }}
                              >
                                Your Delivery
                                <br />
                                Driver
                              </h1>
                              <Card className="card">
                                <CardContent>
                                  <Grid
                                    container
                                    style={{
                                      display: 'flex',
                                      justifyContent: 'center',
                                    }}
                                  >
                                    <Grid container spacing={2}>
                                      <Grid xs={12} md={12} item>
                                        <Avatar
                                          style={{
                                            width: 100,
                                            height: 100,
                                            marginLeft: 'auto',
                                            marginRight: 'auto',
                                          }}
                                          alt={
                                            ((details || {}).transporter || {})
                                              .firstName
                                          }
                                          src={imageSrc}
                                        />
                                      </Grid>
                                      <Grid xs={12} md={12} item>
                                        <p className="class-p">
                                          {` ${
                                            ((details || {}).transporter || {})
                                              .firstName
                                          } is on his way. `}
                                        </p>
                                      </Grid>
                                      <Grid xs={12} md={12} item>
                                        <a
                                          href={`tel:+${
                                            ((details || {}).transporter || {})
                                              .mobile
                                          }`}
                                          className={'call-anchor'}
                                        >
                                          <Button
                                            className={'call-button'}
                                            startIcon={<PhoneOutlinedIcon />}
                                          >
                                            Call delivery driver
                                          </Button>
                                        </a>
                                      </Grid>
                                      <Grid
                                        xs={12}
                                        item
                                        style={{
                                          display: 'flex',
                                          justifyContent: 'center',
                                        }}
                                      >
                                        <a className={'send-message-anchor'}>
                                          {/* <span
                                            onClick={() =>
                                              this.handleTransporterChat()
                                            }
                                            style={{ cursor: 'pointer' }}
                                          >
                                            Send a Message
                                          </span> */}
                                        </a>
                                      </Grid>
                                    </Grid>

                                    {/* <Link
              
              className={ classes.link }
            >
              Send a message
            </Link> */}
                                  </Grid>
                                </CardContent>
                                {/* <CardActions>
        <Button size="small">Learn More</Button>
      </CardActions> */}
                              </Card>
                            </div>
                          </Grid>
                        ) : null}
                      </Grid>
                    )}
                  </Grid>
                </Grid>
              </div>
            </React.Fragment>
          ) : (
            <div style={{ padding: '150px 1rem 1rem 1rem' }}>
              We could not find any request for this parcel number.
            </div>
          )}
        </div>
        <Dialog
          open={isOpen}
          onClose={this.handleDialogClose}
          aria-labelledby="actions-dialog"
          className="u-r-d-actions-dialog"
        >
          <Comp
            {...this.state}
            details={details}
            token={this.token}
            cancelType="user"
            ratingKey="rating"
            feedbackTo="transporter"
            handleDialogClose={this.handleDialogClose}
            handleSetDetails={this.handleSetDetails}
            handleFollowOrder={this.handleFollowOrder}
            handleReturnOrder={this.handleReturnOrder}
            getRequestDetails={this.getRequestDetails}
            handleAuthenticationUser={this.handleAuthenticationUser}
          />
        </Dialog>
        <ChatComponent
          transporter={transporter}
          showChat={this.state.showChat}
          messages={this.state.messages}
          handleStartRecording={this.onStartVoiceRecord}
          handleStopRecording={this.onStopVoiceRecord}
          handleShareLocation={this.onShareCurrentLocation}
          handleRemoveRecording={this.onRemoveVoiceRecord}
          onClick={this.handleToggleChat}
          sendMessage={this.handleSendMessage}
          canSendMessage={actions.canSendMessage}
        />
      </div>
    );
  }
}

