import React, { Component } from 'react';
import Container from '@material-ui/core/Container';
import { Grid } from '@material-ui/core';
import Header from '../Home/Header/Header';
import Footer from '../Footer';
import ApiCalls from '../../service/RequestHandler';
import NewsCard from './NewsCard';

class NewsContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      newsData: [],
    };
  }

  async componentDidMount() {
    const response = await ApiCalls.getNews();
    this.setState({
      newsData: response.data.news.filter((item) => item.active === true),
    });
  }

  render() {
    return (
      <React.Fragment>
        <Header
          showHeader={true}
          //   handleLoginModalOpen={props.handleLoginModalOpen}
        />

        <div>
          <div style={{ marginTop: '5%', padding: '3% 0' }}>
            <Container>
              <Grid container spacing={2}>
                <Grid item md={12}>
                  <h1 style={{ textAlign: 'center', marginBottom: '50px' }}>
                    Frank’s News
                  </h1>
                </Grid>
              </Grid>
              {this.state.newsData.length > 0 ? (
                <Grid container spacing={2}>
                  {this.state.newsData.map((item) => (
                    <Grid
                      item
                      xs={12}
                      sm={12}
                      md={6}
                      lg={4}
                      style={{ paddingBottom: '10%' }}
                    >
                      <NewsCard
                        id={item._id}
                        image={item.image}
                        title={item.title}
                        description={item.description}
                      />
                    </Grid>
                  ))}
                </Grid>
              ) : (
                <h3
                  style={{
                    textAlign: 'center',
                    color: '#a9b1bbb0',
                    fontSize: '30px',
                  }}
                >
                  {' '}
                  No news found{' '}
                </h3>
              )}
            </Container>
          </div>
        </div>
        <Footer bgImagePath="/assets/images/footer-bg.png" />
      </React.Fragment>
    );
  }
}

export default NewsContainer;
