import React from "react";
import { colors } from "../../../constants/colors";
import { withStore } from "../../../utils/store.util";
import {
  FormGroup,
  FormControlLabel,
  Switch,
  makeStyles,
} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  link: {
    color: colors.orange,
    cursor: "pointer",
    textDecoration: "underline",
  },
  p: {
    color: "grey",
    fontWeight: "bold",
    fontSize: "1rem",
  },
}));

const NotHomeComponent = (props) => {
  const classes = useStyles();
  const token = props.store.getFromStore("token");
  const { NotHomeInformation, handleChange } = props;
  return (
    <div>
      <h1>
        Won't be
        <br /> home
      </h1>
      {token ? (
        <p
          onClick={
            token
              ? () =>
                  handleChange({
                    dialogData: {
                      open: true,
                      comp: "absent",
                    },
                  })
              : () => this.handleEditOrder()
          }
          style={{
            color: colors.orange,
            cursor: "pointer",
            textDecoration: "underline",
          }}
        >
          Change
        </p>
      ) : null}
      <div className="p-t-30">
        {NotHomeInformation.map((item, index) => (
          <div className="m-b-20">
            <div style={{ display: "flex" }}>
              <p className={classes.p}>{item.icon} </p>
              <p className={classes.p}> {item.label} </p>
            </div>
            <p>{item.subTitle}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default withStore(NotHomeComponent);
