import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { colors } from '../../constants/colors';
import ArrowRightAltIcon from '@material-ui/icons/ArrowRightAlt';
import ButtonComponent from '../ButtonComponent';
import HeadingWithText from '../HeadingWithText';
// import theme from '../../containers/News/NewsCard';
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core';

import Fade from 'react-reveal/Fade';
import Slide from 'react-reveal/Slide';

const useStyles = makeStyles((theme) => ({
  cards: {
    flexGrow: 1,
    boxShadow: '0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23)',
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

export const theme = createMuiTheme({
  props: {
    // Name of the component
    MuiButtonBase: {
      // The properties to apply
      disableRipple: true, // No more ripple, on the whole application!
    },
  },
});

export default function BigImageWithContent(props) {
  const {
    content,
    columnUnit,
    heading,
    description,
    image,
    buttonText,
    arrow,
    button,
  } = props;
  const classes = useStyles();

  return (
    <MuiThemeProvider theme={theme}>
      <div className={classes.root}>
        <Grid
          style={{ marginTop: '90px' }}
          container
          className="third-section"
          spacing={3}
        >
          <HeadingWithText heading={heading} text={description} />
          {content.map((item, index) => (
            <Grid item xs={12} sm={columnUnit}>
              <Fade bottom big cascade>
                <Card raised={true} className="card">
                  <CardActionArea className="item-figure">
                    {image ? (
                      <img
                        style={{ width: '100%', height: '30vh' }}
                        className={classes.media}
                        src={item.image}
                        title="Contemplative Reptile"
                      />
                    ) : null}
                    <CardContent
                      style={{
                        padding: 20,
                        height: '15vh',
                      }}
                    >
                      <p style={{ fontSize: 16, fontWeight: 'bold' }}>
                        {item.label}
                      </p>
                      <div className="m-b-40">
                        <p style={{ fontSize: 16, lineHeight: '30px' }}>
                          {item.description}
                        </p>
                      </div>
                      {arrow ? (
                        <ArrowRightAltIcon
                          style={{
                            fontSize: 32,
                            float: 'right',
                            color: colors.orange,
                          }}
                        />
                      ) : (
                        <ButtonComponent
                          bgColor={colors.orange}
                          borderRadius="50px"
                          padding="15px"
                          width="200px"
                          color={colors.white}
                        >
                          Learn More
                        </ButtonComponent>
                      )}
                    </CardContent>
                  </CardActionArea>
                </Card>
              </Fade>
            </Grid>
          ))}
          {button ? (
            <ButtonComponent
              bgColor={colors.orange}
              borderRadius="50px"
              padding="15px"
              width="200px"
              marginLeft="auto"
              marginRight="auto"
              color={colors.white}
            >
              {buttonText}
            </ButtonComponent>
          ) : null}
        </Grid>
      </div>
    </MuiThemeProvider>
  );
}
