function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

const MyReturnListComponent = (props) => {
  const {
    activity,
    // status,
    // mode,
    // requests,
    onInputChange,
    // fetched,
    // handlePageClick,
    // pageCount,
    getRequests,
    // categories,
  } = props;

  const tabItem = [
    {
      id: '1',
      label: 'New Returns',
      status: 'pending',
    },
    {
      id: '2',
      label: 'In-progress Returns',
      status: 'inProgress',
    },
    {
      id: '3',
      label: 'History',
      status: 'history',
    },
  ];

  const ipadWidth = useMediaQuery('(max-width:768px)');
  const mobileWidth = useMediaQuery('(max-width:420px)');
  const veryLargeWidth = useMediaQuery('(min-width:1600px)');
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const [searchValue, setSearchValue] = React.useState('');

  const onChangeSearch = (value) => {
    setSearchValue(value);
  };

  const handleChange = async (event, newValue) => {
    console.log('new value', newValue);
    setValue(newValue);
    if (newValue === 0) {
      await getRequests('return', newValue);
    } else if (newValue === 1) {
      await getRequests('delivered', newValue);
    } else if (newValue === 2) {
      await getRequests('cancelled', newValue);
    }
  };

  return (
    <Page activity={activity} className="r-list-container" noActivity>
      <div className="page-title multiple-items">
        <span style={ipadWidth ? { width: '100%', paddingBottom: 10 } : {}}>
          {props.pageTitle}
        </span>
        <div style={{ display: 'flex' }}>
          {/* <BarChartIcon className="icon" onClick={onClickChart} /> */}
          <FormSearchInput
            style={{ marginRight: '1rem', width: '100%' }}
            name="search"
            value={searchValue}
            placeholder="Search Your Requests"
            onChange={onChangeSearch}
            right={
              <img
                alt=""
                src={require('../../../static/icons/icon-search.png')}
              />
            }
          />

          {/* <SettingsIcon onClick={onClickSetting} className="icon" /> */}
        </div>
      </div>

      <div style={{ textAlign: 'right', margin: '1rem 2rem' }}>
        <ButtonComponent
          marginBottom="10px"
          bgColor={colors.orange}
          borderRadius="50px"
          padding="15px"
          width="200px"
          marginRight="5px"
          color={colors.white}
          onClick={() =>
            props.history.push(
              `/${routes.typeMerchant}/${routes.shipping}/${routes.addShipment}`
            )
          }
        >
          New Shipment
        </ButtonComponent>

        <ButtonComponent
          marginBottom="10px"
          bgColor={colors.orange}
          borderRadius="50px"
          padding="15px"
          width="200px"
          color={colors.white}
          onClick={() =>
            props.history.push(
              `/${routes.typeMerchant}/${routes.shipping}/${routes.uploadFile}`
            )
          }
        >
          Upload file
        </ButtonComponent>
      </div>
      <Grid style={{ overflow: 'auto' }} container spacing={16}>
        <Grid
          item
          xs={12}
          sm={12}
          md={12}
          lg={12}
          xl={12}
          style={{ padding: 10 }}
        >
          <TabComponent
            handleChange={handleChange}
            tabItem={tabItem}
            searchValue={searchValue}
            {...props}
          />
        </Grid>
      </Grid>
      {/* <div style={{ textAlign: 'right', margin: '1rem 2rem' }}>
        {!activity ? (
          <div>
            <ExportCSV
              csvData={props.detailsExcelData}
              fileName={props.fileName}
              disabled={activity}
            />
            <Button
              disabled={activity}
              onClick={() =>
                props.history.push(
                  `/${routes.typeFleetManager}/${routes.schedulePickup}`
                )
              }
              style={{
                background: '#fa7816',
                color: 'white',
                margin: '5px 5px',
              }}
            >
              Pickup Schedule
            </Button>
            <Button
              disabled={activity}
              onClick={() =>
                props.history.push(
                  `/${routes.typeFleetManager}/${routes.mapView}`
                )
              }
              style={{
                background: '#fa7816',
                color: 'white',
                margin: '5px 5px',
              }}
            >
              Map View
            </Button>
          </div>
        ) : null}
      </div> */}
    </Page>
  );
};

MyReturnListComponent.defaultProps = {
  pageTitle: 'Returns',
  searchPlaceholder: 'Search Your Requests',
};

MyReturnListComponent.propTypes = {
  user: PropTypes.shape(),
  fetched: PropTypes.bool,
  activity: PropTypes.bool,
  sortBy: PropTypes.string,
  status: PropTypes.string,
  search: PropTypes.string,
  layout: PropTypes.string,
  history: PropTypes.shape(),
  userType: PropTypes.string,
  handleSort: PropTypes.func,
  pageTitle: PropTypes.string,
  onInputChange: PropTypes.func,
  searchPlaceholder: PropTypes.string,
  options: PropTypes.arrayOf(PropTypes.object),
  headings: PropTypes.arrayOf(PropTypes.object),
  shouldListLayoutComponentUpdate: PropTypes.bool,
  sortedRequests: PropTypes.arrayOf(PropTypes.shape({})),
  requests: PropTypes.objectOf(PropTypes.arrayOf(PropTypes.shape({}))),
  handlePageClick: PropTypes.func,
  pageCount: PropTypes.number,
  detailsExcelData: PropTypes.arrayOf(PropTypes.shape({})),
  fileName: PropTypes.string,
};

export default MyReturnListComponent;
