/* eslint-disable no-console */
import React, { Component } from 'react';
import * as authUtil from '../../../utils/auth.util';
import PropTypes from 'prop-types';

import {
  callAfterDebounceTime,
  getSortingFunction,
  getNestedValue,
  isEmpty,
} from '../../../utils/functions';
import { authClass } from '../../../utils/auth.util';
import ApiCalls from '../../../service/RequestHandler';
import routes from '../../../constants/route-constants';
import {
  getContactDetails,
  getVehicleData,
  getPrice,
} from '../../../utils/request-details.util';
import moment from 'moment';
import MyShippingListComponent from './MyShippingListComponent';

export default class MyShippingListContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tabValue: 0,
      activity: true,
      userType: '',
      sortBy: '',
      requests: {},
      status: props.match.params.status || 'all',
      orderType: 'all',
      spaceStatus: props.match.params.spaceStatus,
      layout: 'grid',
      fetched: false,
      sortedRequests: [],
      spaceSearch: false,
      store: '',

      search: '',
      pageCount: 1,
      pageNumber: 1,
      per_page_limit: 50,

      detailsExcelData: [],

      fileName: 'My Requests',
      categories: [],
      itemType: {
        id: '',
        label: 'Select Item Type',
        value: 'Select Item Type',
      },
    };

    this.callAfterDebounceTime = callAfterDebounceTime();
    this.shouldListLayoutComponentUpdate = false;
    this.headings = [
      { label: 'Order #', sortBy: 'orderNumber', key: ['orderNumber'] },
      { label: 'Type', sortBy: 'type', key: ['deliveryType'] },
      { label: 'Pickup Date', sortBy: 'pickupDate', key: ['pickupDate'] },
      { label: 'Pickup', sortBy: 'pickup', key: ['pickup', 'address'] },
      { label: 'Dropoff', sortBy: 'dropoff', key: ['dropoff', 'address'] },
      { label: 'Price', sortBy: 'price', key: ['rates', 'total'] },
      { label: 'Status', sortBy: 'status', key: ['status'] },
    ];
    this.options = [
      { key: 'orderNumber' },
      { key: 'deliveryType', type: 'type' },
      {
        keys: ['pickupDate'],
        getLabel: true,
        type: 'date',
        format: 'DD, MMM, YYYY',
      },
      {
        keys: ['pickup', 'address'],
        type: 'address',
        getLabel: true,
      },
      {
        keys: ['dropoff', 'address'],
        type: 'address',
        getLabel: true,
      },
      { keys: ['rates', 'total'], type: 'price', getLabel: true },
      { key: 'status' },
    ];
  }

  async componentDidMount() {
    const user = await authUtil.getUser();
    this.setState({ userType: user?.userType, store: user?._id });
    this.getRequests(this.state.status);
    this.setState({
      tabValue: 0,
    });
  }

  getCategories = async () => {
    try {
      const response = await ApiCalls.getCategories();
      this.setState({
        activity: false,
        categories: response.data.map((item, index) => {
          return {
            id: item._id,
            label: item.name,
            value: item.name,
          };
        }),
      });
    } catch (error) {
      this.setState({ activity: false });
    }
  };

  async filterRequests({ filterType, value }) {
    const props = {
      text: value,
      filterBy: filterType,
      userType: this.state.userType,
    };
    console.log('This is props of filter requets', props);
    try {
      const response = await ApiCalls.orderFilter(props);
      console.log('THis is the response of filter api', response.data);
      await this.setState((prevState) => ({
        sortBy: '',
        fetched: true,
        activity: false,
        requests: {
          ...prevState.requests,
          [this.state.status]: response.data,
        },
        sortedRequests: response.data,
        pageCount: Math.ceil(parseInt(response.total) / response.limit),
        pageNumber: this.state.pageNumber,
      }));
      // await this.setExcelData(response.data);
    } catch (error) {
      await this.setState((prevState) => ({
        sortBy: '',
        fetched: true,
        activity: false,
        sortedRequests: [],
        requests: {
          ...prevState.requests,
          [this.state.status]: [],
        },
        pageCount: 1,
        pageNumber: this.state.pageNumber,
      }));
    }
    this.shouldListLayoutComponentUpdate = false;
  }

  getRequests = async (status, newValue) => {
    const id = this.state.store;
    this.setState({
      tabValue: newValue,
    });
    const { orderType } = this.state;
    try {
      status = 'all';
      this.shouldListLayoutComponentUpdate = true;
      const response = await ApiCalls.getOrderStore({
        id,
        status,
        orderType,
      });

      if (status === 'all') {
        this.setState((prevState) => ({
          requests: {
            ...prevState.requests,
            [status]: response.data,
          },
          sortedRequests: response.data.filter((item) => {
            if (newValue === 1) {
              return (
                (item.status === 'accepted' ||
                  item.status === 'picked' ||
                  item.status === 'onmyway') &&
                item.isReturnOrder === false
              );
            } else if (newValue === 2) {
              return (
                (item.status === 'delivered' ||
                  item.status === 'cancelledbystore' ||
                  item.status === 'cancelledbyadmin' ||
                  item.status === 'cancelledbytransporter' ||
                  item.status === 'cancelledbyuser' ||
                  item.status === 'cancelled') &&
                item.isReturnOrder === false
              );
            } else {
              return item.status === 'pending' && item.isReturnOrder === false;
            }
          }),
        }));
      } else {
        this.setState((prevState) => ({
          requests: {
            ...prevState.requests,
            [status]: response.data,
          },
          sortedRequests: response.data,
        }));
      }
    } catch (error) {
      console.log(error);
    }
  };

  handleSearch = async (value) => {
    this.setState({ activity: true });
    this.shouldListLayoutComponentUpdate = true;

    try {
      const response = await ApiCalls.searchMyOrders({
        text: value,
        userType: 'transporter',
      });
      await this.setState((prevState) => ({
        sortBy: '',
        activity: false,
        sortedRequests: response.data,
        requests: { ...prevState.requests, [prevState.status]: response.data },
        pageCount: Math.ceil(parseInt(response.total) / response.limit),
        pageNumber: this.state.pageNumber,
      }));
    } catch (error) {
      await this.setState((prevState) => ({
        sortBy: '',
        activity: false,
        sortedRequests: [],
        requests: {
          ...prevState.requests,
          [prevState.status]: [],
        },
        pageCount: 1,
        pageNumber: 1,
      }));
    }
    this.shouldListLayoutComponentUpdate = false;
  };

  handleOnInputChange = (e) => {
    const { name, value, id } = e.target;
    this.setState({
      [name]: value,
      pageCount: 1,
      pageNumber: 1,
      per_page_limit: 50,
    });
    if (name === 'search') {
      this.callAfterDebounceTime(() => this.handleSearch(value), {
        clear: !value,
      });

      if (!value) {
        this.getRequests(this.state.status);
      }
    }
    if (name === 'status') {
      // this.props.history.push(
      //   `/${routes.typeFleetManager}/${routes.myRequests}/${value}`
      // );
      this.getRequests(value);
    }
    if (name === 'mode') {
      // this.props.history.push(
      //   `/${routes.typeFleetManager}/${routes.myRequests}/${value}`
      // );
      this.filterRequests({ filterType: 'transport', value });
    }
    if (name === 'spaceStatus') {
      this.props.history.push(
        `/${routes.typeFleetManager}/${routes.space}/${this.props.match.params.spaceId}/${value}`
      );

      this.getRequests(value);
    }
  };

  handleSort = async (sortBy, key) => {
    console.log('SORTBY', sortBy);
    console.log('KEY', key);
    console.log('this.state.status', this.state.status);
    console.log('this.state.requests', this.state.requests);
    this.shouldListLayoutComponentUpdate = true;
    if (this.state.sortBy && this.state.sortBy === sortBy) {
      await this.setState(
        {
          sortBy: '',
          sortedRequests: this.state.requests[this.state.status].filter(
            (item) => {
              if (this.state.tabValue === 1) {
                return (
                  (item.status === 'accepted' ||
                    item.status === 'picked' ||
                    item.status === 'onmyway') &&
                  item.isReturnOrder === false
                );
              } else if (this.state.tabValue === 2) {
                return (
                  (item.status === 'delivered' ||
                    item.status === 'cancelledbystore' ||
                    item.status === 'cancelledbyadmin' ||
                    item.status === 'cancelledbytransporter' ||
                    item.status === 'cancelledbyuser' ||
                    item.status === 'cancelled') &&
                  item.isReturnOrder === false
                );
              } else {
                return (
                  item.status === 'pending' && item.isReturnOrder === false
                );
              }
            }
          ),
        },
        () => {
          console.log('SORTED REQUESTS', this.state.sortedRequests);
        }
      );
    } else {
      console.log('SORT IN ELSE', sortBy, key);
      console.log('TAB VALUE', this.state.tabValue);
      const requests = [...this.state.sortedRequests];
      const sortingFunction = getSortingFunction(sortBy);
      await this.setState({
        sortBy,
        sortedRequests: requests.sort((a, b) =>
          sortingFunction(getNestedValue(a, key), getNestedValue(b, key))
        ),
      });
    }
    this.shouldListLayoutComponentUpdate = false;
  };

  handlePageClick = (data) => {
    let selected = data.selected;
    this.setState(
      {
        pageNumber: selected + 1,
      },
      () => {
        this.getRequests(this.state.status);
      }
    );
  };

  getRequestVehicleData = async (id) => {
    try {
      const response = await ApiCalls.getVehicleById({ _id: id });
      const vehicleData = await response.data;
      return vehicleData;
    } catch (error) {
      //
    }
  };

  onClickSetting = () => {
    this.props.history.push(`/${routes.typeMerchant}/${routes.storeSettings}`);
  };

  onClickChart = () => {
    this.props.history.push(`/${routes.typeMerchant}/${routes.loggedInHome}`);
  };

  setExcelData = async (details) => {
    let detailsArr = [];
    let contactDetails;
    let vehicle;
    let vehicleData;
    const user = authClass.getUser;

    details.length &&
      details.forEach(async (details) => {
        contactDetails = getContactDetails(details);
        vehicle =
          (details.vehicle &&
            (await this.getRequestVehicleData(details.vehicle))) ||
          {};
        vehicleData = await getVehicleData(vehicle);

        await detailsArr.push({
          OrderNumber: details.orderNumber || '',
          Status: details.status === 'pending' ? 'Waiting' : details.status,
          DeliveryType: details.deliveryType || '',
          PickupFrom: contactDetails.pickup.name || '',
          PickUpContact: `+${details.contact.number}` || '',
          DeliverTo: contactDetails.deliver.name || '',
          DeliverToContact: `+${contactDetails.deliver.number}` || '',
          Pickup: details.pickup.address || '',
          DropOff: details.dropoff.address || '',
          PickupDate: moment(details.pickupDate).format('ll') || '',
          FlexibleDate: details.flexibleDate,
          PickupTime: details.pickupTime
            ? `${details.pickupTime.from} - ${details.pickupTime.to}`
            : '',
          DeliveryDate: details.deliveryDate
            ? moment(details.deliveryDate).format('ll')
            : '',
          DeliveryTime: details.deliveryTime
            ? `${details.deliveryTime.from} - ${details.deliveryTime.to}`
            : '',
          FlexibleDeliveryDate: details.flexibleDeliveryDate,
          Price:
            details.rates && details.rates.price
              ? `${user.config.currency} ${getPrice(
                  details.rates.price,
                  user.userType
                )}`
              : '-',
          TransporterName: details.transporter
            ? `${details.transporter.firstName} ${details.transporter.lastName}`
            : '',
          TransporterContact: details.transporter
            ? `+${details.transporter.mobile}`
            : '',
          TransporterVehicleMode: !isEmpty(vehicle) ? vehicleData.mode : '',
          TransporterVehicleModel: !isEmpty(vehicle)
            ? `${vehicleData.model}`
            : '',

          TransporterVehicleNumber: !isEmpty(vehicle)
            ? `${vehicleData['number plate']}`
            : '',
        });
      });

    await this.setState({ detailsExcelData: detailsArr });
  };

  render() {
    return (
      <MyShippingListComponent
        {...this.state}
        user={authClass.getUser}
        options={this.options}
        headings={this.headings}
        handleSort={this.handleSort}
        history={this.props.history}
        onInputChange={this.handleOnInputChange}
        shouldListLayoutComponentUpdate={this.shouldListLayoutComponentUpdate}
        handlePageClick={this.handlePageClick}
        getRequests={this.getRequests}
        onClickSetting={this.onClickSetting}
        onClickChart={this.onClickChart}
      />
    );
  }
}

MyShippingListContainer.propTypes = {
  history: PropTypes.shape(),
  match: PropTypes.shape({
    params: PropTypes.shape({ status: PropTypes.string }),
  }),
};
