import React, { Component } from 'react';
import './Header.css';
import Container from '@material-ui/core/Container';
import { Grid } from '@material-ui/core';
import { Link } from 'react-router-dom';
import { withStore } from '../../../utils/store.util';
import classnames from 'classnames';

class Header extends Component {
  state = {
    drawerOpen: false,
    headerShow: false,
    meanNavShow: false,
    meanExpands: {
      services: false,
      tracking: false,
      courier: false,
      signin: false,
    },
  };

  toggleMeanNav = () => {
    this.setState({
      meanNavShow: !this.state.meanNavShow,
    });
  };

  meanExpand = (e) => {
    const name = e.target.getAttribute('name');

    if (name === 'services') {
      this.setState({
        meanExpands: { services: !this.state.meanExpands.services },
      });
    }

    if (name === 'tracking') {
      this.setState({
        meanExpands: { tracking: !this.state.meanExpands.tracking },
      });
    }

    if (name === 'courier') {
      this.setState({
        meanExpands: { courier: !this.state.meanExpands.courier },
      });
    }

    if (name === 'signin') {
      this.setState({
        meanExpands: { signin: !this.state.meanExpands.signin },
      });
    }
  };

  handleScroll = () => {
    // console.log('Window Scroll', Window.scrollY);

    if (window.scrollY > 30)
      return this.setState({ headerShow: true }, () =>
        this.props.store.setToStore('headerShow', this.state.headerShow)
      );
    else if (window.scrollY <= 30) {
      return this.setState({ headerShow: false }, () =>
        this.props.store.setToStore('headerShow', this.state.headerShow)
      );
    }
    return this.setState({ headerShow: false });
  };

  async componentDidMount() {
    // if (!this.props.showHeader) {
    //   this.setState({ headerShow: true });
    //   return;
    // }
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    this.props.store.setToStore('headerShow', false);
    window.removeEventListener('scroll', this.handleScroll, true);
  }

  render() {
    return (
      <div>
        <header className="sticky-fixed-after transparent-header">
          <div id="sticky-placeholder"></div>
          <div id="navbar-wrap">
            <div
              className={
                this.state.headerShow
                  ? classnames('navbar-layout1 navbar-background-peach')
                  : classnames('navbar-layout1 navbar-background-transparent')
              }
            >
              <Container>
                <Grid
                  container
                  spacing={2}
                  className="no-gutters d-flex align-items-center position-relative"
                >
                  <Grid
                    item
                    lg={2}
                    xs={2}
                    sm={2}
                    md={2}
                    className="d-flex justify-content-start"
                  >
                    <div className="temp-logo text-center">
                      <Link
                        to="/"
                        className={
                          !this.state.headerShow
                            ? classnames('default-logo visible-logo')
                            : classnames('default-logo hidden-logo')
                        }
                      >
                        <img
                          src={`${process.env.PUBLIC_URL}/assets/images/logo-light.png`}
                          alt="logo"
                          class="img-fluid"
                        />
                      </Link>
                      <Link
                        to="/"
                        className={
                          this.state.headerShow
                            ? classnames('sticky-logo visible-logo')
                            : classnames('sticky-logo hidden-logo')
                        }
                      >
                        <img
                          src={`${process.env.PUBLIC_URL}/assets/images/footer-logo.png`}
                          alt="logo"
                          class="img-fluid"
                        />
                      </Link>
                    </div>
                  </Grid>
                  <Grid
                    item
                    lg={7}
                    xs={7}
                    sm={7}
                    md={7}
                    className="d-flex justify-content-end possition-static"
                  >
                    <nav id="dropdown" className="template-main-menu">
                      <ul>
                        <li>
                          <Link
                            to="#"
                            className={
                              this.state.headerShow ? 'text-color-orange' : null
                            }
                          >
                            Services
                          </Link>
                          <ul className="dropdown-menu-col-1">
                            <li className="has-child-second-level"></li>
                            <li>
                              <Link to="/ship-from-store">
                                <span>Ship from Store</span>
                              </Link>
                            </li>
                            <li>
                              <Link to="/client-return">
                                <span>Client Returns</span>
                              </Link>
                            </li>
                            <li>
                              <Link to="/help-center">
                                <span>Help Center</span>
                              </Link>
                            </li>
                          </ul>
                        </li>
                        <li>
                          <Link
                            className={
                              this.state.headerShow ? 'text-color-orange' : null
                            }
                            to="#"
                          >
                            Tracking
                          </Link>
                          <ul className="dropdown-menu-col-1">
                            <li>
                              <Link to="/follow-my-order">
                                <span>Follow My Order</span>
                              </Link>
                            </li>

                            <li>
                              <Link to="/help-center-for-customers">
                                <span>Help Center for Customers</span>
                              </Link>
                            </li>
                          </ul>
                        </li>
                        <li>
                          <Link
                            className={
                              this.state.headerShow ? 'text-color-orange' : null
                            }
                            to="#"
                          >
                            Courier
                          </Link>
                          <ul className="dropdown-menu-col-1">
                            <li>
                              <Link to="/apply-now">
                                <span>Apply Now</span>
                              </Link>
                            </li>

                            <li>
                              <Link to="/faqs">
                                <span>Need help for Carrier</span>
                              </Link>
                            </li>
                          </ul>
                        </li>

                        <li>
                          <Link
                            className={
                              this.state.headerShow ? 'text-color-orange' : null
                            }
                            to="#"
                          >
                            Sign In
                          </Link>
                          <ul className="dropdown-menu-col-1">
                            <li>
                              <Link to="/merchant">
                                <span>E-merchant BO access</span>
                              </Link>
                            </li>
                            <li>
                              <Link to="/fleetmanager">
                                <span>Fleet Manager BO access</span>
                              </Link>
                            </li>
                          </ul>
                        </li>
                      </ul>
                    </nav>
                  </Grid>
                  <Grid
                    item
                    lg={3}
                    xs={3}
                    sm={3}
                    md={3}
                    className="d-flex justify-content-end"
                  >
                    <ul
                      style={{ display: 'flex', textAlign: 'center' }}
                      className="header-action-items"
                    >
                      <li
                        style={{
                          display: 'flex !important',
                          width: '100%',
                          textAlign: 'center !important',
                        }}
                        className="single-item mr-2"
                      >
                        <Link
                          to="#"
                          className={
                            this.state.headerShow
                              ? 'item-btn-active btn-ghost'
                              : 'item-btn btn-ghost'
                          }
                        >
                          Free Product Demo
                        </Link>
                      </li>
                    </ul>
                  </Grid>
                </Grid>
              </Container>
            </div>
          </div>
        </header>

        <div className="mean-bar" style={{ display: 'none' }}>
          <div className="mobile-menu-nav-back">
            <Link to="/" className="logo-mobile">
              <img
                src={`${process.env.PUBLIC_URL}/assets/images/footer-logo.png`}
                alt="logo"
                class="img-fluid"
                style={{
                  height: '35px',
                  width: '50px',
                }}
              />
            </Link>
          </div>
          <button
            onClick={this.toggleMeanNav}
            className="meanmenu-reveal"
            style={{
              border: 'none',
              background: 'transparent',
              right: '0',
              left: 'auto',
            }}
          >
            <span></span>
            <span></span>
            <span></span>
          </button>
          <nav className="mean-nav">
            <ul
              className={
                this.state.meanNavShow ? 'my-display-block' : 'my-display-none'
              }
            >
              <li>
                <Link to="#">Services</Link>
                <ul
                  className={
                    this.state.meanExpands.services
                      ? classnames('my-display-block dropdown-menu-col-1')
                      : classnames('my-display-none dropdown-menu-col-1')
                  }
                >
                  <li>
                    <Link to="/ship-from-store">
                      <span>Ship from Store</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/client-return">
                      <span>Client Returns</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/help-center">
                      <span>Help Center</span>
                    </Link>
                  </li>
                </ul>
                <button
                  onClick={(e) => this.meanExpand(e)}
                  name="services"
                  className="mean-expand"
                  style={{ fontSize: '18px', textDecoration: 'none' }}
                >
                  {this.state.meanExpands.services ? '-' : '+'}
                </button>
              </li>
              <li>
                <Link to="#">Tracking</Link>
                <ul
                  className={
                    this.state.meanExpands.tracking
                      ? classnames('my-display-block dropdown-menu-col-1')
                      : classnames('my-display-none dropdown-menu-col-1')
                  }
                >
                  <li>
                    <Link to="/follow-my-order">
                      <span>Follow My Order</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/help-center-for-customers">
                      <span>Help Center for Customers</span>
                    </Link>
                  </li>
                </ul>
                <button
                  onClick={(e) => this.meanExpand(e)}
                  name="tracking"
                  className="mean-expand"
                  style={{ fontSize: '18px', textDecoration: 'none' }}
                >
                  {this.state.meanExpands.tracking ? '-' : '+'}
                </button>
              </li>
              <li>
                <Link to="#">Courier</Link>
                <ul
                  className={
                    this.state.meanExpands.courier
                      ? classnames('my-display-block dropdown-menu-col-1')
                      : classnames('my-display-none dropdown-menu-col-1')
                  }
                >
                  <li>
                    <Link to="/apply-now">
                      <span>Apply Now</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/faqs">
                      <span>Need help for Carrier</span>
                    </Link>
                  </li>
                </ul>
                <button
                  onClick={(e) => this.meanExpand(e)}
                  name="courier"
                  className="mean-expand"
                  style={{ fontSize: '18px', textDecoration: 'none' }}
                >
                  {this.state.meanExpands.courier ? '-' : '+'}
                </button>
              </li>
              <li>
                <Link to="#">Sign In</Link>
                <ul
                  className={
                    this.state.meanExpands.signin
                      ? classnames('my-display-block dropdown-menu-col-1')
                      : classnames('my-display-none dropdown-menu-col-1')
                  }
                >
                  <li>
                    <Link to="/merchant">
                      <span>E-merchant BO access</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/fleetmanager">
                      <span>Fleet Manager BO access</span>
                    </Link>
                  </li>
                </ul>
                <button
                  onClick={(e) => this.meanExpand(e)}
                  name="signin"
                  className="mean-expand"
                  style={{ fontSize: '18px', textDecoration: 'none' }}
                >
                  {this.state.meanExpands.signin ? '-' : '+'}
                </button>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    );
  }
}

export default withStore(Header);
