    //       <Grid container spacing={5}>
    //         {content.map((item, index) => (
    //           <Grid item xs={6}>
    //             <p style={{ fontWeight: 'bold', color: colors.darkBlue }}>
    //               {item.label}
    //             </p>
    //             <p>{item.description}</p>
    //           </Grid>
    //         ))}
    //       </Grid>
    //     </Grid>
    //   </Grid>
