import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Avatar from '@material-ui/core/Avatar';
import { AvatarGroup } from '@material-ui/lab';
import BookOutlinedIcon from '@material-ui/icons/BookOutlined';
import { colors } from '../../../constants/colors';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import ButtonComponent from '../../ButtonComponent';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    '& > *': {
      marginLeft: theme.spacing(25),
      marginRight: theme.spacing(25),
      marginTop: theme.spacing(3),
      marginBottom: theme.spacing(2),
      padding: theme.spacing(3),
      width: '100%',
      //   height: theme.spacing(16),
    },
  },
  box: {
    backgroundColor: colors.white,
    borderRadius: 10,
  },
}));

export default function UseCasesCards(props) {
  const classes = useStyles();
  const { content, images, heading } = props;
  return (
    <div className={`e-marchant-hc-container`}>
      <Grid container spacing={2}>
        <Grid xs={12} item className={'align-horizontal-center'}>
          {heading ? (
            <h1 className="e-merchant-hc-heading">{heading}</h1>
          ) : null}
        </Grid>
      </Grid>

      {/* {content.map((item, index) => (
        <Grid container className={classes.box}>
          {images ? (
            <Grid
              item
              xs={2}
              sm={2}
              style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
              }}
            >
              <img
                // className={ classes.media }
                src={item.image}
                // style={ { width: 'auto' } }
                title="Contemplative Reptile"
              />
            </Grid>
          ) : null}

          <Grid item xs={6} sm={images ? '10' : '12'}>
            <h3 style={{ color: colors.darkBlue }}>{item.label}</h3>
            <p>{item.text}</p>
            {item.description ? (
              <div>
                <p>{item.description}</p>
              </div>
            ) : (
              item.points.map((point, index) => <p> {point}</p>)
            )}
          </Grid>
        </Grid>
      ))} */}
      <div className={'e-marchant-hc-card-container'}>
        {content.map((item, index) => {
          return (
            <div key={index} className={'e-marchant-hc-card'}>
              <Grid container spacing={2}>
                <Grid xs={12} md={2} item className={'align-vertical-center'}>
                  <img
                    className={'hc-image'}
                    src={item.image}
                    alt={`help-center-icon-${index + 1}`}
                  />
                </Grid>
                <Grid xs={12} md={10} item>
                  <h3>{item.label}</h3>
                  <p className={"hc-description"}>{item.description}</p>
                </Grid>
              </Grid>
            </div>
          );
        })}
      </div>
    </div>
  );
}
