import React, { Component } from 'react';
import PropTypes from 'prop-types';

import moment from 'moment';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';

import ApiCalls from '../../../../service/RequestHandler';
import FormNativeSelectInputField from '../../../../components/form/FormNativeSelectInputField';
import FormTextArea from '../../../../components/form/FormTextArea';
import FormSubmitBtn from '../../../../components/form/FormSubmitBtn';
import FormInputS from '../../../../components/form/FormInputS';
import FormTelInput from '../../../../components/form/FormTelInput';
import { getPrimaryColors } from '../../../../utils/functions';

export default class AbsentCaseComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: false,
      activity: false,
      contactNumber: '',
      method: { value: '', label: 'Select Type' },
      sampleReasons: [
        { label: 'Reason 1', value: 'Reason 1' },
        { label: 'Reason 2', value: 'Reason 2' },
        { label: 'Reason 3', value: 'Reason 3' },
        { label: 'Reason 4', value: 'Reason 4' },
        { label: 'Reason 5', value: 'Reason 5' },
      ],
      reason: { label: 'Select a reason', value: '' },
      cancellationReason: '',
    };

    const pickupTime = props.details.pickupTime
      ? props.details.pickupTime.from.split(':')
      : '00:00';
    const pickup = moment(props.details.pickupDate)
      .hour(pickupTime[0])
      .minute(pickupTime[1]);

    this.showCancellationFeeMessage = moment(pickup).isSame(moment(), 'day');
  }

  handleChange = (e) => {
    const { name, value } = e.target;
    this.setState({
      [name]: value,
      error: false,
      ...(name === 'reason' ? { cancellationReason: value.value } : {}),
    });
  };

  handleCancelOrder = async () => {
    const reason = this.state.cancellationReason;
    const status =
      this.props.cancelType === 'user'
        ? 'cancelledbyuser'
        : 'cancelledbytransporter';
    if (reason) {
      this.setState({ activity: true });
      try {
        await ApiCalls.cancelOrder({
          _id: this.props.details._id,
          cancellationReason: reason,
          status,
        });
        const newDetails = { ...this.props.details };
        newDetails.status = status;
        newDetails.cancellationReason = reason;

        this.props.handleSetDetails(newDetails);
      } catch (error) {
        this.setState({ activity: false });
      }
    } else {
      this.setState({ error: true });
    }
  };

  handleReasonLabelRef = (ref) => (this.reasonLabelRef = ref);
  handleSelectChange = (e) => {
    const { name, value } = e.target;
    if (value.name === 'Other') {
      this.setState({
        dialogChangeUnits: {
          open: true,
          comp: 'otherCommodity',
        },
      });
    }
  };

  onInputChange = (event) => {
    const { name, value, checked, type } = event.target;
    // if (this.state.errors[name]) {
    this.setState((prevState) => {
      return {
        [name]: type === 'checkbox' ? checked : value,
        errors: { ...prevState.errors, [name]: false },
        getRate: false,
        // deliveryType: this.changeDeliveryType({
        //   pickupDate: prevState.pickupDate,
        //   deliveryDate: prevState.deliveryDate,
        //   [name]: value
        // })
      };
    });
    // } else {
    //   this.setState({
    //     [name]: type === 'checkbox' ? checked : value,
    //     getRate: false,
    //     // deliveryType: this.changeDeliveryType({
    //     //   pickupDate: this.state.pickupDate,
    //     //   deliveryDate: this.state.deliveryDate,
    //     //   [name]: value
    //     // })
    //   });
    // }
  };

  render() {
    const { activity, contactNumber, method } = this.state;

    return (
      <div className="cancel-request-dialog">
        <DialogTitle>In case you're absent</DialogTitle>
        <DialogContent>
          <div className="p-col-12 p-md-12 p-lg-12" style={{ padding: '1rem' }}>
            <p className="heading">Contact Information</p>
            <div style={{ display: 'flex' }}>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  name="contactName"
                  // value={contactName}
                  onChange={this.onInputChange}
                  placeholder="Last Name"
                  // error={errors.contactName}
                  left={
                    <img
                      style={{ width: '1rem' }}
                      alt=""
                      src={require('../../../../static/icons/fullname-icon.png')}
                    />
                  }
                />
              </div>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  name="contactName"
                  // value={contactName}
                  onChange={this.onInputChange}
                  placeholder="First Name"
                  // error={errors.contactName}
                  left={
                    <img
                      style={{ width: '1rem' }}
                      alt=""
                      src={require('../../../../static/icons/fullname-icon.png')}
                    />
                  }
                />
              </div>
            </div>
            <div style={{ display: 'flex' }}>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  inputType="number"
                  // error={errors.contactNumber}
                  input={
                    <FormTelInput
                      name="contactNumber"
                      value={contactNumber}
                      onChange={this.onInputChange}
                      placeholder="Phone Number"
                      ref={this.props.tellInputRef}
                      className="add-order-phone-input"
                      styles={{
                        background: 'none',
                        padding: '5px 5px 5px 83px',
                        margin: '5px',
                        border: 'none',
                        width: '100%',
                        fontSize: '1.2em',
                        outline: 'none',
                        fontFamily: 'Exo2-Medium',
                        color: getPrimaryColors('primary'),
                      }}
                    />
                  }
                />
              </div>
            </div>
          </div>
          <div className="p-col-12 p-md-12 p-lg-12" style={{ padding: '1rem' }}>
            <p className="heading">Specify delivery method</p>
            <div style={{ display: 'flex' }}>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormNativeSelectInputField
                  type="react-select"
                  name="type"
                  options={[
                    { value: 'door', label: 'At my doorstep' },
                    { value: 'pickup', label: 'At pickup point' },
                  ]}
                  value={method}
                  onChange={this.handleSelectChange}
                  // error={errors.type}
                  left={
                    <img
                      style={{ width: '1rem' }}
                      alt=""
                      src={require('../../../../static/icons/request-type-icon.png')}
                    />
                  }
                />
              </div>
              <div className="p-col-6 p-md-6 p-lg-6">
                <FormInputS
                  inputType="number"
                  // error={errors.contactNumber}
                  input={
                    <FormTelInput
                      name="contactNumber"
                      value={contactNumber}
                      onChange={this.onInputChange}
                      placeholder="Phone Number"
                      ref={this.props.tellInputRef}
                      className="add-order-phone-input"
                      styles={{
                        background: 'none',
                        padding: '5px 5px 5px 83px',
                        margin: '5px',
                        border: 'none',
                        width: '100%',
                        fontSize: '1.2em',
                        outline: 'none',
                        fontFamily: 'Exo2-Medium',
                        color: getPrimaryColors('primary'),
                      }}
                    />
                  }
                />
              </div>
            </div>
          </div>
        </DialogContent>
        <DialogActions>
          <FormSubmitBtn
            label="Back"
            disabled={activity}
            onSubmit={this.props.handleDialogClose}
            style={{ width: 'unset', borderRadius: 4 }}
          />
          <FormSubmitBtn
            label="Validate"
            disabled={activity}
            onSubmit={this.handleCancelOrder}
            style={{
              width: 'unset',
              borderRadius: 4,
            }}
          />
        </DialogActions>
      </div>
    );
  }
}
AbsentCaseComponent.propTypes = {
  cancelType: PropTypes.string,
  handleSetDetails: PropTypes.func,
  handleDialogClose: PropTypes.func,
  details: PropTypes.shape({
    _id: PropTypes.string,
    pickupTime: PropTypes.shape(),
    pickupDate: PropTypes.string,
  }),
};
