/* eslint-disable no-console */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import {
  Checkbox,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Dialog,
} from '@material-ui/core';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import moment from 'moment';

import Page from '../../../components/layout/Page';
import { getPrimaryColors } from '../../../utils/functions';
import FormInputS from '../../../components/form/FormInputS';
import FileUploadS from '../../../components/form/FileUploadS';
import FormSubmitBtn from '../../../components/form/FormSubmitBtn';
import { classNames, getDiscountedPrice } from '../../../utils/functions';
import FormDateInputField from '../../../components/form/FormDateInputField';
import FormTimeInputField from '../../../components/form/FormTimeInputField';
import FormNativeSelectInputField from '../../../components/form/FormNativeSelectInputField';
import SelectLocationReusableComponent from '../../../components/map/SelectLocationReusableComponent';
import FormTelInput from '../../../components/form/FormTelInput';
import FormTextArea from '../../../components/form/FormTextArea';

class ShippingAddComponent extends Component {
  constructor(props) {
    super(props);

    this.headingRef = {};
    this.headingHeight = 0;
  }

  componentDidMount() {
    this.headingHeight = this.headingRef.offsetHeight || 0;
    this.setState({});
  }

  handleHeadingRef = (ref) => (this.headingRef = ref);

  render() {
    const {
      details,
      user,
      activity,
      type,
      commodities,
      notes,
      items,
      pickupDate,
      flexibleDate,
      from,
      to,
      contactName,
      contactNumber,
      contactEmail,
      deliveryType,
      preferredMode,
      transporters,
      itemName,
      itemQuantity,
      itemWeight,
      itemLength,
      itemWidth,
      itemHeight,
      dialogData,
      onInputChange,
      errors,
      categories,
      promo,
      getRate,
      rates,
      promoObject,
      itemImages,
      handleImageClick,
      handleSelectChange,
      handleDialogVisibility,
      flexibleDeliveryDate,
      allowMultipleRoutes,
      needHelper,
      deliveryTimeFrom,
      deliveryTimeTo,
      deliveryDate,
      bonus,
      handleChange,
      handleDialogClose,
      canReturn,
      returnDays,
      storeOrderID,
      deliveryMethod,
      warehouses,
      products,
      returnAddress,
    } = this.props;
    console.log('This is all products i received', products);
    const { open: isOpen, comp } = this.props.dialogChangeUnits;
    const Comp = this.props.handleGetDialogComponent(comp);
    return (
      <Page activity={activity} className="orders-add-container" noActivity>
        <div className="page-title">
          <span>Create a shipping</span>
        </div>
        <div className="p-grid" style={{ margin: 0, padding: '1rem' }}>
          <div className="p-col-12 p-md-12 p-lg-4" style={{ padding: '1rem' }}>
            <p className="heading" ref={this.handleHeadingRef}>
              Order number
            </p>
            <FormInputS
              disabled
              name="storeOrderID"
              value={storeOrderID}
              onChange={onInputChange}
              placeholder="Order number"
              error={errors.storeOrderID}
            />
          </div>
          <div className="p-col-12 p-md-12 p-lg-12" style={{ padding: '1rem' }}>
            <p className="heading">Package dimensions</p>
          </div>

          <div className="p-col-12 p-md-6 p-lg-6" style={{ padding: '1rem' }}>
            <p>Item Name</p>
            <div>
              <FormNativeSelectInputField
                valueKey="_id"
                type="react-select"
                name="itemName"
                options={products}
                value={this.props.itemName}
                onChange={handleSelectChange}
                // error={errors.returnAddress}
                getOptionLabel={(option) => option.name}
                left={
                  <img
                    style={{ width: '1.2rem' }}
                    alt=""
                    src={require('../../../static/icons/fullname-icon.png')}
                  />
                }
              />
            </div>
          </div>
          <div className="p-col-12 p-md-6 p-lg-6" style={{ padding: '1rem' }}>
            <p>Item Quantity</p>
            <FormInputS
              inputType="number"
              name="itemQuantity"
              value={itemQuantity}
              onChange={onInputChange}
              placeholder="Quantity"
              error={errors.itemQuantity}
              left={
                <img
                  style={{ width: '1.2rem' }}
                  alt=""
                  src={require('../../../static/icons/quantity-icon.png')}
                />
              }
            />
          </div>
          <div className="p-col-12 p-md-6 p-lg-3" style={{ padding: '1rem' }}>
            <p>Item Width</p>
            <FormInputS
              disabled
              inputType="float"
              name="itemWidth"
              value={itemWidth}
              onChange={onInputChange}
              placeholder={`Width (${user.config.measurementUnit})`}
              error={errors.itemWidth}
              left={
                <img
                  alt=""
                  src={require('../../../static/icons/width-icon.png')}
                />
              }
            />
          </div>
          <div className="p-col-12 p-md-6 p-lg-3" style={{ padding: '1rem' }}>
            <p>Item Height</p>
            <FormInputS
              disabled
              inputType="float"
              name="itemHeight"
              value={itemHeight}
              onChange={onInputChange}
              placeholder={`Height (${user.config.measurementUnit})`}
              error={errors.itemHeight}
              left={
                <img
                  alt=""
                  src={require('../../../static/icons/height-icon.png')}
                />
              }
            />
          </div>
          <div className="p-col-12 p-md-6 p-lg-3" style={{ padding: '1rem' }}>
            <p>Item Length</p>
            <FormInputS
              disabled
              inputType="float"
              name="itemLength"
              value={itemLength}
              onChange={onInputChange}
              placeholder={`Length (${user.config.measurementUnit})`}
              error={errors.itemLength}
              left={
                <img
                  alt=""
                  src={require('../../../static/icons/length-icon.png')}
                />
              }
            />
          </div>

          <div className="p-col-12 p-md-6 p-lg-3" style={{ padding: '1rem' }}>
            <p>Item Weight</p>
            <FormInputS
              disabled
              inputType="float"
              name="itemWeight"
              value={itemWeight}
              onChange={onInputChange}
              placeholder={`Weight (${user.config.weightUnit})`}
              error={errors.itemWeight}
              left={
                <img
                  alt=""
                  src={require('../../../static/icons/weight-icon.png')}
                />
              }
            />
          </div>
          {/* <div
            className={classNames([
              ['p-col-3 p-md-3 p-lg-3', !canReturn],
              ['p-col-3 p-md-3 p-lg-3', canReturn],
            ])}
            style={{ padding: '1rem' }}
          >
            <FormControlLabel
              className="form-checkbox-label-input"
              control={
                <Checkbox
                  checked={canReturn}
                  onChange={onInputChange}
                  value="canReturn"
                  name="canReturn"
                  color="default"
                />
              }
              label="Can Return"
            />
          </div>
          {canReturn ? (
            <div className="p-col-12 p-md-6 p-lg-4">
              <FormInputS
                name="returnDays"
                type="number"
                value={returnDays}
                onChange={onInputChange}
                placeholder="Max return days"
                error={errors.returnDays}
                left={
                  <img
                    style={{ width: '1rem' }}
                    alt=""
                    src={require('../../../static/icons/fullname-icon.png')}
                  />
                }
              />
            </div>
          ) : null} */}

          <div className="p-col-12 p-md-12 p-lg-12" style={{ padding: '1rem' }}>
            <p className="heading">Contact details</p>
          </div>
          <div className="p-col-12 p-md-12 p-lg-6" style={{ padding: '1rem' }}>
            <div className="p-col-12">Sender / Receiver Full Name</div>
            <FormInputS
              name="contactName"
              value={contactName}
              onChange={onInputChange}
              placeholder="Full Name"
              error={errors.contactName}
              left={
                <img
                  style={{ width: '1rem' }}
                  alt=""
                  src={require('../../../static/icons/fullname-icon.png')}
                />
              }
            />
          </div>
          <div className="p-col-12 p-md-12 p-lg-6" style={{ padding: '1rem' }}>
            <div className="p-col-12">Sender / Receiver Phone Number</div>
            <FormInputS
              inputType="number"
              error={errors.contactNumber}
              input={
                <FormTelInput
                  name="contactNumber"
                  inputType="number"
                  value={contactNumber}
                  onChange={onInputChange}
                  placeholder="Phone Number"
                  ref={this.props.tellInputRef}
                  className="add-order-phone-input"
                  styles={{
                    background: 'none',
                    padding: '5px 5px 5px 83px',
                    margin: '5px',
                    border: 'none',
                    width: '100%',
                    fontSize: '1.2em',
                    outline: 'none',
                    fontFamily: 'Exo2-Medium',
                    color: getPrimaryColors('primary'),
                  }}
                />
              }
            />
          </div>
          <div className="p-col-12 p-md-12 p-lg-6" style={{ padding: '1rem' }}>
            <div className="p-col-12">Sender / Receiver Email address</div>
            <FormInputS
              name="contactEmail"
              value={contactEmail}
              onChange={onInputChange}
              placeholder="Email address"
              error={errors.contactEmail}
              left={
                <img
                  style={{ width: '1rem' }}
                  alt=""
                  src={require('../../../static/icons/fullname-icon.png')}
                />
              }
            />
          </div>
          <div className="p-col-12">
            <div className="p-md-12 p-lg-6">
              <p className="heading">Pickup Date</p>
              <FormDateInputField
                placeholder="Pickup Date"
                name="pickupDate"
                value={pickupDate ? new Date(pickupDate) : null}
                onChange={(e) =>
                  onInputChange({
                    target: {
                      name: 'pickupDate',
                      value: moment(e),
                    },
                  })
                }
                error={errors.pickupDate}
                left={
                  <img
                    style={{ width: '1rem' }}
                    alt=""
                    src={require('../../../static/icons/date-icon.png')}
                  />
                }
              />
            </div>
          </div>

          <div style={{ padding: '1rem' }}>
            <p className="heading">Delivery method</p>
            <FormControl
              className="p-col-12 p-md-12 p-lg-12"
              component="fieldset"
            >
              <RadioGroup
                aria-label="deliver"
                name="deliveryMethod"
                value={deliveryMethod}
                onChange={onInputChange}
              >
                <FormControlLabel
                  value="flex"
                  control={<Radio />}
                  label="Flex"
                />
                <FormControlLabel
                  value="classic"
                  control={<Radio />}
                  label="Classic"
                />
                <FormControlLabel
                  value="green"
                  control={<Radio />}
                  label="Green"
                />
              </RadioGroup>
            </FormControl>
          </div>

          <div className="p-col-12" style={{ padding: '1rem' }}>
            <p className="heading">Pick up address</p>
            <div className="p-col-12 p-md-12 p-lg-6">
              <FormNativeSelectInputField
                valueKey="_id"
                type="react-select"
                name="pickup"
                options={warehouses}
                value={this.props.pickup}
                onChange={handleSelectChange}
                error={errors.pickup}
                getOptionLabel={(option) => option.name}
                left={
                  <img
                    style={{ width: '1.2rem' }}
                    alt=""
                    src={require('../../../static/icons/fragile-icon.png')}
                  />
                }
              />
            </div>
          </div>
          {/* <div className="p-col-12" style={{ padding: '1rem' }}>
            <p className="heading">Return address</p>
            <div className="p-col-12 p-md-12 p-lg-7">
              <FormNativeSelectInputField
                valueKey="_id"
                type="react-select"
                name="returnAddress"
                options={warehouses}
                value={this.props.returnAddress}
                onChange={handleSelectChange}
                error={errors.returnAddress}
                getOptionLabel={(option) => option.name}
                left={
                  <img
                    style={{ width: '1.2rem' }}
                    alt=""
                    src={require('../../../static/icons/fragile-icon.png')}
                  />
                }
              />
            </div>
          </div> */}
          <div className="p-col-12 p-md-12 p-lg-6" style={{ padding: '1rem' }}>
            <p className="heading">Drop off address</p>
            <div
              className={classNames([
                'pickup-dropoff-input-div',
                ['form-error', errors.dropoff],
              ])}
              onClick={() =>
                handleDialogVisibility(true, {
                  dropoff: this.props.dropoff,
                  heading: 'Select Dropoff',
                  name: 'dropoff',
                })
              }
            >
              <img
                style={{ width: '1rem' }}
                alt=""
                src={require('../../../static/icons/location-icon.png')}
              />
              <span
                style={{
                  color: this.props.dropoff.address
                    ? getPrimaryColors('primary')
                    : getPrimaryColors('font-color'),
                }}
              >
                {this.props.dropoff.address || 'Select Dropoff Location'}
              </span>
            </div>
            {errors.dropoff ? (
              <small>{this.props.pickupDropOffMessage}</small>
            ) : null}
          </div>

          <div
            className="p-col-12 p-md-6 p-lg-12"
            style={{
              padding: '1rem',
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'right',
            }}
          >
            <FormSubmitBtn
              label="Create"
              // disabled={activity || !getRate}
              onSubmit={this.props.submitOrder}
              style={{
                padding: '4px 1rem',
                borderRadius: 4,
                width: 300,
                margin: 0,
                flexGrow: 1,
              }}
            />
          </div>
        </div>
        <Dialog
          open={isOpen}
          onClose={handleDialogClose}
          aria-labelledby="actions-dialog"
          className="u-r-d-actions-dialog"
        >
          <Comp
            {...this.props}
            details={details}
            dialog={dialogData}
            cancelType="user"
            ratingKey="rating"
            feedbackTo="transporter"
            handleDialogClose={handleDialogClose}
            handleSetDetails={this.props.handleSetDetails}
          />
        </Dialog>
        <Dialog
          fullScreen={true}
          className="full-width-dialog full-height-dialog"
          style={{ margin: 50 }}
          open={this.props.isDialogOpen}
          onClose={handleDialogVisibility}
        >
          <SelectLocationReusableComponent
            editable={true}
            addressKey="address"
            ref={this.props.mapComponentRef}
            heading={dialogData.heading}
            location={
              this.props.isDialogOpen
                ? this.props[dialogData.name].location
                : []
            }
            handleMapClick={this.props.handleDialogCallback}
            handleSelectPlace={this.props.handleDialogCallback}
            actions={
              <div>
                <FormSubmitBtn
                  label="Done"
                  disabled={activity}
                  style={{ borderRadius: 4, width: 'unset' }}
                  onSubmit={handleDialogVisibility}
                />
              </div>
            }
          />
        </Dialog>
      </Page>
    );
  }
}

ShippingAddComponent.propTypes = {
  user: PropTypes.shape(),
  getRate: PropTypes.bool,
  type: PropTypes.shape(),
  promo: PropTypes.string,
  getRates: PropTypes.func,
  activity: PropTypes.bool,
  errors: PropTypes.shape(),
  pickup: PropTypes.shape(),
  onAddItem: PropTypes.func,
  onFileDrop: PropTypes.func,
  dropoff: PropTypes.shape(),
  itemName: PropTypes.string,
  notes: PropTypes.string,
  itemType: PropTypes.shape(),
  transporters: PropTypes.arrayOf(PropTypes.object),
  submitOrder: PropTypes.func,
  itemWidth: PropTypes.string,
  isDialogOpen: PropTypes.bool,
  itemHeight: PropTypes.string,
  itemLength: PropTypes.string,
  itemWeight: PropTypes.string,
  flexibleDate: PropTypes.bool,
  onDeleteItem: PropTypes.func,
  contactName: PropTypes.string,
  dialogData: PropTypes.shape(),
  onInputChange: PropTypes.func,
  onSubmitOrder: PropTypes.func,
  pickupDate: PropTypes.shape(),
  to: PropTypes.instanceOf(Date),
  promoObject: PropTypes.shape(),
  itemQuantity: PropTypes.string,
  mapComponentRef: PropTypes.func,
  deliveryDate: PropTypes.shape(),
  preferredMode: PropTypes.shape(),
  transportersType: PropTypes.shape(),
  contactNumber: PropTypes.string,
  deliveryType: PropTypes.shape(),
  handleImageClick: PropTypes.func,
  from: PropTypes.instanceOf(Date),
  handleSelectChange: PropTypes.func,
  handleDialogCallback: PropTypes.func,
  flexibleDeliveryDate: PropTypes.bool,
  allowMultipleRoutes: PropTypes.bool,
  needHelper: PropTypes.bool,
  handleDialogVisibility: PropTypes.func,
  pickupDropOffMessage: PropTypes.string,
  deliveryTimeTo: PropTypes.instanceOf(Date),
  deliveryTimeFrom: PropTypes.instanceOf(Date),
  rates: PropTypes.oneOfType([
    PropTypes.bool,
    PropTypes.shape({ price: PropTypes.number }),
  ]),
  itemImages: PropTypes.arrayOf(PropTypes.string),
  categories: PropTypes.arrayOf(PropTypes.object),
  commodities: PropTypes.arrayOf(PropTypes.object),
  items: PropTypes.arrayOf(PropTypes.object),
  subCategories: PropTypes.arrayOf(PropTypes.object),
  tellInputRef: PropTypes.func,
};

export default ShippingAddComponent;
