import React from 'react';
import PropTypes from 'prop-types';

import Page from '../../../components/layout/Page';
import FormInputS from '../../../components/form/FormInputS';
import FormSubmitBtn from '../../../components/form/FormSubmitBtn';
import FileUpload from '../../../components/form/FileUpload';
import FlagInput from '../../../components/FlagistInput/FlagInput';

const MyDriversAddComponent = (props) => {
  const { onInputChange, activity, errors, handleChange } = props;

  return (
    <Page className="m-d-add-page" activity={activity} noActivity>
      <div className="page-title">
        <span>Add Driver</span>
      </div>
      <div className="p-grid" style={{ padding: '1em', margin: 0 }}>
        <div className="p-col-12 p-sm-6">
          <FormInputS
            name="firstName"
            type="text"
            label="First Name"
            value={props.firstName}
            placeholder="First Name"
            onChange={onInputChange}
            error={errors.firstName}
          />
        </div>
        <div className="p-col-12 p-sm-6">
          <FormInputS
            name="lastName"
            type="text"
            label="Last Name"
            value={props.lastName}
            placeholder="Last Name"
            error={errors.lastName}
            onChange={onInputChange}
          />
        </div>
        <div className="p-col-12 p-sm-6">
          <FormInputS
            name="email"
            type="email"
            label="Email"
            value={props.email}
            placeholder="Email"
            error={errors.email}
            onChange={onInputChange}
          />
        </div>
        <div className="p-col-12 p-sm-6">
          {/* <FormInputS
            inputType="number"
            name="mobile"
            type="number"
            label="Phone Number"
            value={props.mobile}
            error={errors.mobile}
            onChange={onInputChange}
            placeholder="Phone Number"
          /> */}

          <FlagInput
            // handleChangeMobile={(event) => handleChangeMobile(event)}
            onInputChange={props.onInputChange}
            value={props.mobile}
            handleOpenList={props.handleOpenList}
            open={props.open}
            handleChangeMobileCode={props.handleChangeMobileCode}
            // countryCode={'+92'}
          />
        </div>
        <div
          className="p-col-12"
          style={{ fontWeight: 'bold', color: '#2c2d5b' }}
        >
          Driving Licence
        </div>
        <div className="p-col-12 p-sm-6">
          {props.documents.length && props.documents[0] !== undefined ? (
            <div className="documents">
              <div>
                <img
                  src={props.documents[0]}
                  alt=""
                  className="add-driver-img"
                />
                <i
                  className="fas fa-times"
                  onClick={() => props.removeDocument(0, 'documents')}
                />
              </div>
            </div>
          ) : (
            <FileUpload
              multiple={false}
              message="+"
              activity={activity}
              className="drop-zone-div"
              onDrop={(acceptedFiles) =>
                handleChange({
                  target: {
                    name: 'files',
                    key: 'documents',
                    value: acceptedFiles,
                  },
                })
              }
            />
          )}
        </div>
        <div className="p-col-12 p-sm-6">
          {props.documents.length && props.documents[1] !== undefined ? (
            <div className="documents">
              <div>
                <img
                  src={props.documents[1]}
                  alt=""
                  className="add-driver-img"
                />
                <i
                  className="fas fa-times"
                  onClick={() => props.removeDocument(1, 'documents')}
                />
              </div>
            </div>
          ) : (
            <FileUpload
              multiple={false}
              message="+"
              activity={activity}
              className="drop-zone-div"
              onDrop={(acceptedFiles) =>
                handleChange({
                  target: {
                    name: 'files',
                    key: 'documents',
                    value: acceptedFiles,
                  },
                })
              }
            />
          )}
        </div>
        <div className="p-col-12 text-right" style={{ padding: '1eem' }}>
          <FormSubmitBtn
            onSubmit={props.onSubmit}
            disabled={props.activity}
            style={{ width: 'unset', borderRadius: 4 }}
          />
        </div>

        <div className="p-grid"></div>
        {/* <div className="p-grid">
          <div className="p-col-4 p-sm-4 p-lg-12">
            {props.licensePicture.length &&
            props.licensePicture[0] !== undefined ? (
              <div className="documents">
                <div>
                  <img src={props.licensePicture[0]} alt="" />
                  <i
                    className="fas fa-times"
                    onClick={() =>
                      props.removeDocument(0, 'licensePicture')
                    }
                  />
                </div>
              </div>
            ) : (
              <FileUpload
                message="+"
                multiple={false}
                activity={activity}
                className="drop-zone-div"
                onDrop={(acceptedFiles) =>
                  handleChange({
                    target: {
                      name: 'files',
                      key: 'licensePicture',
                      value: acceptedFiles,
                    },
                  })
                }
              />
            )}
          </div>
        </div> */}
      </div>
    </Page>
  );
};

MyDriversAddComponent.propTypes = {
  email: PropTypes.string,
  onSubmit: PropTypes.func,
  activity: PropTypes.bool,
  mobile: PropTypes.string,
  errors: PropTypes.shape(),
  lastName: PropTypes.string,
  firstName: PropTypes.string,
  onInputChange: PropTypes.func,
};

export default MyDriversAddComponent;
