import React, { Component } from 'react';
import { withStore } from '../../utils/store.util';
import WarehousesComponent from './WarehousesComponent';
import ApiCalls from '../../service/RequestHandler';
import { newGrowl } from '../../components/ui/GrowlComponent';
import { validateAdd } from './my-warehouse-validator';
import { toast } from 'react-toastify';

const INITIAL_STATE = {
  warehouses: [],
  name: '',
  dropoff: {},
  errors: {},
  activity: false,
  dialogData: {},
  isDialogOpen: false,
  isUpdate: false,
  warehouse: '',
};

class WarehousesContainer extends Component {
  constructor(props) {
    super(props);
    this.mapComponentRef = null;
    this.state = INITIAL_STATE;
  }

  componentDidMount() {
    this.getWarehouses();
  }

  getWarehouses = async () => {
    try {
      const response = await ApiCalls.getMyWarehouses();
      this.setState({
        warehouses: response.data,
      });
    } catch (error) {
      this.setState({ activity: false });
    }
  };

  handleDeleteWarehouse = async (id) => {
    this.props.store.setWithRender('messageDialog', {
      open: true,
      className: 'danger',
      actionBtnLabel: 'Delete',
      title: 'Are you sure you want to delete?',
      action: () => {
        this.deleteWarehouse(id);
      },
    });
  };

  deleteWarehouse = async (id) => {
    try {
      const response = await ApiCalls.deleteWarehouse(id);
      console.log('delete warehouse resposne', response.data);
      this.setState({
        warehouses: response.data,
      });
      toast.success('Your Warehouse has been deleted successfully.');
    } catch (error) {
      this.setState({ activity: false });
    }
  };

  handleEditWarehouse = async (item) => {
    if (this.isValid()) {
      this.setState({ activity: true });
      const { warehouse } = this.state;
      const data = {
        name: this.state.name,
        address: this.state.dropoff.address,
        city: this.state.dropoff.city,
        country: this.state.dropoff.country,
        location: {
          latitude: this.state.dropoff.location[1],
          longitude: this.state.dropoff.location[0],
        },
      };
      try {
       
        const response = await ApiCalls.updateWarehouse(warehouse, data);
        console.log('update response', response);
        this.setState({
          ...INITIAL_STATE,
          activity: false,
        });
        this.getWarehouses();
        toast.success('Your Warehouse has been updated successfully.');
      } catch (error) {
        this.setState({ activity: false });
        console.log('update error', error);
      }
    }
  };

  onWarehouseEdit = (item) => {
    const adderess = {
      address: item.address,
      city: item.city,
      country: item.country,
      location: [item.location.coordinates[0], item.location.coordinates[1]],
    };

    this.setState({
      name: item.name,
      dropoff: adderess,
      warehouse: item._id,
      isUpdate: true,
    });

    console.log("dropoff", this.state.dropoff);
  };

  handleDialogVisibility = (value, data) => {
    console.log('data', data);
    // let datta ={
    //   heading:"hh",
    //   name:"jj",
    //   dropoff:
    // };
    this.setState({
      dialogData: typeof value === 'boolean' ? data : {},
      isDialogOpen: typeof value === 'boolean' ? value : false,
    },()=>{console.log('dialogData', this.state.dialogData);});
    
  };

  handleDialogCallback = async (e) => {
    const name = this.state.dialogData.name;
    console.log("onClick map",e.location);
    console.log("event",e);
    await this.setState({ [name]: e });
    await this.setState({
      dropoff: e
    });
    await this.setState({
      dialogData: {
        dropoff : this.state.dropoff
      }
    },()=>{console.log('dialogData', this.state.dialogData);})
    this.mapComponentRef.setZoomForSingleMarker(e.location);
    // this.restrictToEU();
    // this.setTerritory();
  };

  onInputChange = (event) => {
    const { name, value, checked, type } = event.target;
    if (this.state.errors[name]) {
      this.setState((prevState) => {
        return {
          [name]: type === 'checkbox' ? checked : value,
          errors: { ...prevState.errors, [name]: false },
          getRate: false,
          // deliveryType: this.changeDeliveryType({
          //   pickupDate: prevState.pickupDate,
          //   deliveryDate: prevState.deliveryDate,
          //   [name]: value
          // })
        };
      });
    } else {
      this.setState({
        [name]: type === 'checkbox' ? checked : value,
        getRate: false,
        // deliveryType: this.changeDeliveryType({
        //   pickupDate: this.state.pickupDate,
        //   deliveryDate: this.state.deliveryDate,
        //   [name]: value
        // })
      });
    }
  };

  handleAddWarehouse = async () => {
    if (this.isValid()) {
      this.setState({ activity: true });
      const data = {
        name: this.state.name,
        address: this.state.dropoff.address,
        city: this.state.dropoff.city,
        country: this.state.dropoff.country,
        location: {
          latitude: this.state.dropoff.location[1],
          longitude: this.state.dropoff.location[0],
        },
      };

      try {
        const response = await ApiCalls.addWarehouse(data);
        // eslint-disable-next-line no-console
        this.setState({
          ...INITIAL_STATE,
          activity: false,
        });
        this.getWarehouses();
        toast.success('Your Warehouse has been added successfully.');
      } catch (error) {
        this.setState({ activity: false });
      }
    }
  };

  isValid() {
    const { errors, isValid } = validateAdd(this.state);
    if (!isValid) {
      this.setState({ errors }, () => {
        console.log('states', this.state.errors);
      });
    }

    return isValid;
  }

  handleMapComponentRef = (ref) => (this.mapComponentRef = ref);

  render() {
    return (
      <WarehousesComponent
        {...this.state}
        handleDeleteWarehouse={this.handleDeleteWarehouse}
        onWarehouseEdit={this.onWarehouseEdit}
        handleEditWarehouse={this.handleEditWarehouse}
        onInputChange={this.onInputChange}
        handleAddWarehouse={this.handleAddWarehouse}
        handleDialogCallback={this.handleDialogCallback}
        handleDialogVisibility={this.handleDialogVisibility}
        handleMapComponentRef={this.handleMapComponentRef}
      />
    );
  }
}

export default withStore(WarehousesContainer);
