import React, { Component } from 'react';
import Container from '@material-ui/core/Container';
import { Grid } from '@material-ui/core';
import Header from '../Home/Header/Header';
import Footer from '../Footer';
import ApiCalls from '../../service/RequestHandler';
import CardWithImage from './../../components/CardWithImage/CardWithImage';

class NewsContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      clientsData: [],
    };
  }

  async componentDidMount() {
    const response = await ApiCalls.getClients();
    this.setState({
      clientsData: response.data.clients.filter((item) => item.active === true),
    });
  }

  render() {
    return (
      <React.Fragment>
        <Header
          showHeader={true}
          //   handleLoginModalOpen={props.handleLoginModalOpen}
        />

        <div>
          <div style={{ marginTop: '5%', padding: '3% 0' }}>
            <Container>
              <Grid container spacing={2}>
                <Grid item md={12}>
                  <h1 style={{ textAlign: 'center', marginBottom: '50px' }}>
                    Clients
                  </h1>
                </Grid>
              </Grid>
              {this.state.clientsData.length > 0 ? (
                <Grid container spacing={2}>
                  {this.state.clientsData.map((item) => (
                    <Grid
                      item
                      xs={12}
                      sm={12}
                      md={6}
                      lg={4}
                      style={{ paddingBottom: '10%' }}
                    >
                      <CardWithImage
                      id={item._id}
                      image={item.image}
                      title={item.title}
                      description={item.decription}
                    />
                    </Grid>
                  ))}
                </Grid>
              ) : (
                <h3
                  style={{
                    textAlign: 'center',
                    color: '#a9b1bbb0',
                    fontSize: '30px',
                  }}
                >
                  {' '}
                  No Client found{' '}
                </h3>
              )}
            </Container>
          </div>
        </div>
        <Footer bgImagePath="/assets/images/footer-bg.png" />
      </React.Fragment>
    );
  }
}

export default NewsContainer;
