import React from 'react';
import PropTypes from 'prop-types';
import ButtonComponent from './ButtonComponent';
import Grid from '@material-ui/core/Grid';
import { colors } from '../constants/colors';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

const SliderComponent = (props) => {
  const classes = useStyles();
  const { heading, description, color, image } = props;
  return (
    <div>
      <div
        className="carrousel_image"
        style={{
          backgroundColor: '#ffffff',
        }}
      >
        <div className={classes.root}>
          <Grid
            container
            alignItems="center"
            className="main-section padding-70"
            style={{ marginTop: 100 }}
            spacing={10}
          >
            <Grid item xs={12} sm={7}>
              <div className="inner">
                <h1
                  className="main-title m-b-40"
                  style={{ color: colors.orange }}
                >
                  {heading}
                </h1>
                <div className="m-b-40">
                  <p className="m-b-40" style={{ color: colors.orange }}>
                    {description}
                  </p>
                </div>
                <ButtonComponent
                  bgColor={colors.orange}
                  borderRadius="50px"
                  padding="15px"
                  width="200px"
                  color={colors.white}
                >
                  Get started
                </ButtonComponent>
              </div>
            </Grid>
            <Grid item xs={12} sm={5}>
              <img alt="main-image" style={{ width: '100%' }} src={image} />
            </Grid>
          </Grid>
        </div>
      </div>
    </div>
  );
};

SliderComponent.propTypes = {
  sliderImage: PropTypes.string,
};

export default SliderComponent;
