import React, { Component } from 'react';
import FormInputS from '../../../components/form/FormInputS';
import FormTelInput from '../../../components/form/FormTelInput';
import { getPrimaryColors } from '../../../utils/functions';
import FormNativeSelectInputField from '../../../components/form/FormNativeSelectInputField';
import { languages } from '../../../utils/unit.util';
import FormSubmitBtn from '../../../components/form/FormSubmitBtn';
import ApiCalls from '../../../service/RequestHandler';
import { newGrowl } from '../../../components/ui/GrowlComponent';
import * as authUtil from '../../../utils/auth.util';
import { authClass } from '../../../utils/auth.util';
import ButtonComponent from '../../../components/ButtonComponent';
import { colors } from '../../../constants/colors';

const ContactDetailsComponent = (props) => {
  const {
    name,
    mobile,
    language,
    onInputChange,
    handleSelectChange,
    tellInputRef,
    handleContactDetails,
  } = props;

  return (
    <div className="orders-add-container">
      <div className="p-grid">
        <div className="p-col-12 p-md-6 p-lg-12">
          <p className="heading">Contact details</p>
        </div>
        <div className="p-col-12 p-md-6 p-lg-3">
          <p>Contact person</p>

          <FormInputS
            name="name"
            value={name}
            onChange={onInputChange}
            placeholder="Contact person"
            // error={errors.contactName}
          />
        </div>
        <div className="p-col-12 p-md-6 p-lg-3">
          <p> Phone</p>

          <FormInputS
            inputType="number"
            // error={errors.contactNumber}
            input={
              <FormTelInput
                name="mobile"
                value={mobile}
                onChange={onInputChange}
                placeholder="Phone"
                ref={tellInputRef}
                className="add-order-phone-input"
                styles={{
                  background: 'none',
                  padding: '5px 5px 5px 83px',
                  margin: '5px',
                  border: 'none',
                  width: '100%',
                  fontSize: '1.2em',
                  outline: 'none',
                  fontFamily: 'Exo2-Medium',
                  color: getPrimaryColors('primary'),
                }}
              />
            }
          />
        </div>
        <div className="p-col-12 p-md-6 p-lg-3">
          <p> Languages</p>
          <FormNativeSelectInputField
            name="language"
            type="react-select"
            style={{ padding: 0 }}
            // error={this.state.error}
            value={
              languages.find((options) => options.value === language.value) || {
                value: '',
                label: 'Language',
              }
            }
            onChange={handleSelectChange}
            options={languages}
            getOptionValue={(item) => item._id}
            // formatOptionLabel={(item) => getVehicleName(item)}
          />
        </div>
        <div className="p-col-12 p-md-6 p-lg-3 m-t-16">
          <ButtonComponent
            bgColor={colors.orange}
            borderRadius="50px"
            padding="10px 5px"
            width="200px"
            marginRight="20px"
            marginTop="35px"
            color="#fff"
            onClick={handleContactDetails}
          >
            Save
          </ButtonComponent>
        </div>
      </div>
    </div>
  );
};

export default ContactDetailsComponent;
