import React, { Component } from 'react';
import styled from 'styled-components';
import {
  Heading,
  HeadingLink,
  LinkList,
  DropdownSection,
  Icon,
} from './Components';
import routes from '../../../constants/route-constants';

const SigninDropdownEl = styled.div`
  width: 18.5rem;
`;

class SigninDropdown extends Component {
  shouldComponentUpdate(nextProps, nextState) {
    if (nextProps.handleLoginModalOpen == undefined) {
      return false;
    }
    return true;
  }
  render() {
    const { handleLoginModalOpen } = this.props;
    const props = this.props;
    return (
      <SigninDropdownEl>
        <DropdownSection data-first-dropdown-section>
          <ul>
            <HeadingLink>
              <a
                onClick={function () {
                  return handleLoginModalOpen(routes.typeMerchant);
                }}
              >
                E-merchant BO access
              </a>
            </HeadingLink>
            <HeadingLink>
              <a
                onClick={function () {
                  return handleLoginModalOpen(routes.typeFleetManager);
                }}
              >
               Carrier BO Access Or Fleet Manager
              </a>
            </HeadingLink>
          </ul>
        </DropdownSection>
      </SigninDropdownEl>
    );
  }
}

export default SigninDropdown;
