import React, { Component } from 'react';
import Header from './../../../containers/Home/Header/Header';
import { Grid } from '@material-ui/core';
import Container from '@material-ui/core/Container';
import { colors } from './../../../constants/colors';
import FormTextField from './../../../components/form/FormTextField';
import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import ApiCalls from '../../../service/RequestHandler';
import { authClass } from '../../../utils/auth.util';
import { newGrowl } from '../../../components/ui/GrowlComponent';
import { validateCreateAccount } from './../validators/create-account-validator';

const apiCallType = {
  user: ApiCalls.signup,
  fleetmanager: ApiCalls.signupTransporter,
};

class FleetManager extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activity: false,
      firstName: '',
      lastName: '',
      email: '',
      password: '',
      confirmPassword: '',
      noOfVehicles: '',
      noOfDrivers: '',
      countryCode: '+',
      mobile: '',
      errors: {},
    };

    this.handleTelInputRef = (ref) => (this.telInput = ref);
  }

  handleChange = (name, value) => {
    if (this.state.errors[name]) {
      this.setState((prevState) => ({
        [name]: value,
        errors: { ...prevState.errors, [name]: false },
      }));
    } else {
      this.setState({ [name]: value });
    }
  };

  handleSubmit = async (e) => {
    e.preventDefault();

    const countryCode = this.state.countryCode;
    this.setState({ activity: true });
    try {
      const response = await apiCallType['fleetmanager']({
        firstName: this.state.firstName,
        lastName: this.state.lastName,
        email: this.state.email,
        password: this.state.password,
        numberOfVehicles: this.state.noOfVehicles,
        numberOfDrivers: this.state.nuoOfDrivers,
        mobile: countryCode + this.state.mobile,
        transporterType: 'fleetOwner',
      });
      const data = response.data;
      if (data.token) {
        const userDataToSave = {
          token: data.token,
        };
        await authClass.setUser(userDataToSave);
        // authClass.setUser = userDataToSave;
        this.setState({
          activity: false,
          verify: true,
          countryCode,
        });
      }
    } catch (error) {
      newGrowl.showGrowl('error', 'Error', error.response.data.message);
      this.setState({ activity: false });
    }
  };

  render() {
    return (
      <React.Fragment>
        <Header
          showHeader={true}
          handleLoginModalOpen={this.props.handleLoginModalOpen}
        />

        <div>
          <div style={{ marginTop: '100px' }}>
            <Container>
              <Grid container>
                <Grid
                  item
                  xs={12}
                  sm={12}
                  md={6}
                  lg={6}
                  style={{ paddingRight: '20px' }}
                >
                  <Container>
                    <Grid container spacing={2}>
                      <Grid
                        item
                        xs={12}
                        sm={12}
                        md={12}
                        lg={12}
                        style={{ marginBottom: '30px', marginTop: '100px' }}
                      >
                        <h1 className="heading-size">
                          Sign Up for Fleet Manager
                        </h1>
                      </Grid>
                      <Grid item xs={12} sm={12} md={6} lg={6}>
                        <div>
                          <FormTextField
                            type="text"
                            required={true}
                            label="First name"
                            placeholder="Enter your first name"
                            name="firstName"
                            value={this.state.firstName}
                            onChange={(e) =>
                              this.handleChange(e.target.name, e.target.value)
                            }
                            error={this.state.errors.firstName}
                          />
                        </div>
                      </Grid>
                      <Grid item xs={12} sm={12} md={6} lg={6}>
                        <div>
                          <FormTextField
                            type="text"
                            required={true}
                            label="Last name"
                            placeholder="Enter your last name"
                            name="lastName"
                            value={this.state.lastName}
                            onChange={(e) =>
                              this.handleChange(e.target.name, e.target.value)
                            }
                            error={this.state.errors.lastName}
                          />
                        </div>
                      </Grid>
                      <Grid item xs={12} sm={12} md={12} lg={12}>
                        <div>
                          <FormTextField
                            type="email"
                            required={true}
                            label="Email Address"
                            placeholder="Enter your email address"
                            name="email"
                            value={this.state.email}
                            onChange={(e) =>
                              this.handleChange(e.target.name, e.target.value)
                            }
                            error={this.state.errors.email}
                          />
                        </div>
                      </Grid>
                      <Grid item xs={12} sm={12} md={12} lg={12}>
                        <div style={{ display: 'flex' }}>
                          <FormTextField
                            style={{ width: '10% !important' }}
                            type="number"
                            required={true}
                            label="Country code"
                            placeholder="Enter your country code"
                            name="countryCode"
                            value={this.state.countryCode}
                            onChange={(e) =>
                              this.handleChange(e.target.name, e.target.value)
                            }
                            error={this.state.errors.countryCode}
                          />
                          {/* <TextField
                            style={{ paddingTop: '32px' }}
                            select
                            required={true}
                            name="countryCode"
                            value={this.state.countryCode}
                            onChange={(e) =>
                              this.handleChange(e.target.name, e.target.value)
                            }
                            error={this.state.errors.countryCode}
                          >
                            {this.state.countries.map((option) => (
                              <MenuItem key={option.value} value={option.value}>
                                {option.label}
                              </MenuItem>
                            ))}
                          </TextField> */}

                          <FormTextField
                            type="number"
                            required={true}
                            label="Mobile no"
                            placeholder="Enter your mobile no"
                            name="mobile"
                            value={this.state.mobile}
                            onChange={(e) =>
                              this.handleChange(e.target.name, e.target.value)
                            }
                            error={this.state.errors.mobile}
                          />
                        </div>
                      </Grid>
                      <Grid item xs={12} sm={12} md={12} lg={12}>
                        <div>
                          <FormTextField
                            type="number"
                            required={true}
                            label="No of vehicles"
                            placeholder="Enter no of vehicles"
                            name="noOfVehicles"
                            value={this.state.noOfVehicles}
                            onChange={(e) =>
                              this.handleChange(e.target.name, e.target.value)
                            }
                            error={this.state.errors.noOfVehicles}
                          />
                        </div>
                      </Grid>
                      <Grid item xs={12} sm={12} md={12} lg={12}>
                        <div>
                          <FormTextField
                            type="number"
                            required={true}
                            label="No of drivers"
                            placeholder="Enter no of drivers"
                            name="noOfDrivers"
                            value={this.state.noOfDrivers}
                            onChange={(e) =>
                              this.handleChange(e.target.name, e.target.value)
                            }
                            error={this.state.errors.noOfDrivers}
                          />
                        </div>
                      </Grid>
                      <Grid item xs={12} sm={12} md={6} lg={6}>
                        <div>
                          <FormTextField
                            type="password"
                            required={true}
                            label="Password"
                            placeholder="Enter your password"
                            name="password"
                            value={this.state.password}
                            onChange={(e) =>
                              this.handleChange(e.target.name, e.target.value)
                            }
                            error={this.state.errors.password}
                          />
                        </div>
                      </Grid>
                      <Grid item xs={12} sm={12} md={6} lg={6}>
                        <div>
                          <FormTextField
                            type="password"
                            required={true}
                            label="Confirm password"
                            placeholder="Confirm your password"
                            name="confirmPassword"
                            value={this.state.confirmPassword}
                            onChange={(e) =>
                              this.handleChange(e.target.name, e.target.value)
                            }
                            error={this.state.errors.confirmPassword}
                          />
                        </div>
                      </Grid>
                      <Grid item xs={6} sm={6} md={6} lg={6}>
                        <p
                          style={{
                            display: 'inline-block',
                            paddingRight: '20px',
                            marginTop: '10px',
                          }}
                        >
                          Have an account?{' '}
                          <Link to={`/fleetmanager`}>
                            <span
                              style={{
                                color: colors.orange,
                                fontWeight: '700',
                              }}
                            >
                              Log In
                            </span>
                          </Link>
                        </p>
                      </Grid>
                      <Grid
                        item
                        xs={6}
                        sm={6}
                        md={6}
                        lg={6}
                        style={{ marginBottom: '70px' }}
                      >
                        <div style={{ textAlign: 'right' }}>
                          <Button
                            variant="contained"
                            style={
                              this.state.firstName === '' ||
                              this.state.lastName === '' ||
                              this.state.email === '' ||
                              this.state.countryCode === '' ||
                              this.state.mobile === '' ||
                              this.state.noOfVehicles === '' ||
                              this.state.noOfDrivers === '' ||
                              this.state.password === '' ||
                              this.state.confirmPassword === ''
                                ? { minWidth: '100px' }
                                : {
                                    background: colors.orange,
                                    color: '#fff',
                                    minWidth: '100px',
                                  }
                            }
                            disabled={
                              this.state.firstName === '' ||
                              this.state.lastName === '' ||
                              this.state.email === '' ||
                              this.state.countryCode === '' ||
                              this.state.mobile === '' ||
                              this.state.noOfVehicles === '' ||
                              this.state.noOfDrivers === '' ||
                              this.state.password === '' ||
                              this.state.confirmPassword === ''
                                ? true
                                : false
                            }
                          >
                            Sign Up
                          </Button>
                        </div>
                      </Grid>
                    </Grid>
                  </Container>
                </Grid>

                <Grid
                  item
                  xs={12}
                  sm={12}
                  md={6}
                  lg={6}
                  className="content-hide-at-959px"
                >
                  <div style={{ marginTop: '176px' }}>
                    <img
                      alt="image"
                      src={require('../../../static/images/Im-a-professional-signup.png')}
                    />
                  </div>
                </Grid>
              </Grid>
            </Container>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default FleetManager;
