import React from 'react';
import Page from '../../../components/layout/Page';
import CSVReader from 'react-csv-reader';
import { DropzoneArea } from 'material-ui-dropzone';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import FormSubmitBtn from '../../../components/form/FormSubmitBtn';
import ButtonComponent from '../../../components/ButtonComponent';
import { colors } from '../../../constants/colors';

const useStyles = makeStyles((theme) =>
  createStyles({
    previewChip: {
      minWidth: 160,
      maxWidth: 210,
    },
  })
);

const papaparseOptions = {
  header: true,
  dynamicTyping: true,
  skipEmptyLines: true,
  transformHeader: (header) => header.toLowerCase().replace(/\W/g, '_'),
};

const downloadCsvTemp = () => {
  let csvRow = [];
  let csvHeader = [
    [
      'pickup_address',
      'pickup_latitude',
      'pickup_longitude',
      'pickup_city',
      'pickup_country',
      'dropoff_address',
      'dropoff_latitude',
      'dropoff_longitude',
      'dropoff_city',
      'dropoff_country',
      'return_address',
      'return_latitude',
      'return_longitude',
      'return_city',
      'return_country',
      'item',
      'quantity',
      'returnable',
      'max_return_days',
      'weight',
      'length',
      'width',
      'height',
      'pickup_date',
      'delivery_type',
      'order_id',
      'customer_name',
      'customer_number',
      'customer_country_code',
      'customer_email',
    ],
  ];

  for (let i = 0; i < csvHeader.length; ++i) {
    csvRow.push(csvHeader[i].join(','));
  }

  let csvString = csvRow.join('%OA');

  let a = document.createElement('a');
  a.href = 'data:attachment/csv,' + csvString;
  a.target = '_Blank';
  a.download = 'file.csv';
  document.body.appendChild(a);
  a.click();
};

export default function UploadFileComponent(props) {
  const classes = useStyles();
  const { handleChange } = props;
  return (
    <Page className="u-profile-page" noActivity>
      <div className="page-title">
        <span>Upload file</span>
      </div>
      <div style={{ margin: 20 }}>
        <ButtonComponent
          bgColor={colors.orange}
          borderRadius="50px"
          padding="10px 5px"
          width="200px"
          marginRight="20px"
          marginTop="27px"
          marginBottom="80px"
          color="#fff"
          onClick={downloadCsvTemp}
        >
          Download Template
        </ButtonComponent>
        <p style={{ fontWeight: 'bold' }}>Select CSV File</p>
        <CSVReader
          cssClass="csv-reader-input"
          label=""
          onFileLoaded={props.handleChange}
          // onError={this.handleDarkSideForce}
          parserOptions={papaparseOptions}
          inputId="ObiWan"
          inputStyle={{ color: 'red' }}
        />
      </div>
      <div className="p-col-12 p-md-6 p-lg-3">
        <FormSubmitBtn
          label="Save"
          onSubmit={props.handleSubmit}
          style={{ width: '100%', borderRadius: 4, marginTop: 10 }}
        />
      </div>
    </Page>
  );
}
