import React from 'react';

import {
  TextField,
  Grid,
  makeStyles,
  Checkbox,
  FormControlLabel,
} from '@material-ui/core';

import { colors } from '../constants/colors';
import ApiCalls from '../service/RequestHandler';
import { newGrowl } from '../components/ui/GrowlComponent';
import ButtonComponent from '../components/ButtonComponent';
import FormInputS from '../components/form/FormInputS';
import FormNativeSelectInputField from '../components/form/FormNativeSelectInputField';
import { cities, vehicles } from '../utils/unit.util';
import FormTextArea from '../components/form/FormTextArea';
import CircleCheckedFilled from '@material-ui/icons/CheckCircle';
import CircleUnchecked from '@material-ui/icons/RadioButtonUnchecked';

const style = {
  backgroundColor: colors.white,
  borderRadius: 30,
};

export default class ContactFormforCustomer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      errors: {},
      activity: false,
      firstName: '',
      lastName: '',
      subject: '',
      message: '',
      city: { value: '', label: 'Select City...' },
      vehicle: { value: '', label: 'Select Vehicle...' },
      checked : false
    };
  }

  handleSubmit = async () => {
    if (this.isValid()) {
      this.setState({ activity: true });

      try {
        const response = await ApiCalls.sendContactUsMessage(this.state);
        newGrowl.showGrowl('success', 'Success', response.data.message);
        this.setState({
          activity: false,
          name: '',
          email: '',
          subject: '',
          message: '',
        });
      } catch (error) {
        this.setState({ activity: false });
      }
    }
  };

  handleChange = (e) => {
    const { name, value } = e.target;
    if (this.state.errors[name]) {
      this.setState((prevState) => ({
        [name]: value,
        errors: { ...prevState.errors, [name]: false },
      }));
      return;
    }
    this.setState({ [name]: value });
    console.log(this.state,"state h");
  };
  handleChangeForcheck = () => {
        this.setState({checked : !this.state.checked})
  }
  isValid = () => {
    let isValid = true;
    const errors = {};

    const keys = ['name', 'email', 'message', 'subject'];

    keys.forEach((key) => {
      if (!this.state[key]) {
        isValid = false;
        errors[key] = true;
      }
    });

    this.setState({ errors });
    return isValid;
  };

  render() {
    const {
      errors,
      firstName,
      lastName,
      emailAddress,
      phoneNumber,
      trackingNumber,
      subject,
      city,
      vehicle,
      message,
    } = this.state;
    return (
      <form
        noValidate
        autoComplete="off"
        className="contact-us-form"
        style={{ justifyContent: 'center' }}
      >
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6} md={6} xl={6}>
            <FormInputS
              name="firstName"
              value={firstName}
              onChange={this.handleChange}
              placeholder="First Name"
              error={errors.firstName}
            />
          </Grid>
          <Grid item xs={12} sm={6} md={6} xl={6}>
            <FormInputS
              name="lastName"
              value={lastName}
              onChange={this.handleChange}
              placeholder="Last Name"
              error={errors.lastName}
            />
          </Grid>
          <Grid item xs={12} sm={6} md={6} xl={6}>
            <FormInputS
              name="emailAddress"
              value={emailAddress}
              onChange={this.handleChange}
              placeholder="Email Address"
              error={errors.emailAddress}
            />
          </Grid>
          <Grid item xs={12} sm={6} md={6} xl={6}>
            <FormInputS
              name="phoneNumber"
              value={phoneNumber}
              onChange={this.handleChange}
              placeholder="Phone number"
              error={errors.phoneNumber}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={12} xl={12}>
            <FormInputS
              name="trackingNumber"
              value={trackingNumber}
              onChange={this.handleChange}
              placeholder="Tracking number"
              error={errors.trackingNumber}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={12} xl={12}>
            <FormInputS
              name="subject"
              value={subject}
              onChange={this.handleChange}
              placeholder="Subject"
              error={errors.subject}
            />
          </Grid>

          <Grid item xs={12} sm={12} md={12} xl={12}>
            <FormTextArea
              rows={5}
              name="message"
              value={message}
              onChange={this.handleChange}
              error={errors.message}
              placeholder="your message"
            />
          </Grid>
          <Grid item xs={12}>
            <FormControlLabel
              control={
                <Checkbox
                  icon={<CircleUnchecked />}
                  checkedIcon={<CircleCheckedFilled />}
                  checked={this.state.checked}
                  onChange={this.handleChangeForcheck}
                  variant={'outlined'}
                  name="checked"
                  size="medium"
                  style={{
                    color: colors.orange,
                  }}
                />
              }
              label={
                <span style={{ color: colors.white }}>
                  I hereby accept the privacy policy
                </span>
              }
            />
          </Grid>
          <Grid
            item
            xs={12}
            sm={12}
            md={12}
            xl={12}
            style={{ display: 'flex', justifyContent: 'flex-end' }}
          >
            <ButtonComponent
              bgColor={colors.darkBlue}
              borderRadius="50px"
              padding="10px"
              width="140px"
              
              color={colors.white}
              onClick={this.handleSubmit}
            >
              Send
            </ButtonComponent>
          </Grid>
        </Grid>
      </form>
    );
  }
}
