import React, { Component } from 'react';

import { authClass } from '../../utils/auth.util';
import ApiCalls from '../../service/RequestHandler';
import StoreSettingsComponent from './StoreSettingsComponent';
import { newGrowl } from '../../components/ui/GrowlComponent';
import * as authUtil from '../../utils/auth.util';
import isEmpty, {
  isNumber,
  isEmail,
  minLength,
  isMatch,
} from '../../utils/helpers';

export default class StoreSettingsContainer extends Component {
  constructor(props) {
    super(props);
    this.user = authClass.getUser;
    this.state = {
      name: '',
      mobile: '',
      language: '',
      email: '',
      role: '',
      oldPassword: '',
      newPassword: '',
      confirmPassword: '',
      activity: true,
      password: '******',
      errors: {},
      isDialogVisible: false,
      dialogData: { values: [] },
      firstName: this.user.firstName,
      lastName: this.user.lastName,
      mobile: this.user.mobile,
      weightUnit: { value: '', label: 'Weight' },
      currency: { value: '', label: 'Currency' },
      // language: { value: '', label: 'Language' },
      measurementUnit: { value: '', label: 'Measurement' },
      isUpdate: false,
      emailAddresses: [],
      storeId: '',
    };
  }

  async componentDidMount() {
    const user = await authUtil.getUser();
    this.setState({
      storeId: user?._id,
    });
    this.getProfile();
    window.scrollTo(0, 0);
  }

  async getProfile() {
    try {
      const { data } = await ApiCalls.getStoreById(this.state.storeId);
      this.setState({
        name: data.contactDetail.name,
        mobile: data.contactDetail.mobile,
        language: { value: data.contactDetail.language },
        emailAddresses: data.emailAddresses,
      });
    } catch (error) {
      this.setState({ activity: false });
    }
  }

  handleContactDetails = async () => {
    this.setState({ activity: true });
    let valid = true;
    const data = {
      contactDetail: {
        name: this.state.name,
        mobile: this.state.mobile,
        language: this.state.language.value,
      },
    };

    try {
      if (isEmpty(data.contactDetail.name)) {
        newGrowl.showGrowl(
          'error',
          'Error',
          'Contact Person field is required!'
        );
      } else if (isEmpty(data.contactDetail.mobile)) {
        newGrowl.showGrowl('error', 'Error', 'Phone field is required!');
      } else if (!isNumber(data.contactDetail.mobile)) {
        newGrowl.showGrowl(
          'error',
          'Error',
          'Phone field contains only numbers!'
        );
      } else if (isEmpty(data.contactDetail.language)) {
        newGrowl.showGrowl('error', 'Error', 'Languages field is required!');
      } else {
        const response = await ApiCalls.updateContactDetails(data);
        await authClass.setUserOnUpdate(response.data);
        this.setState({
          activity: false,
        });
        newGrowl.showGrowl(
          'success',
          'Success',
          'Your contact details has been submitted successfully.'
        );
      }
    } catch (error) {
      this.setState({ activity: false });
    }
  };

  handleAddEmail = async () => {
    this.setState({ activity: true });
    let valid = true;
    const data = {
      email: this.state.email,
      role: this.state.role.value,
    };

    try {
      if (isEmpty(data.email)) {
        newGrowl.showGrowl(
          'error',
          'Error',
          'Email Address field is required!'
        );
      } else if (!isEmail(data.email)) {
        newGrowl.showGrowl(
          'error',
          'Error',
          'Email Address field contains only Email Address!'
        );
      } else {
        const response = await ApiCalls.addEmail(data);
        this.setState({
          activity: false,
          email: '',
          role: '',
        });
        this.getProfile();
        newGrowl.showGrowl(
          'success',
          'Success',
          'Your email address has been added successfully.'
        );
      }
    } catch (error) {
      newGrowl.showGrowl('error', 'Error', `${error.response.data.message}`);
      this.setState({ activity: false });
    }
  };

  handleChangePassword = async () => {
    this.setState({ activity: true });
    let valid = true;
    const data = {
      oldPassword: this.state.oldPassword,
      newPassword: this.state.newPassword,
      confirmPassword: this.state.confirmPassword,
    };

    try {
      if (isEmpty(data.newPassword)) {
        newGrowl.showGrowl('error', 'Error', 'New Password field is required!');
      } else {
        const response = await ApiCalls.changeStorePassword(data);
        this.setState({
          activity: false,
        });
        newGrowl.showGrowl(
          'success',
          'Success',
          'Your password has been changed successfully.'
        );
      }
    } catch (error) {
      this.setState({ activity: false });
    }
  };

  handleDeleteAccount = async () => {
    this.setState({ activity: true });

    try {
      const response = await ApiCalls.deleteAccount();
      this.setState({
        activity: false,
      });
      newGrowl.showGrowl(
        'success',
        'Success',
        'Your account has been deleted successfully.'
      );
    } catch (error) {
      this.setState({ activity: false });
    }
  };

  handleResendVerification = async (item) => {
    console.log('This is handle verification item', item);
    const data = {
      email: item.email,
      role: 'admin',
    };
    try {
      const response = await ApiCalls.resendVerfication(data);
      newGrowl.showGrowl(
        'success',
        'Success',
        'Your handle resend verification have been  Successfully.'
      );
    } catch (error) {
      console.log(error);
    }
  };

  onInputChange = (event) => {
    const { name, value, checked, type } = event.target;

    this.setState((prevState) => {
      return {
        [name]: type === 'checkbox' ? checked : value,
        errors: { ...prevState.errors, [name]: false },
        getRate: false,
      };
    });
  };

  handleSelectChange = async (e) => {
    const { name, value } = e.target;

    this.setState({
      [name]: value,
    });
  };

  handleUpdateUserProfile = async () => {
    this.setState({
      activity: true,
    });
    const profileToUpdate = {
      firstName: this.state.firstName,
      lastName: this.state.lastName,
      email: this.state.email,
      config: {
        currency: this.state.currency.value,
        language: this.state.language.value,
        weightUnit: this.state.weightUnit.value,
        measurementUnit: this.state.measurementUnit.value,
      },
    };

    try {
      const response = await ApiCalls.updateUserProfile(profileToUpdate);
      this.setState({
        activity: false,
        isUpdate: false,
      });
      newGrowl.showGrowl(
        'success',
        'Success',
        'Your settings have been updated.'
      );
      await authClass.setUserOnUpdate(response.data);
    } catch (error) {
      this.setState({ activity: false, isUpdate: true });
    }
  };

  // handleResendVerification = async (data) => {
  // this.setState({
  //   activity: true,
  // });
  // const data = {
  //   email: 'test2@test.com',
  //   role: 'admin',
  // };

  // try {
  //   const response = await ApiCalls.handleResendVerification(data);
  //   newGrowl.showGrowl(
  //     'success',
  //     'Success',
  //     'Your Password have been Changed Successfully.'
  //   );
  // } catch (error) {
  //   newGrowl.showGrowl('error', 'Error', 'Invalid Password');
  //   this.setState({
  //     activity: false,
  //     oldPassword: '',
  //     newPassword: '',
  //   });
  // }
  // };

  handleChangePassword = async () => {
    this.setState({
      activity: true,
    });
    const changePassword = {
      oldPassword: this.state.oldPassword,
      newPassword: this.state.newPassword,
      confirmPassword: this.state.confirmPassword,
    };
    console.log('This is all change password', changePassword);
    try {
      if (isEmpty(changePassword.newPassword)) {
        newGrowl.showGrowl('error', 'Error', 'New Password field is required!');
      } else if (!minLength(changePassword.newPassword)) {
        newGrowl.showGrowl(
          'error',
          'Error',
          'New Password field contains atleast 4 Characters!'
        );
      } else if (
        !isMatch(changePassword.newPassword, changePassword.confirmPassword)
      ) {
        newGrowl.showGrowl('error', 'Error', 'Passwords must be match!');
      } else {
        const response = await ApiCalls.changeStorePassword(changePassword);
        this.setState({
          activity: false,
          isDialogVisible: false,
          oldPassword: '',
          newPassword: '',
          confirmPassword: '',
        });
        newGrowl.showGrowl(
          'success',
          'Success',
          'Your Password have been Changed Successfully.'
        );
      }
    } catch (error) {
      newGrowl.showGrowl('error', 'Error', 'Invalid Password');
      this.setState({
        activity: false,
        oldPassword: '',
        newPassword: '',
        confirmPassword: '',
      });
    }
  };

  handleSetProfile = (profile) => {
    this.setState({
      activity: false,
      profile,
    });
  };

  handleChange = (e) => {
    const { name, value } = e.target;
    if (this.state.errors[name]) {
      this.setState((prevState) => ({
        [name]: value,
        errors: { ...prevState.errors, [name]: false },
      }));
    } else if (name === 'oldPassword' || name === 'newPassword') {
      this.setState({ [name]: value });
    } else {
      this.setState({ [name]: value, isUpdate: true });
    }
  };

  handleDialogVisibility = (visibility, dialogData) => {
    if (visibility === true) {
      this.setState({
        dialogData: dialogData,
        isDialogVisible: visibility,
      });
    } else {
      if (this.isValid()) {
        this.setState({
          dialogData: { values: [] },
          isDialogVisible: false,
        });
      }
    }
  };

  isValid = () => {
    let errors = {},
      isValid = true;

    this.state.dialogData.values.forEach((item) => {
      if (!this.state[item.name]) {
        isValid = false;
        errors[item.name] = true;
      }
    });

    this.setState({ errors });
    return isValid;
  };

  handleTelInputRef = (ref) => (this.telInput = ref);

  render() {
    return (
      <StoreSettingsComponent
        {...this.state}
        {...this.props}
        handleContactDetails={this.handleContactDetails}
        handleAddEmail={this.handleAddEmail}
        handleChangePassword={this.handleChangePassword}
        handleDeleteAccount={this.handleDeleteAccount}
        onInputChange={this.onInputChange}
        handleSelectChange={this.handleSelectChange}
        getProfile={this.getProfile}
        handleChange={this.handleChange}
        handleSetProfile={this.handleSetProfile}
        handleChangePassword={this.handleChangePassword}
        handleDialogVisibility={this.handleDialogVisibility}
        handleUpdateUserProfile={this.handleUpdateUserProfile}
        handleResendVerification={this.handleResendVerification}
        tellInputRef={this.handleTelInputRef}
      />
    );
  }
}
