import React, { Component } from 'react';
import { withGoogleMap, GoogleMap, Marker } from 'react-google-maps';
import { MapComponentForFleet } from './MapComponentForFleet';

const MapForDriver = withGoogleMap((props) => (
  <GoogleMap
    defaultZoom={10}
    defaultCenter={{ lat: 25.046216233655368, lng: 66.93432138094492 }}
  >
    {/* Default Location need to be set Dinamically */}
     {(props.DiverLocations || []).map((DriverMark, index) =>
      (DriverMark || {}).location ? (
        <Marker
          key={index}
          position={{
            lat: (DriverMark || {}).location[1],
            lng: (DriverMark || {}).location[0],
          }}
          icon={ {
            url: require('../../static/images/marker.png'),
            scaledSize: { width: 35, height: 35 },
          } }
        />
      ) : (
          null
        // alert('location nai h' + DriverMark.location)
      )
    )}
  </GoogleMap>
));

export default class MapContainerForFleet extends Component {
  render() {
    return (
      <MapComponentForFleet
        MapForDriver={MapForDriver}
        DiverLocations={this.props.DiverLocations}
      />
    );
  }
}
