import React from "react";
import { Grid } from "@material-ui/core";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import moment from "moment";
import FormNativeSelectInputField from "../../../components/form/FormNativeSelectInputField";
import ApiCalls from "../../../service/RequestHandler";

const monthNames = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

export default class FullCalenderDemo extends React.Component {
  constructor() {
    super();
    this.state = {
      dates: [],
      timeSlotsData: [],
      month: {},
      monthOptions: [],
    };
  }

  componentDidMount() {
    this.createDates(new Date("07-15-2020"));
    this.getTimeSlots();
    this.getMonth();
  }

  handleSelectChange = (e) => {
    const { name, value } = e.target;
    this.setState((prevState) => {
      return {
        [name]: value,
      };
    });
    var d = new Date();
    var year = d.getFullYear();
    var dateStr = value.key + "/" + 1 + "/" + year;
    this.createDates(dateStr);
  };

  getMonth = () => {
    const month = {};
    let d = new Date();
    let getMonth = monthNames[d.getMonth()];
    Object.assign(month, { label: getMonth, value: getMonth });
    this.setState({ month });
  };

  async getTimeSlots() {
    const data = {
      startDate: "07-10-2020",
      endDate: "07-30-2020",
    };
    try {
      const response = await ApiCalls.getTimeSlots(data);
      console.log("This is all data getTimeSlots", response.data);
      this.setState({
        timeSlotsData: response.data,
      });
    } catch (error) {
      this.setState({ activity: false });
    }
  }

  createDates = (date) => {
    let sDate = new Date(date);
    let dates = [];

    for (let index = 0; index < 7; index++) {
      let newDate = new Date(sDate);
      newDate.setDate(sDate.getDate() + index);

      dates.push(newDate);
      /* FOR NEXT 
      let lastDate = this.state.dates[this.state.dates.length - 1]
      lastDate.setDate(lastDate.getDate() + 1)
      this.createDates(lastDate)*/
    }

    this.setState({ dates });
  };

  findAvailableSlots = ({ date, time }) => {
    const { timeSlotsData } = this.state;
    return Object.keys(timeSlotsData).map((_time) => {
      if (moment(date).format("DD-M-YYYY") === _time) {
        return <p>Hello</p>;
      } else {
        return <p>Wrong</p>;
      }
    });
  };

  render() {
    let timeSlots = [
      { name: "8:00 am - 12:00 pm", value: { from: "8:00", to: "12:00" } },
      { name: "12:00 pm - 4:00 pm", value: { from: "12:00", to: "16:00" } },
      { name: "4:00 pm - 8:00 pm", value: { from: "16:00", to: "20:00" } },
    ];
    let days = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
    const { dates } = this.state;
    const startDate = moment(dates[0]).format("YYYY-MM-DD");
    const endDate = moment(dates[dates.length - 1]).format("YYYY-MM-DD");

    return (
      <React.Fragment>
        <div style={{ marginTop: 30 }}>
          <Grid container>
            <Grid item xs={1}>
              <ArrowBackIosIcon />
            </Grid>
            <Grid item xs={1}>
              <ArrowForwardIosIcon />
            </Grid>
            <Grid item xs={7}>
              <p>{`${startDate} - ${endDate}`}</p>
            </Grid>
            <Grid item xs={3}>
              <FormNativeSelectInputField
                type="react-select"
                style={{ marginBottom: 5 }}
                name="month"
                options={[
                  { value: "August", label: "August", key: 8 },
                  { value: "September", label: "September", key: 9 },
                ]}
                value={this.state.month}
                onChange={this.handleSelectChange}
              />
            </Grid>
          </Grid>
          <div
            style={{
              position: "relative",
            }}
          >
            <div style={{ display: "flex", marginLeft: 170 }}>
              {this.state.dates.map((value, index, array) => {
                return (
                  <div
                    style={{
                      textAlign: "center",
                      width: 120,
                    }}
                  >
                    <p style={{ fontWeight: "bold" }}>{value.getDate()} </p>
                    <p style={{ fontWeight: "bold" }}>{days[value.getDay()]}</p>
                  </div>
                );
              })}
            </div>
            <div style={{ marginLeft: 170 }}>
              {timeSlots.map((item, index, array) => {
                return (
                  <div style={{ display: "flex" }}>
                    {this.state.dates.map((value, index, array) => {
                      let from = "";
                      let toTime = "";
                      (
                        Object.values(this.state.timeSlotsData)[index] || []
                      ).map((item, index) => {
                        from = item.timeSlot.from;
                        toTime = item.timeSlot.to;
                      });
                      return Object.keys(this.state.timeSlotsData)[index] ===
                        moment(value).format("DD-M-YYYY") &&
                        from === item.value.from &&
                        toTime === item.value.to ? (
                        <div
                          style={{
                            backgroundColor: "rgb(250, 120, 22)",
                            textAlign: "center",
                            width: 120,
                            paddingBottom: 10,
                            paddingTop: 10,
                            border: "1px solid #f2f2f2",
                            cursor: "pointer",
                          }}
                        >
                          <p
                            onClick={() => {
                              this.props.onSlotSelect({
                                date: moment(value).format("MM/DD/YYYY"),
                                timeSlot: item.value,
                              });
                            }}
                            style={{ color: "black" }}
                          >{`${item.name} `}</p>
                        </div>
                      ) : (
                        <div
                          style={{
                            textAlign: "center",
                            width: 120,
                            paddingBottom: 10,
                            paddingTop: 10,
                            border: "1px solid #f2f2f2",
                          }}
                        >
                          <p style={{ color: "black" }}>{`${item.name} `}</p>
                        </div>
                      );
                    })}
                  </div>
                );
              })}
            </div>
            <div style={{ position: "absolute", left: 0, top: 30 }}>
              {timeSlots.map((slot, index, array) => {
                return (
                  <div
                    style={{
                      paddingBottom: 25,
                      paddingTop: 25,
                    }}
                  >
                    <p style={{ fontWeight: "bold" }}>{slot.name}</p>
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
