import React from 'react';
import styled from 'styled-components';
import {
  Heading,
  HeadingLink,
  LinkList,
  DropdownSection,
  Icon,
} from './Components';
import routes from '../../../constants/route-constants';

const ServicesDropdownEl = styled.div`
  width: 18.5rem;
`;

const ServicesDropdown = () => {
  return (
    <ServicesDropdownEl>
      <DropdownSection data-first-dropdown-section>
        <ul>
          <HeadingLink>
            <a href={`#/${routes.ShipFromStore}`}>Ship from store</a>
          </HeadingLink>
          <HeadingLink>
            <a href={`#/${routes.ClientReturns}`}>Client returns</a>
          </HeadingLink>
          {/* <HeadingLink>
            <a href={`#/${routes.FrankForDevelopers}`}>Frank for developers</a>
          </HeadingLink> */}
          <HeadingLink noMarginBottom>
            <a href={`#/${routes.HelpCenter}`}>Help Center</a>
          </HeadingLink>
        </ul>
      </DropdownSection>
    </ServicesDropdownEl>
  );
};

export default ServicesDropdown;
