/**
 * Validator for Login
 */

export const validateLogin = data => {
  const errors = {};
  let isValid = true;

  if (!data.email) {
    errors.email = true;
    isValid = false;
  }

  if (!data.password) {
    errors.password = true;
    isValid = false;
  }

  return { errors, isValid };
};
